<template>
<div class="opOrderPayDetail">
    <div class="topHeader">
        <header class="mint-header is-fixed" style="background-color: rgb(57, 60, 65);">
            <div class="mint-header-button is-left">
                <a href="#/operapay" class="router-link-active">
            <i class="mintui mintui-back"></i>
          </a>
            </div>
            <h1 class="mint-header-title">订单支付</h1>
            <div class="mint-header-button is-right">
                <mt-button icon="more" @click="sheetVisible=true" slot="right"></mt-button>
                <actionsheet :actions="actions" v-model="sheetVisible"></actionsheet>
            </div>
        </header>
    </div>
    <div class="topSearch">
        <input type="text" class="search" placeholder="桌号" v-model="orderReqObj.tableNo">
        <div class="iconfont icon-saoma" @click="scanOrder"></div>
        <div class="searchCheck" @click="searchOrder">搜索</div>
    </div>
    <div v-if="!isMultiple">
        <div class="info" v-bind:class="{ 'hide': isEmpty }">
            <ul>
                <li class="clear">
                    <p style="width:30%">
                        <label>账单：</label>
                        <b @click='openDetail'>{{orderBaseInfo?orderBaseInfo.checkNum:""}}</b>
                    </p>
                    <p style="width:20%">
                        <label>人数：</label>
                        <span>{{detailSummary.covers}}</span>
                    </p>
                    <p style="width:30%">
                        <span>{{detailSummary.checkOpenTime | datef}}</span>
                    </p>
                </li>
                <li>
                    <p style="width:55%">
                        <label>会员卡号：</label>
                        <template v-if="orderDetail.carNo">
                            {{orderDetail.carNo}}
                        </template>
                        <template v-else>

                        </template>
                        <span class="iconfont icon-tianjia" @click='openVip(false)'></span>
                    </p>

                    <p style="width:35%">
                        <label style="float:left">优惠：</label>
                        <span class="yh">{{orderDetail.posModeDes}} </span>
                    </p>
                </li>
            </ul>
        </div>
        <div class="detail detail2" v-bind:class="{ 'hide': isEmpty} ">
            <div class="topLine">
                <div class="changeColor"></div>
            </div>
            <div class="infoDetails">
                <menu-list :detailLine="detailLine"></menu-list>
                <div class="allserver">
                    <ul>
                        <li>
                            <div class="payFirst"></div>
                            <div class="titleS">合计：</diV>
                            <span>{{(parseFloat(detailSummary.subTotal) - getAlldsc()).toFixed(2)}}</span>
                        </li>
                        <li v-if="getAlldsc() != 0">
                            <div class="payFirst"></div>
                            <div class="titleS">折扣：</diV>
                            <span>{{getAlldsc() | tofixed}}</span>
                        </li>
                        <li v-if="detailSummary.autoGratuity != 0">
                            <div class="payFirst"></div>
                            <div class="titleS">服务费：</diV>
                            <span>{{detailSummary.autoGratuity | tofixed}}</span>
                        </li>
                        <li v-if="getOtherdsc() != 0">
                            <div class="payFirst"></div>
                            <div class="titleS">小费：</diV>
                            <span>{{getOtherdsc() | tofixed}}</span>
                        </li>
                        <li v-if="detailSummary.tax != 0">
                            <div class="payFirst"></div>
                            <div class="titleS">含税：</diV>
                            <span>{{detailSummary.tax | tofixed}}</span>
                        </li>
                        <li v-if="detailSummary.payment != 0">
                            <div class="payFirst"></div>
                            <div class="titleS">已付款：</diV>
                            <span>{{detailSummary.payment | tofixed}}</span>
                        </li>
                    </ul>
                </div>

                <div class="clearFloat"></div>
            </div>
        </div>
    </div>
    <div v-else class="multipleBaseInfo clearfix">
        <template v-for="(item,index) in multipleBaseInfo">
            <div class="mul-warp" @click="selectOpenChecks(item,index)" :key="index">
                <div class="topOrderList">
                    <p>
                        账单：{{item.checkNum}}
                    </p>
                    <p>
                        {{item.checkOpenTime}}
                    </p>
                </div>
                <ul class="mul-content">
                    <li>
                        桌号：{{item.checkTableName}}
                    </li>
                    <!-- <li>
               类型：{{item.orderTypeStr}}
            </li> -->
                    <li>
                        雇员：{{item.checkEmployee}}
                    </li>
                    <!-- <li>
               雇员号：{{item.checkEmployeeObjectNum}}
            </li> -->
                    <li>
                        时间：{{item.checkOpenTime | datef}}
                    </li>
                    <li>
                        金额：<small>￥</small>{{item.checkTotalDue | tofixed}}
                    </li>
                </ul>
            </div>
        </template>
    </div>
    <div v-bind:class="{ 'hide': !isEmpty }">
        <img class="nothing" src="../../assets/img/nothing.png" v-show="!isFrist">
        <p class="nothingTxt" v-show="!isFrist">什么都没有</p>
        <img class="nothing" src="../../assets/img/chenggong.png" v-show="isSucceed">
        <p class="nothingTxt" v-show="isSucceed">付款成功</p>
    </div>
    <div class="clearChoice"></div>
    <div class="footerChoice" v-bind:class="{ 'hide': isEmpty||isMultiple }">
        <div class="allPrice">
            <div class="titleS">应付金额：</diV>
            <div class="payFirst">
                <small>￥</small>{{this.getPayAmout().toFixed(2)}}</div>
        </div>
        <template v-if="SYS_DEVICE == 'BOC'">
            <div class="fboc">
                <ul>
                    <li class="fb" @click="checkUnionPay()" style="margin-right: 0.3rem;"><img src="../../assets/img/bank.png"/>
                        <div>中国银行</div>
                    </li>
                    <li class="fb" @click="checkUnionScan('alipay')"><img src="../../assets/img/alipay.png"/>
                        <div>支付宝</div>
                    </li>
                </ul>
                <div class="ro" @click='showList'>
                    <div class="iconfont icon-you" v-if='show==true'></div>
                    <div class="iconfont icon-xia" v-else></div>
                </div>
                <ul v-if='show==false'>
                    <li class="fb" @click="checkUnionScan('unionPay')" style="margin-right: 0.3rem;"><img src="../../assets/img/ysf.png"/>
                        <div>云闪付</div>
                    </li>
                    <li class="fb" @click="checkUnionScan('wechatPay')"><img src="../../assets/img/weixin.png"/>
                        <div>微信</div>
                    </li>
                </ul>
                <ul v-if='show==false'>
                    <li class="fb" @click="toFrontDest" v-if="vc.isPunRoom" style="margin-right: 0.3rem;"><img src="../../assets/img/room.png"/>
                        <div>转房间</div>
                    </li>
                     <li class="fb" @click="cardPayS" v-if="vc.isMember"><img src="../../assets/img/card.png"/>
                        <div>储值支付</div>
                    </li>
                </ul>
               
            </div>
        </template>
        <template v-else>

        <div class="fixL">
            <ul v-if="SYS_DEVICE == 'NG'">
                <li @click="checkUnionScan" style="width:90%">支付宝微信扫码支付</li>
            </ul>
            <ul v-else>
                <li @click="checkUnionPay">银行卡支付</li>
                <li @click="checkUnionScan">支付宝微信</li>
            </ul>
            <ul v-if='show==false'>
                <li @click="toFrontDest" v-if="vc.isPunRoom">转房间</li>
                <li @click="cardPayS" v-if="vc.isMember">会员卡支付</li>
                <!-- <li @click="toAR">转AR</li> -->
            </ul>
        </div>
        <div class="fixR" @click='showList' v-if="vc.isPunRoom || vc.isMember">
            <div class="omit" v-if='show==true'>...</div>
            <div class="omit" v-else>
                <div class="iconfont icon-xia"></div>
            </div>
        </div>
        </template>
    </div>
    <mt-popup v-model="vipOpen" class="fullp" modal='false'>
        <top-nav title="会员支付" @notseeChild='notSee' :emptyRight="true"></top-nav>
        <div class="vipSearch">
            <div class="vipTop" v-if="vc.hCardNo">
                <input class="orderNumer" type="text" name="" id="" placeholder="会员卡号/姓名/手机" v-model="cardInfo">
                       <div class="searchResult" @click="getCardInfo">查询</div>
                 <div class="searchResult" @click="readCard" style="width:20%;margin-right:0.2rem;">读卡</div>
         
            </div>
            <div class="vipTop" v-else>
                <div class="searchResult" @click="readCard" style="width:80%;margin-right:10%;">会员卡读卡</div>
            </div>
            <div class="vipList" v-for="(item, index) in cardInfoObj" :key="index" @click="selectCard(item,index)">
                <div class="blueLind"></div>
                <div class="vipCheckBox">
                    <div class="greenCheck">
                        <div class="checkBox">
                            <div class="iconfont " v-bind:class="{ 'icon-right':  item.isSelect  }"></div>
                        </div>
                    </div>
                </div>
                <div class="infoL">
                    <div class="lineInfo">
                        <div class="lineInfo">
                            <div class="titleThird">卡号：</div>
                            <span>{{item.cardNo}}</span>
                        </div>
                        <div class="titleThird">姓名：</div>
                        <span>{{item.cardInfo}}</span>
                    </div>
                    <div class="lineInfo">
                        <div class="titleThird nomalL">手机号：</div>
                        <span>{{item.mobile}}</span>
                    </div>
                </div>
                <div class="infoL">
                    <div class="lineInfo">

                        <div class="titleS">余额：</div>
                        <span>{{item.balance}}</span>
                    </div>
                    <div class="lineInfo">

                        <div class="titleS">状态：</div>
                        <span>{{item.sta}}</span>
                    </div>
                    <div class="lineInfo">
                        <div class="titleThird">优惠：</div>

                        <span>{{item.dscModeDescript}}</span>
                    </div>
                </div>
            </div>
            <div v-bind:class="{ 'hide': !isCardEmpty }">
                <img class="nothing" src="../../assets/img/nothing.png" v-show="!isCardFrist">
                <p class="nothingTxt" v-show="!isCardFrist">什么都没有</p>
            </div>
        </div>
    </mt-popup>
    <mt-popup v-model="popupVisible" class="fullp" modal='false'>
        <div class="topHeader">
            <header class="mint-header is-fixed" style="background-color:#00487C;">
                <div class="mint-header-button is-left">
                    <a class="router-link-exact-active router-link-active" @click='popupVisible=false' style=" padding:.2rem .6rem .2rem 0;">
              <i class="mintui mintui-back"></i>
            </a>
                </div>
                <h1 class="mint-header-title">餐单详情</h1>
                <div class="mint-header-button is-right">
                </div>
            </header>
        </div>
        <operaOrderDetail ref="masterListC"></operaOrderDetail>
    </mt-popup>
</div>
</template>

<script>
import topNav from "../common/topNav";
import menuList from "../common/menuList";
import operaOrderDetail from "../common/operaOrderDetail";
import {
    Toast,
    Actionsheet,
    MessageBox
} from "mint-ui";
import moment from "moment";
import _ from "lodash";
import {
    getKeyUrl,
    getAccountJson
} from "../../assets/js/md5Data.js";
export default {
    data() {
        return {
            isMultiple: true, //一个桌号有多个订单
            orderReqObj: {
                tableNo: "",
                employeeObjectNum: 0,
                rvcObjNum: "",
                searchFlag: 1
            },
            multipleBaseInfo: [],
            orderBaseInfo: {
                //getOpenChecks得到的基本信息
            },
            //订单信息
            detailLine: null,
            detailSummary: "",
            orderDetail: {},
            orderDetailDefaultObj: {},
            popupVisible: false,
            isEmpty: true,
            isCardFrist: true,
            bcardPay: false,
            isCardEmpty: false,
            cardInfoObj: [],
            isFrist: true,
            isSucceed: false,
            orderInfoObj: "",
            show: true,
            cardInfo: null,
            vipOpen: false,
            sheetVisible: false,
            actions: [],
            vc: !!localStorage.vc ?
                JSON.parse(localStorage.vc) : {
                    invoice: false,
                    rvcObjectNum: "",
                    isPunRoom: false,
                    checkIn: true,
                    isMember: false,
                    hCardNo: false,
                    RFCardSec: null,
                },
            SYS_DEVICE: "",
            lastPrintObj: null,
            membalance: 0
        };
    },
    mounted() {
        this.SYS_DEVICE = this.$SYS_DEVICE;
        this.orderReqObj.rvcObjNum = this.vc.rvcObjectNum;
        // 头部有参数的时候默认去查询相关的
        if (typeof this.$route.query.tableno == "string") {
            this.orderReqObj.tableNo = this.$route.query.tableno;
            if (localStorage.OpenChecks) {
                this.orderBaseInfo = JSON.parse(localStorage.OpenChecks);
                this.isMultiple = false;
                this.isEmpty = false;
                this.getCheckDetail(
                    this.orderBaseInfo.checkNum,
                    this.orderBaseInfo.checkSeq
                );
            } else {
                this.searchOrder();
            }
        }
        // 支付成功的时候相关的查询内容
        this.actions = [{
                name: "打印菜目",
                method: () => {
                    this.PrintV();
                }
            },
            {
                name: "入账核对",
                method: this.reInAccount
            }
            /*{ name: "关联会员", method: this.openVip },
            { name: "关联协议", method:()=>{this.companyOpen=true;} }*/
        ];
        if (typeof this.$route.query.isSucceed == "string") {
            (this.isEmpty = true), (this.isFrist = true);
            this.isSucceed = true;
        }
    },
    methods: {
        // 扫描
        scanOrder() {
            ScanData(this.scanSucceed, null);
        },
        // 扫描成功后
        scanSucceed(value) {
            if (value != null && value != "") {
                if (value.length < 10) {
                    this.orderReqObj.tableNo = value;
                    this.searchOrder();
                } else {
                    this.msg("不是有效桌号");
                }
            } else {
                this.msg("扫描数据为空");
            }
        },
        openVip(b) {
            this.bcardPay = b;
            this.vipOpen = !this.vipOpen;
            if (this.cardInfoObj.length == 0 && !this.vc.hCardNo) {
                this.readCard();
            }
        },
        cardPayS() {
            if (!this.orderDetail.carNo) {
                this.msg('请先关联会员卡');
                this.openVip(false);
            } else {
                this.$http.post("/ohotel/posApi/getCardInfo", {
                        cardInfo: this.orderDetail.carNo
                    })
                    .then(res => {
                        if (res.data.success) {
                            if (res.data.result.length == 1) {
                                var vitem = res.data.result[0];
                                var left = vitem.balance;
                                if (left < this.getPayAmout()) {
                                    this.msg("余额不足：" + left + "，请先充值!")
                                } else {
                                    MessageBox({
                                        title: "提示",
                                        message: "确定使用会员卡：" + this.orderDetail.carNo + " 支付？",
                                        showCancelButton: true,
                                        closeOnClickModal: false,
                                        confirmButtonText: "确定"
                                    }).then(action => {
                                        if (action == "confirm") {
                                            this.membalance = left;
                                            this.cardPay(this.orderDetail.carNo);
                                        }
                                    });
                                }

                            } else {
                                this.cardInfoObj = res.data.result;
                                for (var item in this.cardInfoObj) {
                                    this.cardInfoObj[item].isSelect = false;
                                    this.isCardEmpty = false;
                                    this.isCardFrist = false;
                                }
                                this.bcardPay = true;
                                this.vipOpen = true
                                this.cardInfo = this.orderDetailObj.cardNo;
                            }

                        } else {
                            this.msg(res.data.message);
                        }
                    });
            }

        },
        PrintV(payway) {
            if (this.isEmpty) {
                this.msg("请输入桌号");
            } else {
      
                    var tobj = this.orderDetail;
                    tobj.membalance = this.membalance;
                    tobj.tableNo = this.orderBaseInfo.checkTableName;
                    tobj.checkEmployee = this.orderBaseInfo.checkEmployee;
                    tobj.orderTypeStr = this.orderBaseInfo.orderTypeStr;
                    tobj.summary.due = this.orderBaseInfo.checkTotalDue;
                    if (payway != undefined) {
                        tobj.payway = getPayWayName(payway);
                    } else {
                        tobj.payway = null;
                    }
                    var gatewayJson = localStorage.getItem("simgateway");
                    if (gatewayJson != null) {
                        var gateway = JSON.parse(gatewayJson);
                        tobj.hotelName = gateway.hotelName;
                        if (this.vc.invoice && payway) {
                            var keyarry = [];
                            keyarry.push({
                                k: "checkNumber",
                                v: this.orderBaseInfo.checkNum
                            });
                            keyarry.push({
                                k: "hotelGroupCode",
                                v: gateway.hotelGroupCode
                            });
                            keyarry.push({
                                k: "hotelCode",
                                v: gateway.hotelCode
                            });
                            keyarry.push({
                                k: "bollingType",
                                v: 2
                            });
                            keyarry.push({
                                k: "revobj",
                                v: this.orderReqObj.rvcObjNum
                            });

                            keyarry.push({
                                k: "checkSeqNumber",
                                v: this.orderBaseInfo.checkSeq
                            });

                            var urlv =
                                gateway.url +
                                "/ohotel/posApi/checkBilling?" + getKeyUrl(keyarry);
                            tobj.invoiceGateway = urlv;
                        }
                    }
                    this.lastPrintObj = tobj; // JSON.stringify(tobj);
                    //console.log(this.lastPrintObj);
                    printSimphonyData(
                        this.printSucceed,
                        this.printError,
                        this.lastPrintObj
                    );
            }
        },
        notSee() {
            this.vipOpen = false;
        },
        readCard() {
            if (!this.vc.RFCardSec) {
                this.msg('请先设置会员卡起始扇区号');
                return;
            }
            rfcScan(this.readSuccess, null, this.vc.RFCardSec);
            // this.cardInfo = "0000444";
            // this.getCardInfo();
        },
        readSuccess(v) {
            this.cardInfo = v;
            this.getCardInfo();
        },
        getCardInfo() {
            if(!this.cardInfo){
                this.msg('请先'+(this.vc.hCardNo?"输入卡号":"读会员卡"));
                return;
            }
            this.$http
                .post("/ohotel/posApi/getCardInfo", {
                    cardInfo: this.cardInfo
                })
                .then(res => {
                    if (res.data.success) {
                        this.cardInfoObj = res.data.result;
                        for (var item in this.cardInfoObj) {
                            this.cardInfoObj[item].isSelect = false;
                            this.isCardEmpty = false;
                            this.isCardFrist = false;
                        }
                    } else {
                        this.cardInfoObj = res.data.result;
                        this.isCardEmpty = true;
                        this.isCardFrist = false;
                        this.msg(res.data.message);
                    }
                });
        },
        selectCard(item, index) {

            var carll = () => {
                var itemObj = item;
                //只能选中其中一个
                if (!itemObj.isSelect) {
                    for (var i = 0; i < this.cardInfoObj.length; i++) {
                        this.cardInfoObj[i].isSelect = false;
                    }
                }
                itemObj.isSelect = false;
                this.$set(this.cardInfoObj[index], index, itemObj);
                // 触发数组检测
                this.cardInfoObj.push(11);
                this.cardInfoObj.pop(11);
                this.vipOpen = !this.vipOpen;
                if (this.bcardPay) {
                    var left = item.balance;
                    if (left < this.getPayAmout()) {
                        this.msg('余额不足');
                    } else {
                        this.membalance = item.balance;
                        this.cardPay(item.cardNo);
                    }
                } else {
                    this.updateCardInfoToMaster(item);
                }
            };
            if (this.bcardPay) {
                MessageBox({
                    title: "提示",
                    message: "确定使用会员卡支付?",
                    showCancelButton: true,
                    closeOnClickModal: false,
                    confirmButtonText: "确定"
                }).then(action => {
                    if (action == "confirm") {
                        carll();
                    }
                });
            } else {
                carll();
            }

        },
        updateCardInfoToMaster(item) {
            this.$http
                .post("/ohotel/posApi/discountByCode", {
                    accnt: this.orderBaseInfo.checkNum + "_" + this.orderBaseInfo.checkSeq,
                    posMode: item.dscMode,
                    cardInfo: item.cardNo,
                })
                .then(res => {
                    if (res.data.success) {
                        this.msg('绑定会员卡成功');
                        this.getOpenChecks();
                    } else {                   
                        this.msg(res.data.message);
                    }
                });
        },
        printError(v) {
            MessageBox.confirm("打印出错:" + v + "，继续打印?")
                .then(action => {
                    printSimphonyData(this.printSucceed, this.printError, this.lastPrintObj);
                })
                .catch(action => {});
        },
        // 调用硬件的UnionPay
        checkUnionPay() {
            if (this.getPayAmout() <= 0) {
                this.msg("本订单不需要支付");
                return;
            }
            if (this.detailSummary.payment > 0) {
                this.msg("本订单已存在支付，不支持MiPOS操作");
                return;
            }
            unionPay(
                this.successUnionPay,
                null,
                Math.round(this.getPayAmout() * 100),
                this.getOrderNo()
            );
        },
        checkUnionScan(pw) {
            if (this.getPayAmout() <= 0) {
                this.msg("本订单不需要支付");
                return;
            }
            if (this.detailSummary.payment > 0) {
                this.msg("本订单已存在支付，不支持MiPOS操作");
                return;
            }
            unionScan(
                this.successUnionPay,
                null,
                Math.round(this.getPayAmout() * 100),
                this.getOrderNo(),
                pw
            );
        },
        getOrderNo() {
            return this.orderBaseInfo.checkNum + "" + this.orderBaseInfo.checkSeq;
        },
        //转房间
        toFrontDest() {
            if (!this.orderBaseInfo) {
                this.msg("请先查询桌号");
            } else {
                if (this.getPayAmout() <= 0) {
                    this.msg("本订单不需要支付");
                    return;
                }
                localStorage.orderDetail = JSON.stringify(this.orderDetail);
                this.$router.push("operaToFrontDest");
            }
            // var transData = {
            //     amt:this.getPayAmout(),
            //     CARD_ISSUER:"支付宝支付",
            //     traceNo:"1234",
            //     refNo:"1234",

            // };
            // console.log(JSON.stringify(transData));
            // this.checkOutOrder(transData);
        },
        showList() {
            this.show = !this.show;
        },
        openDetail() {
            this.popupVisible = true;
            this.$refs.masterListC.getMasterList(this.orderBaseInfo);
        },
        // 打印成功后
        printSucceed(value) {

        },
        // 打印
        printOrder() {
            if (this.isEmpty) {
                this.msg("请输入桌号");
            } else {
        
                    printData(
                        this.printSucceed,
                        null,
                        this.orderDetailObj
                    );
                //todo 添加空数据判断，一定要查出房间数据才能打印
            }
        },
        msg(msg) {
            Toast({
                message: msg,
                position: "bottom",
                duration: 3000
            });
        },
        getCheckDetail(checkNumber, checkSeqNumber) {
            this.$http
                .post("/ohotel/posApi/getCheckDetail", {
                    checkNumber: checkNumber,
                    checkSeqNumber: checkSeqNumber
                })
                .then(res => {
                    if (res.data.success) {
                        if (res.data.result.length == 0) {
                            this.msg("没有找到数据");
                            this.detailLine = null;
                            this.detailSummary = null;
                            this.orderDetail = null;
                        } else {
                            let result = res.data.result;
                            this.detailLine = result.detailLines.detailLine;
                            this.detailSummary = result.summary;
                            this.orderDetail = result;
                        }
                    } else {
                        this.isMultiple = false;
                        this.orderBaseInfo = null;
                        this.isEmpty = true;
                        this.isFrist = false;
                        this.msg(res.data.message);
                    }
                });
        },
        getOpenChecks() {
            this.$http
                .post("/ohotel/posApi/getOpenChecks", this.orderReqObj)
                .then(res => {
                    // console.log(res)
                    if (res.data.success) {
                        if (res.data.result == null || res.data.result.length == 0) {
                            this.msg("没有找到数据");
                            this.isEmpty = true;
                            this.isFrist = false;
                        } else {
                            let result = res.data.result;
                            if (result.length > 1) {
                                this.isMultiple = true;
                                this.multipleBaseInfo = result;
                            } else {
                                this.isMultiple = false;
                                this.orderBaseInfo = result[0];
                                localStorage.setItem(
                                    "OpenChecks",
                                    JSON.stringify(this.orderBaseInfo)
                                );
                                this.getCheckDetail(result[0].checkNum, result[0].checkSeq);
                            }
                            this.isEmpty = false;
                            this.isFrist = false;
                        }
                    } else {
                        this.msg(res.data.message);
                        this.orderBaseInfo = {};
                        this.isEmpty = true;
                        this.isFrist = false;
                        this.isSucceed = false;
                    }
                });
        },
        selectOpenChecks(item, index) {
            this.orderBaseInfo = this.multipleBaseInfo[index];
            this.isMultiple = false;
            localStorage.setItem("OpenChecks", JSON.stringify(this.orderBaseInfo));
            this.getCheckDetail(
                this.orderBaseInfo.checkNum,
                this.orderBaseInfo.checkSeq
            );
        },
        searchOrder() {
            if (!this.orderReqObj.tableNo) {
                this.msg("请输入正确桌号");
            } else {
                this.isSucceed = false;
                this.getOpenChecks();
            }
        },
        //银行卡,支付宝支付成功
        successUnionPay(value) {
            if (value.transData.resCode == "00") {
                this.checkOutOrder(value.transData);
            } else {
                //failed 支付失败，弹出对应的支付失败的错误码
                this.msg(value.transData.resDesc);
            }
        },
        checkOutOrder(transData) {
            var postObj = {};
            postObj.employeeObjectNum = this.orderBaseInfo.checkEmployeeObjectNum;
            postObj.checkRevenueCenterID = this.orderBaseInfo.checkRevenueCenterObjectNum;
            postObj.checkNum = this.orderBaseInfo.checkNum;
            postObj.checkSeq = this.orderBaseInfo.checkSeq;

            postObj.paymenttotal = Number(transData.amt); //this.getPayAmout();
            postObj.tmedObjectNum = getPayWayPOS(transData);

            postObj.orderTypeStr = this.orderBaseInfo.orderTypeStr;
            postObj.amount = postObj.paymenttotal; //this.orderBaseInfo.amount;
            postObj.periodID = this.detailSummary.servingPeriodID;
            // postObj.serviceAmount = this.detailSummary.autoGratuity;
            // postObj.taxAmount = this.detailSummary.tax;
            // postObj.tipAmount = this.detailSummary.other;
            // postObj.disAmount = this.getAlldsc();
            postObj.accountJson = getAccountJson(this.orderDetail);

            postObj.checkEmployee = this.orderBaseInfo.checkEmployee;
            postObj.checkRevenueCenter = this.orderBaseInfo.checkRevenueCenter;
            // postObj.balance='';
            postObj.orderno = transData.traceNo;
            postObj.serialno = transData.refNo;
            postObj.tableno = this.orderBaseInfo.checkTableObjectNum;
            postObj.checkin = this.vc.checkIn ? 0 : 1;
            if (transData.eSignJpeg) {
                transData.eSignJpeg = null;
            }
            postObj.remarkjson = JSON.stringify(transData);

            // console.log(JSON.stringify(postObj))

            this.$http.post("/ohotel/posApi/checkInAccount", postObj).then(res => {
                if (res.data.success) {
                    if (res.data.message != null && res.data.message.indexOf("300_") == 0) {
                        var arr = res.data.message.split('_');
                        MessageBox('提示', '关单成功，结账金额与Opera抛多金额不一致，请人工补账，上次抛账金额为：' + arr[2]);
                    } else {
                        this.msg("入账成功");
                    }
                    this.PrintV(getPayWayPOS(transData));

                    //支付成功后显示对应的页面内容
                    this.isEmpty = true;
                    this.isFrist = true;
                    this.isSucceed = true;
                    this.orderBaseInfo = {};
                    this.orderReqObj.tableNo = "";
                    this.orderDetailObj = this.orderDetailDefaultObj;
                } else {
                    MessageBox.alert(res.data.message);
                }
            });
        },

        cardPay(cardNo) {
            var postObj = {};
            postObj.employeeObjectNum = this.orderBaseInfo.checkEmployeeObjectNum;
            postObj.checkRevenueCenterID = this.orderBaseInfo.checkRevenueCenterObjectNum;
            postObj.checkNum = this.orderBaseInfo.checkNum;
            postObj.checkSeq = this.orderBaseInfo.checkSeq;

            postObj.paymenttotal = this.getPayAmout();
            postObj.tmedObjectNum = 'RCV';

            postObj.orderTypeStr = this.orderBaseInfo.orderTypeStr;
            postObj.amount = postObj.paymenttotal; //this.orderBaseInfo.amount;
            postObj.periodID = this.detailSummary.servingPeriodID;
            // postObj.serviceAmount = this.detailSummary.autoGratuity;
            // postObj.taxAmount = this.detailSummary.tax;
            // postObj.tipAmount = this.detailSummary.other;
            // postObj.disAmount = this.getAlldsc();
            postObj.accountJson = getAccountJson(this.orderDetail);

            postObj.checkEmployee = this.orderBaseInfo.checkEmployee;
            postObj.checkRevenueCenter = this.orderBaseInfo.checkRevenueCenter;
            // postObj.balance='';
            postObj.orderno = '';
            postObj.serialno = '';
            postObj.cardInfo = cardNo;
            postObj.tableno = this.orderBaseInfo.checkTableObjectNum;
            postObj.checkin = this.vc.checkIn ? 0 : 1;

            this.$http.post("/ohotel/posApi/checkInAccount", postObj).then(res => {
                if (res.data.success) {
                    if (res.data.message != null && res.data.message.indexOf("300_") == 0) {
                        var arr = res.data.message.split('_');
                        MessageBox('提示', '关单成功，结账金额与Opera抛多金额不一致，请人工补账，上次抛账金额为：' + arr[2]);
                    } else {
                        this.msg("入账成功");
                    }
                    this.PrintV('RCV');

                    //支付成功后显示对应的页面内容
                    this.isEmpty = true;
                    this.isFrist = true;
                    this.isSucceed = true;
                    this.orderBaseInfo = {};
                    this.orderReqObj.tableNo = "";
                    this.orderDetailObj = this.orderDetailDefaultObj;
                } else {
                    MessageBox.alert(res.data.message);
                }
            });
        },

        getPayAmout() {
            return Number(this.orderBaseInfo.checkTotalDue);
        },
        getAlldsc() {
            let dsc = 0;

            if (!this.detailLine) {
                return dsc;
            }
            this.detailLine.forEach(function (el, key, arr) {
                if (el.detailType == "DtlDscType") {
                    dsc += parseFloat(el.total);
                }
            });
            return dsc;
        },
        reInAccount() {
            if (!this.orderReqObj.tableNo) {
                this.msg("请输入桌号");
                return;
            }
            // console.log(this.getOrderNo());
            reAccount(this.successUnionPay, null, this.getOrderNo());
        },
        getOtherdsc() {
            let dsc = 0;
            if (!this.detailLine) {
                return dsc;
            }
            this.detailLine.forEach(function (el, key, arr) {
                if (el.detailType == "DtlSvcType") {
                    dsc += parseFloat(el.total);
                }
            });
            return dsc;
        }
    },
    filters: {
        tofixed(v) {
            var f = parseFloat(v);
            return isNaN(f) ? "" : f.toFixed(2);
        },
        datef(value) {
            return moment(value)
                .add(8, "hour")
                .format("MM/DD HH:mm");
        }
    },
    components: {
        topNav,
        actionsheet: Actionsheet,
        menuList,
        operaOrderDetail
    }
};
</script>

