<template>
<div class="opRefund">
    <div class="topHeader" style="height:1.1rem;">
        <header class="mint-header is-fixed" style="background-color:#00487c;">
            <div class="mint-header-button is-left">
                <a href="#/operapay" class="router-link-active">
              <i class="mintui mintui-back"></i>
            </a>
            </div>
            <h1 class="mint-header-title">退款</h1>
            <div class="mint-header-button is-right">
                <mt-button icon="more" @click="sheetVisible=true" slot="right"></mt-button>
                <actionsheet :actions="actions" v-model="sheetVisible"></actionsheet>
            </div>
        </header>
    </div>
    <div class="dateAll">
        <div class="searchBox">
            <div class="search">
                <input class="tableNuber" type="text" placeholder="桌号" v-model='searchOption.tableNo'>
                <input class="orderNumer" type="text" placeholder="账单号" v-model='searchOption.checkNumbers'>
                <div class="searchResult" @click='search'>查询</div>
            </div>
        </div>
    </div>
    <div class="scrollBox">
        <div class="orderList" v-for='item in listInfo' :key='item.checkNum'>
            <div class="blueLind"></div>
            <div class="infoL" @click='openDetail(item)'>
                <div class="lineInfo">
                    <div class="titleThird">账单：</div>
                    <span>{{item.checkNum}}</span>
                </div>
                <div class="lineInfo">
                    <div class="titleThird">餐厅：</div>
                    <span>{{item.checkRevenueCenter}}</span>
                </div>
                <div class="lineInfo">
                    <div class="titleThird">订单金额：</div>
                    <span><small>￥</small>{{item.amount | tofixed}}</span>
                </div>
                <div class="lineInfo">
                    <div class="titleThird">开单时间：</div>
                    <span>{{item.checkOpenTime | fd}}</span>
                </div>
            </div>
            <div class="infoL infoR" @click='openDetail(item)'>
                <div class="lineInfo">
                    <span class="ss">{{item.checkEmployee}}</span>
                </div>
                <div class="lineInfo" v-if="item.payCode == 'PUN_ROOM'">
                    <span class="ss">房间：<font style="color:red">{{item.orderNo}}</font></span>
                </div>
                <div class="lineInfo" v-else-if="item.refunds == 0">
                    <div class="titleThird">状态：</div>
                    <span>已结</span>
                </div>
                <div class="lineInfo" v-else>
                    <div class="titleThird">已退款：</div>
                    <span style="color:red"><small>￥</small>{{item.refunds | tofixed}}</span>
                </div>
                <div class="lineInfo">
                    <div class="titleThird">桌号：</div>
                    <span>{{item.checkTableName}}</span>
                </div>
                <!-- <div class="lineInfo">
            <div class="titleThird">应付金额：</div>
            <span>{{item.checkTotalDue  | tofixed}}</span>
          </div> -->
                <div class="lineInfo">
                    <div class="titleThird">{{item.payCode | payName}}：</div>
                    <span><small>￥</small>{{item.payTotal}}</span>
                </div>
            </div>
            <i class="mintui mintui-back" ></i>
            <div class="returnPay">
                <div class="returnButton detailButton" @click='openDetail(item)'>详情</div>
                <div style="color:#ccc;margin-top:13px;float:right" v-if="item.payCode == 'PUN_ROOM'">挂客房订单不可撤消</div>
                <div class="returnButton" @click='showAlter(item)' v-else-if="parseFloat(item.payTotal) > parseFloat(item.refunds)">撤销</div>
            </div>
        </div>
        <div v-bind:class="{ 'hide': !isEmpty }">
            <img class="nothing" src="../../assets/img/nothing.png" v-show="!isSucceed">
            <p class="nothingTxt" v-show="!isFrist && !isSucceed">什么都没有</p>
            <p class="nothingTxt" v-show="isFrist">请输入查询条件查询退款订单<br>(仅能退营业日当天订单）</p>
                <img class="nothing" src="../../assets/img/chenggong.png" v-show="isSucceed">
                <p class="nothingTxt" v-show="isSucceed">撤销成功</p>
        </div>
    </div>
    <div class="alertBox" v-if='showTwo==true && currentItem'>
        <div class="bgBox"></div>
        <div class="contentBox">
            <div class="top">
                <span>交易退款</span>
                <div class="iconfont icon-cuowu" @click='showTwo=false'></div>
            </div>
            <div class="tip"> 可退金额：<b style="color:red"><small>￥</small>{{(currentItem.payTotal - currentItem.refunds)  | tofixed}}</b><span style="margin-left:0.3rem;font-size:10px" @click="setfullRefund">[全部退款]</span>

                <div v-if="currentItem.refunds> 0">已退金额：<b style="color:red"><small>￥</small>{{currentItem.refunds}}</b></div>
            </div>
            <div class="inputTwo">
                <input type="text" placeholder="退款金额" class="inp" v-model="rmoney">
                <div style="color:#ccc;margin-top:0.3rem"><b style="color:red">注意</b>：请先Simphony补账务操作后，再退款</div>
            </div>
            <span class="twoSure" @click="refund()">确认</span>
        </div>
    </div>
    <mt-popup v-model="popupVisible" position="right" modal='false'>
        <div class="topHeader">
            <header class="mint-header is-fixed" style="background-color:#00487C;">
                <div class="mint-header-button is-left">
                    <a class="router-link-exact-active router-link-active" @click='popupVisible=false' style="padding:.2rem .6rem .2rem 0;">
              <i class="mintui mintui-back"></i>
            </a>
                </div>
                <h1 class="mint-header-title">餐单详情</h1>
                <div class="mint-header-button is-right">
                </div>
            </header>
        </div>
        <operaOrderDetail ref="ordetailf"></operaOrderDetail>
    </mt-popup>
    <mt-popup v-model="popupAdmin" style="width:100%;height:100%" modal='true' closeOnClickModal='false'>
        <adminpass @success="popupAdmin=false"></adminpass>
    </mt-popup>
</div>
</template>

<script>
import operaOrderDetail from "../common/operaOrderDetail";
import adminpass from "../common/adminpass";
import moment from "moment";
import {
    Toast,
    MessageBox,
    Actionsheet
} from "mint-ui";

export default {
    data() {
        return {
            isEmpty: true,
            isFrist: true,
            isSucceed: false,
            popupVisible: false,
            popupAdmin: true,
            orderInfoObj: "",
            listInfo: [],
            showTwo: false,
            currentItem: null,
            rmoney: null,
            sheetVisible: false,
            actions: [],
            searchOption: {
                lookUpStartDate: moment().format("YYYY-MM-DD"),
                checkNumbers: "",
                tableNo: "",
                orderTypeID: null,
                rvcObjectNum: null,
                currentPage: 1,
                pageSize: 50
            },
            vc: !!localStorage.vc ? JSON.parse(localStorage.vc) : {
                invoice: false,
                rvcObjectNum: ''
            },
            SYS_DEVICE: ''
        };
    },
    computed: {},
    mounted() {
        this.SYS_DEVICE = this.$SYS_DEVICE;
        this.searchOption.rvcObjectNum = this.vc.rvcObjectNum;
        if (this.SYS_DEVICE == "NG") {
            this.actions.push({
                name: "退款核对",
                method: this.reDoFunc
            });
        }
    },
    filters: {
        tofixed(v) {
            return parseFloat(v).toFixed(2);
        },
        payName(v) {
            return getPayWayName(v);
        },
        fd(value) {
            return moment(value)
                .format("HH:mm:ss");
        }
    },
    methods: {
        search() {
            if (
                this.searchOption.tableNo == "" &&
                this.searchOption.checkNumbers == ""
            ) {
                this.msg("查询内容不能为空");
            } else {
                this.getInfo();
            }
        },
        setfullRefund() {
            this.rmoney = (this.currentItem.payTotal - this.currentItem.refunds).toFixed(2);
        },
        reDoFunc() {
            if (localStorage.getItem("simlastReTraceNo") == null) {
                this.msg('没有退款记录');
            } else {
                reDoFund(this.refundSucceed, null, localStorage.getItem("simlastReTraceNo"), localStorage.getItem("simlastRefundNo"));
            }
        },
        refund() {
            var m = parseFloat(this.rmoney);
            if (isNaN(m)) {
                this.msg('请输入数字金额');
                return;
            }
            if (m < 0) {
                this.msg('金额必需大于0');
                return;
            }
            if (m > (this.currentItem.payTotal - this.currentItem.refunds).toFixed(2)) {
                this.msg('退款金额超出最大退款金额');
                return;
            }
            if (m.toFixed(2) != m) {
                this.msg('退款金额格式错误');
                return;
            }
            this.showTwo = false;

            //流水号
            var traceNo = this.currentItem.orderNo;
            localStorage.setItem("simlastReTraceNo", traceNo);
            localStorage.setItem("simlastRefundNo", this.currentItem.refundNo);
            var credit = Math.round(m.toFixed(2) * 100);
            var paycodeDesc = this.currentItem.payCode;
            refund(this.refundSucceed, null, traceNo, credit, paycodeDesc, this.currentItem);
        },
        refundSucceed(value) {
            if (value.transData.resCode == '00') {
                var m = parseFloat(this.rmoney);
                var pd = {
                    checkNum: this.currentItem.checkNum,
                    checkSeq: this.currentItem.checkSeq,
                    orderno: this.currentItem.refundNo ? this.currentItem.refundNo + "T" : this.currentItem.orderNo + "_T",
                    amount: m,
                    tableNo: this.currentItem.checkTableObjectNum,
                    rvcNum: this.searchOption.rvcObjectNum
                };
                this.$http
                    .post("/ohotel/posApi/refundCheck", pd)
                    .then(res => {
                        if (res.data.success) {
                            this.msg("账单撤销成功");
                            this.isEmpty = true,
                                this.isFrist = false;
                            this.rmoney = null,
                                this.isSucceed = true;
                            this.currentItem = null;
                            this.listInfo = [];
                            localStorage.removeItem("simlastReTraceNo");
                            localStorage.removeItem("simlastRefundNo");
                        } else {
                            this.msg(res.data.message);
                        }
                    });
            } else {
                this.msg(value.transData.resDesc);
            }
        },
        showAlter(item) {
            MessageBox.confirm("确定撤消?")
                .then(action => {
                    if (this.SYS_DEVICE == "NG") {
                        this.currentItem = item;
                        this.showTwo = !this.showTwo;
                    } else {
                        this.currentItem = item;
                        this.rmoney = item.payTotal;
                        this.refund();
                    }
                })
                .catch(action => {});
        },
        openDetail(item) {
            this.popupVisible = true;
            // console.log(JSON.stringify(item));
            this.$refs.ordetailf.getMasterList(item);
            //  this.$router.push("/operaOrderDetail?checkNumber="+item.checkNum+"&checkSeqNumber="+item.checkSeq+"&redirect=/opTransactSearch");
        },
        getInfo() {
            this.$http
                .post("/ohotel/posApi/getChecks", this.searchOption)
                .then(res => {
                    if (res.data.success) {
                        this.listInfo = res.data.result;
                        this.isFrist = false;
                        this.isEmpty = this.listInfo == null || this.listInfo.length == 0;
                    } else {
                        this.isEmpty = true;
                        this.listInfo = [];
                        this.msg(res.data.message);
                    }
                });
        },
        msg(msg) {
            Toast({
                message: msg,
                position: "bottom",
                duration: 3000
            });
        }
    },
    components: {
        operaOrderDetail,
        adminpass,
        Actionsheet
    }
};
</script>

<style lang="less">
.opRefund {
    .testButton {
        width: 40%;
        height: 0.9867rem;
        text-align: center;
        line-height: 0.9067rem;
        color: #fff;
        font-size: 0.3733rem;
        background: #ff8902;
        border-radius: 0.1867rem;
        display: inline-block;
        margin-top: 0.4667rem;
        margin-left: 0.36rem;
    }

    overflow-y: hidden;

    .dateAll {
        width: 100%;
        background: #fff;

        margin-top: -1px;
        z-index: 2000;

        .dataBox {
            margin: 0 auto;
            width: 96%;
            height: 100%;

            .data {
                width: 59%;
                height: 1.0667rem;
                margin-top: 0.1067rem;
                display: inline-block;

                .iconBox {
                    border-radius: 2.6667rem;
                    width: 0.8533rem;
                    height: 0.8533rem;
                    display: inline-block;
                    text-align: center;
                    background: #fff;
                    -moz-transform: none;
                    -webkit-transform: none;
                    -o-transform: none;
                    -ms-transform: none;
                    transform: none;

                    .icon-jiantou,
                    .icon-caret-right-copy {
                        font-size: 0.32rem;
                        line-height: 0.88rem;
                        color: #d8d8d8;
                        transform: scale(0.7);
                    }
                }

                .blakBg {
                    background: #f6f8f9 !important;
                    border-radius: 2.6667rem;
                }

                .fontBlack {
                    color: black !important;
                }

                .dataDtail {
                    display: inline-block;
                    font-size: 0.3733rem;
                    color: #393c41;
                    margin: 0.2667rem 0.3733rem 0rem;
                }
            }

            .line {
                width: 0.0267rem;
                text-align: center;
                display: inline-block;
                vertical-align: top;
                margin-top: 0.3733rem;
                height: 0.5867rem;
                background: #cccccc;
            }

            .allBox {
                width: 38%;
                line-height: 1.0667rem;
                height: 1.0667rem;
                display: inline-block;
                color: #393c41;
                font-size: 0.32rem;
                vertical-align: top;
            }
        }

        .searchBox {
            height: 1.6rem;
            margin: 0 auto;
            width: 100%;

            background: #f4f5f7;

            .search {
                width: 94%;
                margin: 0 auto;
                height: 0.8rem;
                padding-top: 0.4rem;

                .tableNuber {
                    width: 21%;
                    height: 0.7733rem;
                    font-size: 0.3733rem;
                    padding-left: 0.2667rem;
                    background: #fff;
                    border: 0.0267rem solid #cccccc;
                    border-radius: 0.1333rem;
                    margin-left: 0.1333rem;
                    float: left;
                }

                .orderNumer {
                    width: 41%;
                    height: 0.7733rem;
                    font-size: 0.3733rem;
                    padding-left: 0.2667rem;
                    background: #fff;
                    border: 0.0267rem solid #cccccc;
                    border-radius: 0.1333rem;
                    float: left;
                    margin-left: 0.2667rem;
                }

                .searchResult {
                    width: 20.5%;
                    height: 0.7733rem;
                    font-size: 0.3733rem;
                    background: #d8af68;
                    border-radius: 0.1867rem;
                    float: right;
                    color: #fff;
                    line-height: 0.7467rem;
                    margin-right: 0.08rem;
                    margin-top: 0.0533rem;
                }
            }
        }
    }

    .scrollBox {
        width: 100%;
        height: 79%;
        overflow-y: auto;
        padding-bottom: 2rem;

        .orderList {
            background: #ffffff;
            margin-bottom: 0.2667rem;
            width: 96%;
            height: auto;
            padding-bottom: 0.2667rem;
            margin: 0 auto 0.2667rem;
            box-shadow: 0 0 0.2133rem 0rem #dadada;
            border-radius: 0.1333rem;
            position: relative;

            .mintui {
                position: absolute;
                top: 30%;
                right: 5%;
                color: #ccc;
                transform: rotate(180deg);
            }

            .blueLind {
                height: 0.3733rem;
                width: 1.1%;
                background: #646f83;
                float: left;
                margin-top: 0.48rem;
            }

            .lineInfo {
                margin-bottom: 0.1067rem;
                color: #999999;
                clear: both;

                .ss {
                    width: 4rem;
                    font-size: 0.32rem;
                }

                .titleThird {
                    color: #999999;
                    letter-spacing: 0rem;
                    float: left;
                }

                span {
                    font-size: 0.3733rem;
                    color: #151515;
                    width: 2.5rem;
                    float: left;
                    white-space: nowrap;
                    text-overflow: ellipsis;
                    overflow: hidden;
                }
            }

            .infoL {
                width: 49%;
                height: auto;
                text-align: left;
                display: inline-block;
                padding-top: 0.3733rem;
            }

            .infoR {
                width: 45%;
            }

            .infoBo {
                text-align: left;
                margin-left: 0.3733rem;

                b {
                    font-size: 0.32rem;
                    color: #4990e2;
                    text-decoration: underline;
                    margin-left: 0.1333rem;
                }
            }
        }
    }

    .nothing {
        width: 80px;
        height: 88px;
        margin: 150px auto 0 auto;
    }

    .returnPay {
        width: 94%;
        height: 1.2rem;
        border-top: 0.0267rem solid #eeeeee;
        margin: 0 auto;

        .returnButton {
            float: right;
            width: 33%;
            height: 0.8267rem;
            background: #ff8902;
            border-radius: 0.1867rem;
            color: #fff;
            font-size: 0.3733rem;
            line-height: 0.7467rem;
            margin-top: 0.2133rem;
        }

        .detailButton {
            float: left;
            background: #2d8cf0;
        }

        .icon-you {
            float: right;
            font-size: 0.3733rem;
            line-height: 1.1733rem;
            margin-right: 0.08rem;
        }
    }

    .nothingTxt {
        text-align: center;
        font-size: 20px;
        color: #ccd1d5;
        margin-top: 20px;
    }

    .show {
        display: block;
    }

    .hide {
        display: none;
    }

    .mint-popup-right {
        width: 100%;
        height: 100%;
    }

    .infoWarn {
        height: 50px;
    }

    .alertBox {
        .bgBox {
            opacity: 0.7;
            background: #000000;
            width: 100%;
            height: 100%;
            position: fixed;
            top: 0rem;
        }

        .contentBox {
            width: 80%;
            height: 7.3067rem;
            background: #FFFFFF;
            position: fixed;
            top: 14%;
            left: 10%;

            .tip {
                text-align: left;
                margin: 0.2rem 1rem;

                b {
                    font-size: 0.5rem;
                }
            }

            .top {
                width: 92%;
                height: 1.1467rem;
                margin: 0 auto;

                span {
                    font-size: 0.32rem;
                    color: #333333;
                    float: left;
                    margin-top: 0.3467rem;
                    font-weight: bold;
                }

                .icon-cuowu {
                    font-size: 0.32rem;
                    color: #999999;
                    float: right;
                    margin-top: 0.2667rem;
                }
            }

            .inputTwo {
                height: 3rem;
                width: 76.7%;
                margin: 0 auto;

                .inp {
                    width: 100%;
                    height: 0.9867rem;
                    text-transform: uppercase;
                    background: #FFFFFF;
                    border: 0.0267rem solid #CCCCCC;
                    border-radius: 0.1333rem;
                    margin-top: 0.2933rem;
                    padding-left: 0.3733rem;
                }
            }

            .twoSure {
                font-size: 0.3733rem;
                color: #FFFFFF;
                width: 45%;
                height: 0.9867rem;
                line-height: 0.9067rem;
                background: #FF8902;
                border-radius: 0.1867rem;
                display: inline-block;
                margin-bottom: 0.3467rem;
            }

            p {
                font-size: 0.32rem;
                color: #999999;
            }
        }
    }
}
</style>



