<template>
  <div class="opDetail_box">
  
    <div  v-if="orderInfoObj">
      <div class="oDTop">
        <div class="topL">账单号：{{orderInfoObj.summary.checkNumber}}</div>
        <div class="topR">
          <span>状态：</span>
          <div class="circleBox" v-if="item.payCode && item.refunds == 0">
            已结
          </div>
           <div class="circleBox" v-else-if="item.refunds > 0">
            退款
          </div>
          <div class="circleBox" v-else>
            待结
          </div>
        </div>
      </div>
      <div class="restaurantList">
        <div class="listBox">
          <div class="blueLind"></div>
          <div class="vipCheckBox">
            <div class="infoBo">
              <span>基本信息</span>
            </div>
          </div>
          <div class="infoDetail clearfix">
            <ul>
               <li>   <p>
                  <label>桌号：</label>
                  {{item.checkTableName}}
                </p>
              </li>
              <li>   <p>
                  <label>人数：</label>   {{orderInfoObj.summary.covers}}
                </p>
              </li>
             <li>
                <p>
                  <label>订单类型：</label>
                  {{item.orderTypeStr}}</p>
              </li>
              <li>
                <p>
                  <label>员工：</label>
                  {{item.checkEmployee}}</p>
              </li>
              <li>
                <p>
                  <label>餐厅名称：</label>
                 {{item.checkRevenueCenter}}</p>
              </li>
              <li class="p100">
                <p class="p100">
                  <label>开单时间：</label>{{orderInfoObj.summary.checkOpenTime | datef }}</p>
                
              </li>
               <li class="p100">
                <p class="p100">
                  <label>开桌时间：</label>{{orderInfoObj.summary.tableOpenTime | datef }}</p>
                
              </li>
              
               <li class="p100">
                <p class="p100">
                  <label>服务时间：</label>{{orderInfoObj.summary.serviceTotalTime | datef }}</p>
              </li>
                
            </ul>
          </div>
        </div>
     </div>

       <div class="restaurantList">
        <div class="listBox">
          <div class="blueLind"></div>
          <div class="vipCheckBox">
            <div class="infoBo">
              <span>账务信息</span>
            </div>
          </div>
          <div class="infoDetail clearfix">
            <ul>
              
              <li>
                <p>
                  <label>合计：</label>
                  <small>￥</small> {{(orderInfoObj.summary.subTotal - getAlldsc())  | tofixed}}</p>

              </li>
              <li>
                <p>
                  <label>服务费：</label>
                  <small>￥</small>{{orderInfoObj.summary.autoGratuity  | tofixed}}</p>
              </li>
              <li v-if="orderInfoObj.summary.tax>0">
                <p>
                  <label>税费：</label>
                  <small>￥</small>{{orderInfoObj.summary.tax  | tofixed}}</p>
              </li>
              <li v-if="orderInfoObj.summary.other > 0">
                <p>
                  <label>其它费用：</label>
                  <small>￥</small>{{orderInfoObj.summary.other  | tofixed}}</p>
              </li>
              <li>
                <p>
                  <label>折扣：</label>
                  <small>￥</small> {{getAlldsc()  | tofixed}}</p>
              </li>
              <li>
                <p :class="{red:orderInfoObj.summary.payment> 0}">
                  <label>已付款：</label>
                  <small>￥</small> {{orderInfoObj.summary.payment  | tofixed}}</p>
              </li>
              <li>
                <p  :class="{red:item.checkTotalDue> 0}">
                  <label>应付款：</label>
                  <small>￥</small>{{item.checkTotalDue | tofixed}}</p>
              </li>

              <li v-if="item.payCode">
                <p class="red">
                  <label>支付方式：</label>
                  {{item.payCode | payName}}</p>
              </li>

              <li v-if="item.payTotal">
                <p  class="red">
                  <label>支付金额：</label>
                  <small>￥</small>{{item.payTotal  | tofixed}}</p>
              </li>
               <li v-if="item.refunds > 0">
                <p class="red">
                  <label>退款金额：</label>
                   <small>￥</small>{{item.refunds  | tofixed}}</p>
              </li>
                <li v-if="item.payCode == 'PUN_ROOM'">
                <p class="red">
                  <label>挂账房间：</label>
                  {{item.orderNo}}</p>
              </li>
            </ul>
          </div>
        </div>
     </div>



 <div class="restaurantList">
        <div class="listBox">
          <div class="blueLind"></div>
          <div class="vipCheckBox">
            <div class="infoBo">
              <span>菜目信息</span>
            </div>
          </div>
          <div class="infoDetail clearfix" style="margin-bottom:1rem;margin-top:0.3rem">
           <menu-list :detailLine="orderInfoObj.detailLines.detailLine"> </menu-list>
          </div>
        </div>
     </div>



 
    </div>
  </div>
</template>
<script>
import {Toast} from 'mint-ui';
import moment from "moment";
import menuList from '../common/menuList'
export default {
  data() {
    return {
      orderInfoObj: null,
      item:null,
    }
  },
  props: {

  },
  methods: {
    msg(msg) {
      Toast({
        message: msg,
        position: 'bottom',
        duration: 3000
      });
    },
     getAlldsc(){
        let dsc=0
        this.orderInfoObj.detailLines.detailLine.forEach(function(el,key,arr){
          if(el.detailType=='DtlDscType'){
            dsc+=parseFloat(el.total)
          }
        })
        return dsc
      },
    getMasterList(orderDetailObj,amount) {
      if (!orderDetailObj) {
        return;
      }
      if(amount != null){
        orderDetailObj.checkTotalDue = amount;
      }
      this.item = orderDetailObj;
     this.$http.post("/ohotel/posApi/getCheckDetail", {
          checkNumber:orderDetailObj.checkNum,
          checkSeqNumber:orderDetailObj.checkSeq,
        }).then(res=>{
           if (res.data.success) {
             this.orderInfoObj = res.data.result;
              //console.log( this.orderInfoObj);
           }else{
              this.msg(res.data.message);
           }
        })
    }
  },
  mounted() {

  },
  filters: {
    tofixed(v) {
      return parseFloat(v).toFixed(2);
    },
    datef(value) {
      return moment(value).add(8,'hour').format("YYYY/MM/DD HH:mm:ss");
    },
    payName(v){
      return getPayWayName(v);
    },
  },
  components: {
      menuList
    }
}
</script>
<style lang="less">
.opDetail_box {
  overflow: auto;
  height: 100%;
  .oDTop {
    width: 90%;
    height: 1.2533rem;
    margin: 1.1rem auto 0;
    
    font-size: 0.32rem;
    color: #333333;
    .topL {
      float: left;
      line-height: 1.2267rem;
    }
    .topR {
      float: right;
      line-height: 1.2267rem;
      span {
        display: inline-block;
      }
      .circleBox {
        width: 0.8rem;
        height: 0.54rem;
        background: #646F83;
        border-radius: 1rem;
        color: #FFF;
        display: inline-block;
        vertical-align: top;
        margin-top: 0.3733rem;
        line-height:0.54rem;
      }
    }
  }
  .restaurantList {
    background: #FFFFFF;
    margin-bottom: 0.2667rem;
    width: 96%;
    height: auto;
    margin: 0 auto 0.2667rem;
    box-shadow: 0 0 0.2133rem 0rem #dadada;
    border-radius: 0.1333rem;
    .listBox {
      width: 100%;
      height: auto;
      padding-bottom: 0.2667rem;
   
      .blueLind {
        height: 0.3733rem;
        width: 1.1%;
        background: #646f83;
        float: left;
        margin-top: 0.4rem;
      }
      .infoDetail {
        width: 90%;
        margin: 0 auto;
        text-align: left;
        margin-top: 0.2667rem;
        font-size: 0.32rem;
        color: #333333;
        ul {
          li {
            p {
              width: 50%;
              float: left;
              margin: 2px 0;
            }
          }
          .p100 {
            width: 100%;
            clear: both
          }
        }
      }
      .vipCheckBox {
        width: 94.5%;
        margin: 0 auto;
        padding-top: 0.2933rem;
        .infoBo {
          text-align: left;
          margin-left: 0.08rem;
          span {
            font-weight: bold;
            margin-left: 0.1333rem;
            font-size: 0.3733rem;
            color: #151515;
          }
        }
      }
    }
    .clearBottom {
      border-bottom: none;
    }
    .red{color:red}
  }
}
</style>


