<template>
<div class="settingPay">
   <topNav title="Simphony参数设置" url="/operapay"></topNav>
    <div class="titleS">请选择OHotel接口地址</div>
    <div class="settingContent">
         <table>
             <tr>
               <th style="width:8%;"></th>
               <th style="width:23.3%">网络</th>
               <th>地址 </th>
               <th style="width: 8.5%;">响应</th>
             </tr>
             <tr v-for="item in address" :key="item.url">
               <td style="border:none">
                   <div class="checkBox">
                       <input type="radio" :value="item.url" name="urlcheck" v-model="vurl" :checked="select != null && select.url == item.url">
                   </div>
               </td>
               <td>{{item.name}}</td>
               <td>{{item.url}}</td>
               <td>{{item.ping}}</td>
             </tr>
         </table>
         <div class="settingAll">
           <div class="allBox">
              <div class="testButton" @click="ping(0)">测试</div>
              <router-link to='addApiSim'>
                   <div class="testButton">新增</div>
              </router-link>
              <div class="testButton" @click="selectUrl">选择</div>
              <div class="testButton" @click="clear">清空接口</div>
            </div>
         </div>
    </div>
    <div class="defaultNumber">
       <h3>餐厅#</h3>
      <div class="inputNumber">
         <input type="text" class="workNumber" placeholder="不填写获取EMC配置的餐厅" v-model="vc.rvcObjectNum" @blur="saveParams">
      </div>
    
      <div class="switch clearfix">
        <label>一键开票</label>
          <mt-switch v-model="vc.invoice" @change="saveParams"></mt-switch>
      </div>
       <div class="switch clearfix">
        <label>是否会员</label>
          <mt-switch v-model="vc.isMember" @change="saveParams"></mt-switch>
      </div>
      <div class="switch clearfix">
        <label>是否手写卡号</label>
          <mt-switch v-model="vc.hCardNo" @change="saveParams"></mt-switch>
      </div>
        <h3>读卡扇区号</h3>
      <div class="inputNumber">
         <input type="text" class="workNumber" placeholder="" v-model="vc.RFCardSec" @blur="saveParams">
      </div>
       <div class="switch clearfix">
        <label>是否支持客房挂账</label>
          <mt-switch v-model="vc.isPunRoom" @change="saveParams"></mt-switch>
      </div>
       <div class="switch clearfix">
        <label>是否结账后自动Simphony关单（需稳定的接口）</label>
        <mt-switch v-model="vc.checkIn" @change="saveParams"></mt-switch>
      </div>
    </div>
 <mt-popup v-model="popupAdmin"  style="width:100%;height:100%" modal='true' closeOnClickModal='false'>
         <adminpass @success="popupAdmin=false"></adminpass>
    </mt-popup>
</div>
</template>
<script>
import { Toast, Switch } from "mint-ui";
import adminpass from "../common/adminpass";
import axios from "axios";

export default {
  data() {
    return {
      address: [],
      select: null,
      vurl: "",
      popupAdmin:this.$route.query.notp? false:true,
      popupVisible: true,
      vc: !!localStorage.vc
        ? JSON.parse(localStorage.vc)
        : {
            invoice: false,
            rvcObjectNum: "",
            isPunRoom:false,
            checkIn:true,
            isMember:false,
            hCardNo:false,
            RFCardSec:null,
          },
      
    };
  },
  mounted() {

    this.success();
  },
  methods: {
    clear() {
      localStorage.removeItem("simgateway");
      localStorage.removeItem("simaddress");
      this.address = [];
    },
    success() {
      this.popupVisible = false;
      var selectJson = localStorage.getItem("simgateway");
      if (selectJson != null) {
        this.select = JSON.parse(selectJson);
        this.vurl = this.select.url;
      }
      var adds = localStorage.getItem("simaddress");
      //console.log(selectJson);
      // console.log(adds);
      if (adds != null) {
        var urls = adds.split("|");
        for (var i = 0; i < urls.length; i++) {
          var data = JSON.parse(urls[i]);
          data.ping = "";
          this.address.push(data);
        }
        this.ping();
      }
    },
    saveParams() {
      localStorage.setItem("vc", JSON.stringify(this.vc));
    },
   
    ping(i) {
      if (i == undefined) {
        i = 0;
      }
      var add = this.address;
      if (add.length > i) {
        var item = this.address[i];
        var requestTime = new Date().getTime();
        var instance = axios.create({ timeout: 5000 });
        instance
          .get(item.url + "/ohotel/posApi/ping")
          .then(res => {
            var responseTime = new Date().getTime();
            var pv = Math.abs(requestTime - responseTime);
            item.ping = pv;
          })
          .catch(function(error) {
            item.ping = "超时";
          });
        this.ping(i + 1);
      }
    },
    selectUrl() {
      for (var i = 0; i < this.address.length; i++) {
        if (this.address[i].url == this.vurl) {
          localStorage.setItem("simgateway", JSON.stringify(this.address[i]));
          this.msg("选择成功");
          return;
        }
      }
      this.msg("没有选中节点");
    },
    msg(msg) {
      Toast({
        message: msg,
        position: "bottom",
        duration: 3000
      });
    }
  },
  components: {
    adminpass
  }
};
</script>

<style lang='less'>
.settingPay {
  width: 100%;
  margin: 0 auto;
  text-align: left;
  .clearfix:before,
  .clearfix:after {
    content: "";
    display: block;
    clear: both;
  }
  .clearfix {
    zoom: 1;
  }
  .titleS {
    width: 96%;
    margin: 0 auto;
    font-size: 0.32rem;
    display: block;
    height: 1.0667rem;
    color: #666666;
    line-height: 1.0667rem;
  }
  .defaultNumber {
    box-shadow: 0 0 0.2133rem 0rem #dadada;
    border-radius: 0.1333rem;
    width: 96%;
    margin: 0 auto;
    margin-top: 0.2667rem;
    background: #fff;
    padding-top: 0.0267rem;
    padding-bottom: 0.267rem;
    h3 {
      font-size: 0.32rem;
      color: #666666;
      margin-top: 0.3733rem;
      margin-bottom: 0.1333rem;
      margin-left: 3.65%;
    }
    .switch {
      padding: 10px 0 0 0;
    }
    .switch label {
      float: left;
      margin-left: 20px;
      margin-right: 10px;
      line-height: 37px;
      height: 37px;
    }
    .inputNumber {
      width: 92.7%;
      margin: 0 auto;
      padding-bottom: 0.5333rem;
      .workNumber {
        width: 90%;
        padding-left: 0.2667rem;
        border-radius: 0.1333rem;
        height: 0.9867rem;
        text-transform: uppercase;
        border: 0.0267rem solid #cccccc;
        margin: 0 auto;
      }
    }
  }

  .settingContent {
    box-shadow: 0 0 0.2133rem 0rem #dadada;
    border-radius: 0.1333rem;
    width: 96%;
    margin: 0 auto;
    height: auto;
    background: #fff;
    table {
      margin: 0 auto;
      font-size: 0.3733rem;
      width: 96%;
      tr {
        th {
          height: 1.36rem;
          border-bottom: 0.0267rem solid #eeeeee;
        }
      }
      tr {
        td {
          height: 1.0667rem;
          border-bottom: 0.0267rem dashed #eeeeee;
          .checkBox {
            width: 0.4267rem;
            height: 0.4267rem;
            margin: 0 auto;
            border-radius: 0.1333rem;
            .icon-right {
              color: #6ed200;
              line-height: 0.4267rem;
            }
          }
        }
      }
    }
    .settingAll {
      width: 100%;
      height: auto;
      .allBox {
        width: 85%;
        margin: 0 auto;
        margin-top: 0.5333rem;
        padding-bottom: 0.32rem;
        .testButton {
          width: 44%;
          height: 0.9867rem;
          text-align: center;
          line-height: 0.9067rem;
          color: #fff;
          font-size: 0.3733rem;
          background: #ff8902;
          border-radius: 0.1867rem;
          display: inline-block;
          margin-bottom: 0.2667rem;
          margin-left: 0.36rem;
        }
      }
    }
  }
}
</style>



