<template>
    <div class="pik_calendar">
        <div class="arrows" @click='DateJump(false)' :class='{grey:isMin()}'>
            <i class="iconfont icon-jiantou"></i>
        </div>
        <div class="calendar" @click="openPick">
            {{value | dateF}}
        </div>
        <div class="arrows" @click='DateJump(true)'>
            <i class="iconfont icon-caret-right-copy" :class='{grey:isMax()}'></i>
        </div>
        <mt-datetime-picker ref="picker" @confirm="handleConfirm" type="date" :startDate="minDate" :endDate="maxDate" v-model="value">
        </mt-datetime-picker>
    </div>
</template>
<script>
import moment from 'moment'
import { Toast } from 'mint-ui';
export default {
    name:'datepik',
    data() {
        return {
            value: this.defaultValue != '' ? this.defaultValue : moment().format('YYYY-MM-DD'),
            minDate: moment(moment().format('YYYY-MM-DD')).add(this.minMonth,"M").toDate(),
            maxDate: moment(moment().format('YYYY-MM-DD')).add(this.maxMonth,"M").toDate(),
        }
    },
    props: {
        step: {
            type: String,
            default: "1"
        },
        defaultValue: {
            type: String,
            default: ''
        },
        minMonth:{
            default: '-6'
        },
        maxMonth:{
            default: '6'
        },
        limit: {
            type: String,
            default: 'next'
        }
    },
    mounted() {
        if (this.limit == 'next') {
            this.minDate = moment(this.value).toDate();
        }
        if (this.limit == 'pre') {
            this.maxDate = moment(this.value).toDate();
        }
    },
    methods: {
        isMin() {
            return this.minDate != null && moment(this.value) <= moment(this.minDate);
        },
         handleConfirm(d) {
            this.$emit('change', this.value)
        },
        setDate(date){
            this.value =  moment(date).format('YYYY-MM-DD');
        },
        isMax() {
            return this.maxDate != null && moment(this.value) >= moment(this.maxDate);
        },
        openPick() {
            this.$refs.picker.open();
        },
        msg(msg){
                Toast({
                        message:msg,
                        position: 'center',
                        duration: 1000
                    });
        },
        DateJump(next) {
            if (next) {
                if (this.isMax()) {
                   this.msg('不能向后了')
                    return;
                }
            }
            else {
                if (this.isMin()) {
                    this.msg('不能向前了')
                    return;
                }
            }
            var d = moment(this.value).add(next ? this.step : -this.step, 'd');
            if(next){
                if(this.maxDate != null  && d > moment(this.maxDate)){
                    d = moment(this.maxDate);
                }
            }
            else{
                if(this.minDate != null && d < moment(this.minDate)){
                    d = moment(this.minDate);
                }
            }
            this.value = d.format('YYYY-MM-DD');
            this.$emit('change', this.value)
        },
    },
    filters: {
        dateF(v) {
            return v != null ? moment(v).format('YYYY-MM-DD') : '';
        }
    }
}
</script>
<style lang='less'>
.pik_calendar {
    width: 5rem;
    height: 1.2rem;
    margin: 0 auto;
    .arrows {
        float: left;
        width: 0.64rem;
        height: 0.64rem;
        margin:.28rem 0.15rem 0;
        vertical-align: middle;
        text-align: center;
        background: #F6F8F9;
        border-radius: 50%;
        .iconfont {
            font-size: .3rem;
            transform: scale(0.8);
            display: inline-block;
            line-height: .65rem;
            vertical-align: top;
        }
    }
    .calendar {
        width: 3.1rem;
        line-height: 1.2rem;
        float: left;
        text-align: center;
        font-size: .5rem;
    }
    .grey {
        color: #D8D8D8;
    }
}
</style>



