<template>
  <mt-popup v-model="popupVisible" class="oyDetail" position="right" modal='false'>
    <top-nav title='订单详情' v-on:notseeChild='popupVisible=false'></top-nav>
    <div class="rtDetail_box">
      <div class="DeTop">
        <div class="topL">预订单号：{{orderInfoObj.accnt}}</div>
        <div class="topR">
          <span>状态：</span>
          <div class="circleBox"> {{orderInfoObj.sta}}</div>
        </div>
      </div>
      <div class="restaurantList">
        <div class="listBox">
          <div class="blueLind"></div>
          <div class="vipCheckBox">
            <div class="infoBo">
              <span>基本信息</span>
            </div>
          </div>
          <div class="infoDetail clearfix">
            <ul class="infoL">
              <li>主桌：&nbsp;{{orderInfoObj.tableno}}</li>
              <li>人数：&nbsp;{{orderInfoObj.gsts}}</li>
              <li>班次：&nbsp;{{orderInfoObj.shift | fmShift}}</li>

              <li>优惠模式：&nbsp;{{orderInfoObj.dscMode}}</li>

              <li>折扣金额：&nbsp;{{orderInfoObj.dscAmount}}</li>
              <li>税费：&nbsp;{{orderInfoObj.taxAmount}}</li>

              <li>服务费：&nbsp;{{orderInfoObj.srvAmount}}</li>
              <li>消费金额：&nbsp;{{orderInfoObj.charge}}</li>
              <li>付款金额：&nbsp;{{orderInfoObj.credit}}</li>



            </ul>
          </div>
        </div>
        <div class="listBox clearBottom">
          <div class="blueLind"></div>
          <div class="vipCheckBox">
            <div class="infoBo">
              <span>更多信息</span>
            </div>
          </div>
          <div class="infoDetail clearfix">
            <div class="infoL">
              <p>会员卡：&nbsp;{{orderInfoObj.cardNo}}</p>
              <p>折扣模式：&nbsp;{{orderInfoObj.dscMode}} </p>
              <p>营业日期：&nbsp;{{orderInfoObj.bizDate}} </p>

              <p>来源码描述：&nbsp;{{orderInfoObj.sourceDescript}} </p>
            </div>
            <div class="infoR">
              <p>会员卡信息：&nbsp;{{orderInfoObj.cardInfo}}</p>
              <p>折扣模式描述：&nbsp;{{orderInfoObj.dscModeDescript}} </p>
              <p>销售员：&nbsp;{{orderInfoObj.saleName}} </p>
              <p>市场码描述：&nbsp;{{orderInfoObj.marketDescript}} </p>
            </div>
          </div>
        </div>
      </div>
    </div>
  </mt-popup>
</template>

<script>
  export default {
    props: {

    },
    data() {
      return {
        orderInfoObj: {},
        popupVisible: false
      };
    },
    mounted() {

    },
    methods: {
      getMasterList(accnt) {
        this.$http.post("/pos/posUnion/getMasterList", {
          accnt: accnt
        }).then(res => {
          if (res.data.errorCode == 0) {
            this.orderInfoObj = res.data.result;
            this.popupVisible = true;
          } else {
            this.msg(res.data.errorMsg);
          }
        });
      },
    },
    filters: {
      fmShift(value) {
        if (value == 1) return "早班";
        if (value == 2) return "中班";
        if (value == 3) return "晚班";
        if (value == 4) return "夜班";
      }
    },
    components: {

    }
  };
</script>

<style lang='less' scoped>
  .oyDetail {
    .rtDetail_box {
      overflow: auto;
      height: 100%;
      .DeTop {
        width: 90%;
        height: 1.2533rem;
        margin: 0 auto;
        font-size: 0.32rem;
        color: #333333;
        .topL {
          float: left;
          line-height: 1.2267rem;
        }
        .topR {
          float: right;
          line-height: 1.2267rem;
          span {
            display: inline-block;
          }
          .circleBox {
            width: 0.5333rem;
            height: 0.5333rem;
            background: #646F83;
            border-radius: 1.36rem;
            color: #FFF;
            display: inline-block;
            vertical-align: top;
            margin-top: 0.3733rem;
            line-height: 0.5867rem;
          }
        }
      }
      .restaurantList {
        background: #FFFFFF;
        margin-bottom: 0.2667rem;
        width: 96%;
        height: auto;
        margin: 0 auto 0.2667rem;
        box-shadow: 0 0 0.2133rem 0rem #dadada;
        border-radius: 0.1333rem;
        .listBox {
          width: 100%;
          height: auto;
          padding-bottom: 0.2667rem;
          border-bottom: 0.0267rem dashed #EEEEEE;
          .blueLind {
            height: 0.3733rem;
            width: 1.1%;
            background: #646f83;
            display: inline-block;
            float: left;
            margin-top: 0.4rem;
          }
          .infoDetail {
            width: 86%;
            margin: 0 auto;
            text-align: left;
            margin-top: 0.2667rem;
            font-size: 0.3733rem;
            color: #333333;
            .infoL {
              li {
                width: 50%;
                height: auto;
                text-align: left;
                float: left;
                white-space: nowrap;
                overflow: hidden;
                text-overflow: ellipsis;
              }
            }
            .infoR {
              width: 50%;
              height: auto;
              text-align: left;
              display: inline-block;
              white-space: nowrap;
              overflow: hidden;
              text-overflow: ellipsis;
              float: right;
            }
          }
          .vipCheckBox {
            width: 94.5%;
            margin: 0 auto;
            padding-top: 0.2933rem;
            .infoBo {
              text-align: left;
              margin-left: 0.08rem;
              span {
                font-weight: bold;
                margin-left: 0.1333rem;
                font-size: 0.3733rem;
                color: #151515;
              }
            }
          }
        }
        .clearBottom {
          border-bottom: none;
        }
      }
    }
  }
</style>



