<template>
  <div class="toAR">
    <topNav :title="title" :url="'/orderPayDetail?tableno='+orderDetailObj.tableno"></topNav>
    <div class="listBox">
      <div class="infoDetails">
        <p style="width:47%;float:left;">
          主餐台:
          <span>{{orderDetailObj.tableno}}</span>
        </p>
        <p style="width:50%;float:left;">
          账号：
          <span>{{orderDetailObj.accnt}}</span>
        </p>
        <div class="tableList">
          <div>相关餐台：</div>
          <span
            v-for="item in orderDetailObj.unionslist"
            :key="item.unionTableNo"
          >{{item.unionTableNo}}</span>
        </div>
        <div class="infoL">
          <p>
            当前消费：
            <span>
              <small>￥</small>
              {{getFP().toFixed(2)}}
            </span>
          </p>
          <p>
            含税：
            <span>
              <small>￥</small>
              {{orderDetailObj.taxAmount}}
            </span>
          </p>
          <p>
            付款：
            <span>
              <small>￥</small>
              {{orderDetailObj.credit}}
            </span>
          </p>
        </div>
        <div class="infoR">
          <p>
            服务费：
            <span>
              <small>￥</small>
              {{orderDetailObj.srvAmount}}
            </span>
          </p>
          <p>
            折扣：
            <span>
              <small>￥</small>
              {{orderDetailObj.dscAmount}}
            </span>
          </p>
          <p>
            应收：
            <b>
              <small>￥</small>
              {{getPayAmout().toFixed(2)}}
            </b>
          </p>
        </div>
      </div>
    </div>
    <div class="iconBox">
      <div class="iconfont icon-zhuanhuan"></div>
    </div>
    <div class="vipTop">
      <input class="orderNumer" type="text" name id placeholder="AR名称" v-model="keyInput">
      <div class="searchResult" @click="searchRoom">搜索</div>
    </div>
    <div
      class="vipList"
      v-for="(item, index) in roomObj"
      :key="index"
      @click="selectRoom(item,index)"
    >
      <div class="blueLind"></div>
      <div class="vipCheckBox">
        <div class="infoBo">
          <div class="titleS">账号：</div>
          <span>{{item.accnt}}</span>
        </div>
        <div class="greenCheck">
          <div class="checkBox" @click="checkTick()">
            <div class="iconfont" v-bind:class="{ 'icon-right': item.isSelect }"></div>
          </div>
        </div>
      </div>
      <div class="infoDetail">
        <div class="infoL">
          <p>
            姓名:
            <span>&nbsp;{{item.name}}</span>
          </p>
          <p style="width:100%">
            余额:
            <span>￥{{item.amount}}</span>
            <span style="padding-left:30px">
              信用:
              <span>￥{{item.credit}}</span>
            </span>
          </p>
        </div>
        <!--  <div class="infoR">
          <p>描述：&nbsp;
            <span>HotelGC</span>
          </p>
          <p>信用：&nbsp;
            <span>￥12312</span>
          </p>
        </div>-->
      </div>
      <div class="infoDetail">
        <div class="infoDivide">
          <span>{{item.isPermanent == "F"?"非永久账户" : "永久账户"}} / {{item.limitType =="2"?"有限额":"无限额"}}</span>
          <p style="text-align: center;">
            状态：&nbsp;
            <span>{{item.sta}}</span>
          </p>
          <p>
            记账：&nbsp;
            <span>&nbsp;{{item.postingFlag}}</span>
          </p>
          <div class="clearfix"></div>
        </div>
      </div>
      <div class="infoDetail clearBottom">
        <div class="infoDivide">
          <p>联系人／备注：</p>
          {{item.linkMan}} / {{item.remark}}
          <div class="iconfont icon-xia"></div>
        </div>
      </div>
    </div>
    <div v-if="isEmpty">
      <img class="nothing" src="../../assets/img/nothing.png" v-show="!isRoomFrist">
      <p class="nothingTxt" v-show="!isRoomFrist">什么都没有</p>
    </div>
    <div class="clearChoice"></div>
    <div class="footerSure">
      <span @click="showAlter()">确定</span>
    </div>
  </div>
</template>

<script>
import { Toast, MessageBox } from "mint-ui";
import md5 from "js-md5";
export default {
  data() {
    return {
      show: false,
      keyInput: "",
      roomObj: "",
      orderDetailObj: JSON.parse(localStorage.orderDetail),
      selectRoomObj: {},
      isEmpty: true,
      isRoomFrist: true,
      password: "",
      title:"",
      lastPrintObj: null,
      printMaxCount: 0,
      printNowCount: 0,
      ps: !!localStorage.ps
        ? JSON.parse(localStorage.ps)
        : {
            accountLogUser: "",
            invoice: false,
            printPayCount: 1,
            printRoomCount: 1
          },
        payCodeSelect:!!localStorage.payCodeSelect
        ? JSON.parse(localStorage.payCodeSelect)
        : {
            catPosting:"TA",
            descript:"挂AR"
          },
    };
  },
  mounted() {
      this.title = this.payCodeSelect.descript;
  },
  methods: {
    checkTick() {
      this.show = !this.show;
      console.log(this.show);
    },
    showAlter() {
      if (this.selectRoomObj.name == undefined) {
        this.msg("请选择转账AR账户");
        return false;
      }
       MessageBox({
                title: "提示",
                message: "确定挂账？",
                showCancelButton: true,
                closeOnClickModal: false,
                confirmButtonText: "确定"
            }).then(action => {
                if (action == "confirm") {
                   this.checkOutOrder();
                }
            });
    },
    msg(msg) {
      Toast({
        message: msg,
        position: "bottom",
        duration: 3000
      });
    },
    msg2(msg) {
      if (msg == "用户取消") {
        this.msg(msg);
      } else {
        MessageBox.alert(msg);
      }
    },
    getFoFkList(keyInput) {
      this.$http
        .post("/pos/posUnion/getArFkList", { keyInput: keyInput,payCode:this.payCodeSelect.code })
        .then(res => {
          if (res.data.errorCode == 0) {
            this.roomObj = res.data.result;
            if (this.roomObj.length > 0) {
              for (var item in this.roomObj) {
                this.roomObj[item].isSelect = false;
              }
              this.isEmpty = false;
              this.isRoomFrist = false;
            } else {
              this.isEmpty = true;
              this.isRoomFrist = false;
            }
            //console.log(this.roomObj.length);
            //console.log(this.isEmpty);
          } else {
            this.msg2(res.data.errorMsg);
            this.isEmpty = true;
          }
        });
    },
    searchRoom() {
      if (this.keyInput) {
        this.getFoFkList(this.keyInput);
      } else {
        this.msg(" 请输AR名称");
      }
    },
    selectRoom(item, index) {
      this.selectRoomObj = item;
      var itemObj = item;
      //只能选中其中一个
      if (!itemObj.isSelect) {
        for (var i = 0; i < this.roomObj.length; i++) {
          this.roomObj[i].isSelect = false;
        }
      }
      itemObj.isSelect = !itemObj.isSelect;
      this.$set(this.roomObj[index], index, itemObj);
      // 触发数组检测
      this.roomObj.push(11);
      this.roomObj.pop(11);
    },
    getPayAmout() {
      return (
        Number(this.orderDetailObj.charge) - Number(this.orderDetailObj.credit)
      );
    },
    getFP() {
      return (
        Number(this.orderDetailObj.charge) +
        Number(this.orderDetailObj.dscAmount) -
        Number(this.orderDetailObj.srvAmount)
      );
    },
    checkOutOrder() {
      var postObj = {};
      postObj.accnt = this.orderDetailObj.accnt;
      //postObj.pcrec=this.orderDetailObj.pcrec;
      if (this.orderDetailObj.pcrec) {
        postObj.pcrec = this.orderDetailObj.pcrec;
      }
      postObj.charge = this.getPayAmout();
      postObj.payCode = this.payCodeSelect.catPosting;
     
      postObj.credit = this.orderDetailObj.credit;
      postObj.userCode = this.ps.accountLogUser;
      postObj.flag = 0;
      postObj.orderno = "";
      postObj.serialno = "";
      postObj.remarkjson = "";
      postObj.accntNo = this.selectRoomObj.accnt;
      postObj.info = this.selectRoomObj.name;
      if(this.selectRoomObj.hotelId){
        postObj.pmsHotelId  = this.selectRoomObj.hotelId;
        postObj.accntNo  = this.selectRoomObj.gustNum;
        postObj.info = this.selectRoomObj.name;
      }
      if(this.payCodeSelect.code){
        postObj.payCodeMode = 1;
        postObj.payCode = this.payCodeSelect.code;
      }
      this.$http.post("/pos/posUnion/saveOrderCheckOut", postObj).then(res => {
        if (res.data.errorCode == 0) {
          // 校验用户信息成功，开始执行入账程序
          //this.msg();
          this.signCode("入账成功，请客人签名！");
        } else {
          this.msg2(res.data.errorMsg);
        }
      });
    },
    signCode(msg) {
      handWrite(this.signSccess, null, msg);
    },
    getmlist(obj, i, tscount) {
      if (i >= tscount) {
        this.printFact(obj);
        return;
      }
      var element = obj.unionslist[i];
      this.$http
        .post("/pos/posUnion/getMasterList", {
          accnt: element.unionAccnt,
          tableno: element.unionTableNo
        })
        .then(res => {
          if (res.data.errorCode == 0) {
            element.datas = res.data.result.detailDtos;
            this.getmlist(obj, i + 1, tscount);
          } else {
            this.msg2(res.data.errorMsg);
          }
        });
    },
    printOrder(tobj, count) {
      this.printMaxCount = count;
      this.printNowCount = 1;
      var tscount = tobj.unionslist.length;
      if (tscount > 0) {
        MessageBox({
          title: "提示",
          message: "关联桌号是否一起打印?",
          showCancelButton: true,
          closeOnClickModal: false,
          confirmButtonText: "确定"
        }).then(action => {
          if (action == "confirm") {
            this.getmlist(tobj, 0, tscount);
          } else {
            this.printFact(tobj);
          }
        });
      } else {
        this.printFact(tobj);
      }
    },
    printFact(tobj) {
      this.lastPrintObj = tobj; //JSON.stringify(tobj);
      printDataSign(this.printSuccess, this.printError, this.lastPrintObj);
    },
    signSccess(value) {
      if (value == "1") {
        var tobj = this.orderDetailObj;
        var gatewayJson = localStorage.getItem("gateway");
        if (gatewayJson != null) {
          var gateway = JSON.parse(gatewayJson);
          tobj.hotelName = gateway.hotelName;
        }
        tobj.invoiceGateway = null;
        tobj.payway = null;
        tobj.addInfo = "AR账号：" + this.selectRoomObj.name;
        this.printOrder(tobj, this.ps.printRoomCount);
      } else {
        MessageBox({
          title: "提示",
          message: "请确定是否取消打印?",
          showCancelButton: true,
          closeOnClickModal: false,
          confirmButtonText: "确定"
        }).then(action => {
          if (action == "confirm") {
            this.$router.push({
              path: "/orderPayDetail?isSucceed=true"
            });
          } else {
            this.signCode("");
          }
        });
      }
    },
    printError(v) {
      MessageBox.confirm("打印出错:" + v + "，继续打印?")
        .then(action => {
          printDataSign(this.printSuccess, this.printError, this.lastPrintObj);
        })
        .catch(action => {
          this.$router.push({
            path: "/orderPayDetail?isSucceed=true"
          });
        });
    },
    printSuccess() {
      if (this.printNowCount < this.printMaxCount) {
        MessageBox({
          title: "提示",
          message:
            "正在打印第" + (this.printNowCount + 1) + "份，请撕掉后点继续?",
          showCancelButton: true,
          closeOnClickModal: false,
          confirmButtonText: "继续"
        }).then(action => {
          if (action == "confirm") {
            this.printNowCount++;
            printDataSign(
              this.printSuccess,
              this.printError,
              this.lastPrintObj
            );
          }
        });
      } else {
        this.$router.push({
          path: "/orderPayDetail?isSucceed=true"
        });
      }
    }
  }
};
</script>

