<template>
    <div style=" height:100%;">
        <div class="loginCommon" style='background:url(static/images/loginbg.png);height:100%;background-size:cover;background-size: 100% 100%;'>
            <div class="loginHeader">
                <span><img src="../../assets/img/posLogo.png"></span>
                <!--<span class='point'>·</span>
                <span class='pms'>MiHotel</span>-->
            </div>
            <div class="loginForm" @click='goAnchor' id="input-text-box">
                <mt-field placeholder="酒店代码" v-model="formOption.hotelCode"></mt-field>
                <mt-field placeholder="用户名" v-model="formOption.username"></mt-field>
                <mt-field placeholder="请输入密码" type="password" v-if="formOption.password.length < 100" v-model="formOption.password"></mt-field>
                <mt-field placeholder="请输入密码" type="text" v-else @input.native="formOption.password=''" v-model="vsave"></mt-field>
            </div>
            <div class="centerIn">
                <mt-button type="primary" size='large' @click='login'>登录</mt-button>
            </div>
            <div class="remember">
                <mt-checklist v-model="value" :options="['记住账号密码']">
                </mt-checklist>
            </div>
        </div>
    </div>
</template>

<script>
import { Toast } from 'mint-ui';
import {rsalogin} from'../../assets/js/RSA_Stripped.js'
export default {
    data() {
        return {
            formOption: !!localStorage.LoginOption ? JSON.parse(localStorage.LoginOption) : {
                hotelCode: '',
                username: '',
                password: '',
            },
            value: ['记住账号密码'],
            vsave:"保存的密码",
            rsaKey:null
        }
    },
    methods: {
        login() {
            var errorMsg = { hotelCode: "请填写酒店代码", username: "请填写用户名", password: "请填写密码" }
            var errors = ''
            var postForm = _.transform(this.formOption, (result, n, key) => {
                if (!n && _.isEmpty(errors)) {
                    errors = errorMsg[key];
                    return;
                } else {
                    if (key != 'password') {
                        result[key] = n.toUpperCase()
                    } else {
                        result[key] = rsalogin(n)
                    }
                }
            })
            if (!_.isEmpty(postForm) && Object.keys(postForm).length == 3) {
                console.log(_.isEmpty(this.value));
                if (_.isEmpty(this.value)) {
                    localStorage.removeItem("LoginOption");
                } else {
                    localStorage.setItem("LoginOption", JSON.stringify(postForm));
                }
                this.$http.post('/mobilepmsapi/user/login', postForm).then(res => {

                    if (res.data.result == 0) {
                        //全局token
                        localStorage.setItem("JWT_TOKEN", res.data.retVal);
                       // var redirect = this.$route.query.redirect == '/login' ? '/' : this.$route.query.redirect;
                        this.$router.push({ path: '/anypay' })

                    } else {
                        Toast({
                            message: res.data.msg,
                            position: 'bottom',
                            duration: 3000
                        });
                    }
                })
            } else {
                Toast({
                    message: errors,
                    position: 'bottom',
                    duration: 3000
                });
            }

        },

        goAnchor(event) {

            var inputTextBox = document.getElementById('input-text-box');

            setInterval(function() {
                inputTextBox.scrollIntoView(true);
            }, 200)
        }
    },
    mounted() {
        if(this.$route.query.jwtToken != null){
             localStorage.setItem("JWT_TOKEN", this.$route.query.jwtToken);
             this.$router.push({ path: '/anypay' });
             return;
        }
    }
}
</script>
<style lang='less'>

.loginCommon {
    overflow: hidden;
    user-select: none;
    ::-webkit-input-placeholder {
        /* WebKit browsers */
        font-size: 0.4rem;
        color: #fff;
        text-decoration: none;
    }
    ;
    .mint-cell-wrapper {
        background: none;
        padding: 0;
        overflow: visible;
    }

    .loginHeader {
        margin: 2.2rem 0 1.5rem 1.2rem;
        color: #fff;
        width: 7.5rem;
        text-align: center;
        img{width: 5.6rem;}
        span {
            vertical-align: top;
            position: relative;
            top: 0.1rem
        }
        .point {
            font-size: 1rem;
            top: 0.5rem;
        }
        .pms {
            font-size: 0.6rem;
            top: 0.8rem;
            left: 0.3rem;
        }
        .iconfont {
            font-size: 1.6rem;
            color: #fff;
        }
    }
    .mintui-field-error {
        font-size: .5rem;
        margin-right: .2rem;
    }
    .mint-cell-value{display: block;position: relative;}
    .mint-field-core {
        text-align: center;
        height: 1.2rem;
        opacity: 0.8;
        font-size: 0.4rem;
        background: none;
        width:80%;
        margin: 0 .73rem;
        color: #fff;
        border: none;
        text-transform: uppercase;
        text-decoration: none;
        outline: none;
    }
    .mint-field-clear{position: absolute;right:0;}
    .mint-cell {

        color: #fff;
        width: 7.3rem;
        height: 1.2rem;

        margin: 0 auto;
        margin-top: 0.45rem;
        font-size: 0.5rem;
        background: none;
    }
    .mint-cell:last-child {
        background: none;
    }
    .mint-cell-wrapper {
        border: 1px solid #f4f4f4;
        border-radius: 0.6rem;
        @media only screen and (-webkit-min-device-pixel-ratio: 2),
        only screen and ( min--moz-device-pixel-ratio: 2),
        only screen and ( -o-min-device-pixel-ratio: 2/1) {
            border:2px solid #f4f4f4;
        }
    }
    .centerIn {
        width: 7.3rem;
        margin: 0 auto;
        margin-top: 0.45rem;
        button {
            height: 1.2rem;
            font-size: 0.46rem;
            border-radius: .6rem;
            background: #f6a623;
            background-image: linear-gradient(90deg, #f6a129 0%, #ff6e61 98%);
            border-radius: 100px;
        }
    }
    .remember {
        .mint-cell-wrapper {
            border: none;
            border-radius: 0;
            background: none;
        }
        .mint-cell {
            background: none;
            border: none;
        }

        .mint-checkbox-core {
            border-radius: 0.06rem;
            width: 0.3rem;
            height: 0.3rem;
            margin-right: 0.1rem;
        }
        .mint-checkbox-core::after {
            width: 0.1rem;
            height: 0.1rem;
            top: 0.06rem;
            left: 0.08rem;
            transform: rotate(45deg) scale(1, 2.2)!important;
            border-width: 1px!important;
            @media only screen and (-webkit-min-device-pixel-ratio: 2),
            only screen and ( min--moz-device-pixel-ratio: 2),
            only screen and ( -o-min-device-pixel-ratio: 2/1) {
                border-width:2px!important;
            }
        }
        .mint-checkbox-label {
            font-size: 0.4rem;
        }
        .mint-checkbox-input:checked+.mint-checkbox-core {
            border-color: #F89635;
            background: #fff;
        }
        .mint-checkbox-input:checked+.mint-checkbox-core::after {
            border-color: #F89635;
        }
    }
}

.mint-toast {
    border-radius: 0.2rem;
    .iconfont {
        font-size: 2.5rem
    }
}
</style>



