<template>
  <div class="transactSearch">
    <topNav title="交易查询" url='/'></topNav>
    <div class="dateAll">
      <div class="dataBox">
        <div class="data">
          <datepik @change='dateChange' style="margin:0 auto;float:none;" limit="pre" step="1"></datepik>
        </div>
        <div class="line"></div>
        <div class="allBox">
          共{{info.masterTotal}}笔，¥{{info.masterAmont |tofixed }}
        </div>
      </div>
      <div class="searchBox">
        <div class="search">
          <input class="tableNuber" type="text" name="" id="" placeholder="桌号" v-model='searchOption.tableNo'>
          <input class="orderNumer" type="text" name="" id="" :placeholder="SYS_DEVICE=='ICBC'?'参考号 / 订单号':'凭证号 / 订单号'" v-model='searchOption.masterNo'>
          <div class="searchResult" @click='search'>查询</div>
        </div>
      </div>
    </div>
    <div class="scrollBox" v-infinite-scroll="loadMore" infinite-scroll-disabled="loading" infinite-scroll-distance="10">
      <div class="orderList" v-for='item in listInfo' :key='item.index'>
        <div class="blueLind"></div>
        <div class="infoL"  @click='openDetail(item)'>
          <div class="lineInfo">
            <div class="titleThird nomalL">订单号</div>&nbsp;:
            <span>{{item.accnt}}</span>
          </div>
          <div class="lineInfo">
            <div class="titleThird nomalL">营业点</div>&nbsp;:
            <span>{{item.pcCodeDescript}}</span>
          </div>
          <div class="lineInfo">
            <div class="titleThird nomalL">总金额</div>&nbsp;:
            <span><small>￥</small>{{item.charge}}</span>
          </div>
          <div class="lineInfo">
            <div class="titleThird nomalL">支付时间</div>&nbsp;:
            <span>{{item.logdate | fd}}</span>
          </div>
        </div>
        <div class="infoL infoR"  @click='openDetail(item)'>
          <div class="lineInfo">
            <div class="titleThird">桌</div>
            <div class="titileS">号&nbsp;:</div>
            <span>{{item.tableno}}</span>
          </div>
          <div class="lineInfo">
            <div class="titleThird nomalL">{{SYS_DEVICE=="ICBC"?"参考号":"凭证号"}}</div>&nbsp;:
            <span>{{item.orderno | fitOrderNo}}</span>
          </div>
          <div class="lineInfo">
            <div class="titleThird nomalL">{{item.descript}}</div>&nbsp;:
            <span><small>￥</small>{{item.credit}}</span>
          </div>
          <div class="lineInfo">
            <div class="titleThird nomalL">状态</div>&nbsp;:
            <span>{{item.sta}}</span>
          </div>
        </div>
        <i class="mintui mintui-back"></i>
         <div class="returnPay" v-if="item.sta == 'O'">
          <div class="returnButton" @click='confirmP(item)'>重打印</div>
        </div>
      </div>
      <div v-bind:class="{ 'hide': !isEmpty }">
        <img class="nothing" src="../../assets/img/nothing.png">
        <p class="nothingTxt">什么都没有</p>
      </div>
      <div class="infoWarn" v-if='this.cutOff==1' style="height:50px">我是有底线的</div>
    </div>

    <mt-popup v-model="popupVisible" modal='false' style="width: 100%;height: 100%;overflow:auto">
      <div class="topHeader">
        <header class="mint-header is-fixed" style="background-color:#00487C;">
          <div class="mint-header-button is-left">
            <a class="router-link-exact-active router-link-active" @click='openDetail'>
              <i class="mintui mintui-back"></i>
            </a>
          </div>
          <h1 class="mint-header-title">餐单详情</h1>
          <div class="mint-header-button ">
          </div>
        </header>
      </div>
      <masterList ref="masterListC"></masterList>
    </mt-popup>
  </div>
</template>
<script>
import datepik from "../common/datepik";
import masterList from "../common/masterList";
import moment from "moment";
import _ from 'lodash';
import {getKeyUrlYun} from'../../assets/js/md5Data.js'
import { Toast, MessageBox, InfiniteScroll } from "mint-ui";
export default {
  data() {
    return {
      accnt: "",
      select_time: "",
      cutOff: "",
      isEmpty: true,
      info: {
        masterAmont: 0,
        masterTotal: 0
      },
      orderInfoObj: "",
      SYS_DEVICE: "",
      popupVisible: false,
      detailTitle: "餐单详情",
      listInfo: [],
      searchOption: {
        bizDate: moment().format("YYYY-MM-DD"),
        masterNo: "",
        paycode: "",//PUN,PUN_CARD,PUN_ALIPAY,PUN_WEIXIN,PUN_UPAY
        currentPage: 1,
        pageSize: 10,
        sta:"O",
        page: 1
      },
      lastPrintObj: null
    };
  },
  computed: {},
  mounted() {
    this.SYS_DEVICE = this.$SYS_DEVICE;
    this.getInfo();
  },
  methods: {
    notsees() {
      this.popupVisible = false;
    },
    search() {
      //查询的时候充值分页
      this.searchOption.page = 1;
      this.getInfo();
    },
    dateChange(value) {
      this.searchOption.bizDate = moment(value).format("YYYY-MM-DD");
      this.listInfo = [];
      this.getInfo();
    },
    openDetail(item) {
      this.popupVisible = !this.popupVisible;
      this.$refs.masterListC.getMasterList(item.accnt, item.tableno);
    },
    getInfo(isloadMore) {
      this.cutOff = 0;
      this.$http
        .post("/pos/posUnion/getMasterListWithSta", this.searchOption)
        .then(res => {
          if (res.data.errorCode == 0) {
            this.info = res.data.result;
            this.isEmpty = false;
            this.masterTotal = this.info.masterTotal;

            //
            if (isloadMore) {
              // 加载更多
              _.forEach(this.info.masterlist, (el, key) => {
                this.listInfo.push(el);
              });
            } else {
              // 第一次加载数据
              this.listInfo = this.info.masterlist;
            }

            if (Number(this.searchOption.page) > 1) {
              if (this.info.masterlist.length < this.searchOption.pageSize) {
                this.cutOff = 1;
              }
            }

            this.loading = false;
            if (this.info.masterTotal == 0) {
              //this.msg('查询结果为0笔')
              this.isEmpty = true;
              this.cutOff = 0;
            }
          } else {
            this.msg(res.data.errorMsg);
          }
        });
    },
    getMasterList(accnt, tableno, payway, traceNo) {
      var traceNo =
        traceNo != null && traceNo.indexOf("_") > 0
          ? traceNo.split("_")[0]
          : traceNo;
      this.$http
        .post("/pos/posUnion/getMasterList", {
          accnt: accnt,
          tableno: tableno
        })
        .then(res => {
          if (res.data.errorCode == 0) {
            this.printOrder(res.data.result,accnt, payway, traceNo);
          } else {
            this.msg(res.data.errorMsg);
          }
        });
    },
    confirmP(item) {
      event.preventDefault();
      MessageBox({
        title: "提示",
        message: "确定重打印?",
        showCancelButton: true,
        closeOnClickModal: false,
        confirmButtonText: "确定"
      }).then(action => {
        if (action == "confirm") {
           this.getMasterList(
            item.accnt,
            item.tableno,
            item.descript,
            item.orderno
          );
        }
      });
    },

    getmlist(obj, i, tscount,accnt,payway, traceNo) {
      if (i >= tscount) {
        this.printFact(obj,accnt,payway,traceNo);
        return;
      }
      var element = obj.unionslist[i];
      this.$http
        .post("/pos/posUnion/getMasterList", {
          accnt: element.unionAccnt,
          tableno: element.unionTableNo
        })
        .then(res => {
          if (res.data.errorCode == 0) {
            element.datas = res.data.result.detailDtos;
            
            this.getmlist(obj, i + 1, tscount,accnt,payway, traceNo);
          } else {
            this.msg(res.data.errorMsg);
          }
      });
    },
    printOrder(tobj, accnt,payway, traceNo) {
        var tscount = tobj.unionslist.length;
        if (tscount > 0) {
          MessageBox.confirm("关联桌号是否一起打印?")
            .then(action => {
              this.getmlist(tobj,0,tscount,accnt,payway,traceNo); 
            })
            .catch(action => {
              this.printFact(tobj, accnt,payway, traceNo);
            });
        } else {
          this.printFact(tobj, accnt,payway, traceNo);
        }
    },
    // 打印
    printFact(tobj, accnt,payway, traceNo) {
      if (payway) {
        tobj.traceNo = traceNo;
        tobj.payway = payway;//getPayWayName(payway);
      } else {
        tobj.payway = null;
      }
      //console.log(tobj);
      var gatewayJson = localStorage.getItem("gateway");
      if (gatewayJson != null) {
        var gateway = JSON.parse(gatewayJson);
        tobj.hotelName = gateway.hotelName;

        var taxJson = localStorage.getItem("ps");
        if (taxJson != null) {
          var ps = JSON.parse(taxJson);
          if (ps.invoice && payway) {
            var keyarry = [];
            var urlv = gateway.url + "/pos/posUnion/getQRCode?" +
            getKeyUrlYun(gateway.hotelGroupCode ,gateway.hotelCode,accnt);
            tobj.invoiceGateway = urlv;
          }
        }
      }
      tobj.reprint = "1";
      this.lastPrintObj = tobj;
      console.log(JSON.stringify(tobj));
      printData(this.psucess, this.pError, this.lastPrintObj);
    },
    psucess(v) {},
    pError(v) {
          MessageBox({
          title: "提示",
          message: "打印出错:" + v + "，继续打印?",
          showCancelButton: true,
          closeOnClickModal: false,
          confirmButtonText: "确定"
        }).then(action => {
          if (action == "confirm") {
          printData(this.psucess, this.pError, this.lastPrintObj);
          } 
        });

    },
    msg(msg) {
      Toast({
        message: msg,
        position: "bottom",
        duration: 3000
      });
    },
    loadMore() {
      if (
        this.info.masterTotal >=
        parseFloat(this.searchOption.pageSize) *
          parseFloat(this.searchOption.page)
      ) {
        this.searchOption.page += 1;
        this.loading = true;
        let sef = this;
        var isloadMore = true;
        sef.getInfo(isloadMore);
      }
    }
  },
  filters: {
    tofixed(v) {
      return parseFloat(v).toFixed(2);
    },
    fitOrderNo(v) {
      return v != null && v.indexOf("_") > 0 ? v.split("_")[0] : v;
    },
    fd(value) {
      if (value == null || value == "") return "";
      return value.substr(11);
    }
  },
  components: {
    masterList,
    datepik
  }
};
</script>

<style lang="less">
.transactSearch {
  overflow-y: hidden;
  .dateAll {
    width: 100%;
    height: 2.6rem;
    background: #fff;
    position: fixed;

    z-index: 2000;
    .dataBox {
      margin: 0 auto;
      width: 96%;
      height: 100%;
      .data {
        width: 59%;
        height: 1.0667rem;
        display: inline-block;
        .iconBox {
          border-radius: 2.6667rem;
          width: 0.8533rem;
          height: 0.8533rem;
          display: inline-block;
          text-align: center;
          background: #fff;
          -moz-transform: none;
          -webkit-transform: none;
          -o-transform: none;
          -ms-transform: none;
          transform: none;
          .icon-jiantou,
          .icon-caret-right-copy {
            font-size: 0.32rem;
            line-height: 0.88rem;
            color: #d8d8d8;
            transform: scale(0.7);
          }
        }
        .blakBg {
          background: #f6f8f9 !important;
          border-radius: 2.6667rem;
        }
        .fontBlack {
          color: black !important;
        }
        .dataDtail {
          display: inline-block;
          font-size: 0.3733rem;
          color: #393c41;
          margin: 0.2667rem 0.3733rem 0rem;
        }
      }
      .line {
        width: 1px;
        text-align: center;
        display: inline-block;
        vertical-align: top;
        margin-top: 0.3rem;
        height: 0.5867rem;
        background: #cccccc;
      }
      .allBox {
        width: 38%;
        line-height: 1.04rem;
        height: 1.0667rem;
        display: inline-block;
        color: #393c41;
        font-size: 0.32rem;
        vertical-align: top;
      }
    }
    .searchBox {
      height: 1.6rem;
      margin: 0 auto;
      width: 100%;
      position: fixed;
      background: #f4f5f7;
      top: 2.2rem;
      .search {
        width: 94%;
        margin: 0 auto;
        height: 0.8rem;
        padding-top: 0.4rem;
        .tableNuber {
          width: 21%;
          height: 0.7733rem;
          font-size: 0.3733rem;
          padding-left: 0.2667rem;
          background: #fff;
          border: 0.0267rem solid #cccccc;
          border-radius: 0.1333rem;
          margin-left: 0.1333rem;
          float: left;
        }
        .orderNumer {
          width: 41%;
          height: 0.7733rem;
          font-size: 0.3733rem;
          padding-left: 0.2667rem;
          background: #fff;
          border: 0.0267rem solid #cccccc;
          border-radius: 0.1333rem;
          float: left;
          margin-left: 0.2667rem;
        }
        .searchResult {
          width: 20.5%;
          height: 0.7733rem;
          font-size: 0.3733rem;
          background: #d8af68;
          border-radius: 0.1867rem;
          float: right;
          color: #fff;
          line-height: 0.7467rem;
          margin-right: 0.08rem;
          margin-top: 0.0533rem;
        }
      }
    }
  }
  .scrollBox {
    position: fixed;
    margin-top: 2.7rem;
    width: 98%;
    height: 79%;
    overflow-y: auto;
    padding-bottom: 2rem;
    .orderList {
      background: #ffffff;
      margin-bottom: 0.2667rem;
      width: 96%;
      height: auto;
      padding-bottom: 0.2667rem;
      margin: 0 auto 0.2667rem;
      box-shadow: 0 0 0.2133rem 0rem #dadada;
      border-radius: 0.1333rem;
      position: relative;
      .mintui {
        position: absolute;
        top: 1.2rem;
        right: 0.05rem;
        color:#ccc;
        transform: rotate(180deg);
      }
      .blueLind {
        height: 0.3733rem;
        width: 1.1%;
        background: #646f83;
        display: inline-block;
        float: left;
        margin-top: 0.48rem;
      }
        .returnPay {
    width: 94%;
    height: 1rem;
    border-top: 0.0267rem solid #eeeeee;
    margin: 0 auto;
    .returnButton {
      margin: 0 auto;
      width: 25%;

      background: #999;
      border-radius: 0.1867rem;
      color: #fff;
  
      line-height: 0.7467rem;
      margin-top: 0.2133rem;
    }
  }
      .infoL {
        width: 49%;
        height: auto;
        text-align: left;
        display: inline-block;
        padding-top: 0.3733rem;
        .lineInfo {
          margin-bottom: 0.1067rem;
          color: #999999;
          .titleThird {
            display: inline-block;
            color: #999999;
            letter-spacing: 0.2667rem;
          }
          .titileS {
            display: inline-block;
            color: #999999;
          }
          .nomalL {
            letter-spacing: 0rem;
          }
          span {
            font-size: 0.32rem;
            color: #151515;
          }
        }
      }
      .infoR {
        width: 45%;
      }
      .infoBo {
        text-align: left;
        margin-left: 0.3733rem;
        .titleThird {
          display: inline-block;
          color: #999999;
          letter-spacing: 0.2667rem;
        }
        .titileS {
          display: inline-block;
          color: #999999;
        }
        .nomalL {
          letter-spacing: 0rem;
        }
        span {
          margin-left: 0.1333rem;
          font-size: 0.3733rem;
          color: #151515;
        }
        b {
          font-size: 0.32rem;
          color: #4990e2;
          text-decoration: underline;
          margin-left: 0.1333rem;
        }
      }
    }
  }
}
.nothing {
  width: 80px;
  height: 88px;
  margin: 150px auto 0 auto;
}
.nothingTxt {
  text-align: center;
  font-size: 20px;
  color: #ccd1d5;
  margin-top: 20px;
}
.show {
  display: block;
}
.hide {
  display: none;
}
.restaurantDetail_box {
  overflow: auto;
  height: 100%;
  .DetailTop {
    width: 90%;
    height: 1.2533rem;
    margin: 0 auto;
    font-size: 0.32rem;
    color: #333333;
    .topL {
      float: left;
      line-height: 1.2267rem;
    }
    .topR {
      float: right;
      line-height: 1.2267rem;
      span {
        display: inline-block;
      }
      .circleBox {
        width: 0.5333rem;
        height: 0.5333rem;
        background: #646f83;
        border-radius: 1.36rem;
        color: #fff;
        display: inline-block;
        vertical-align: top;
        margin-top: 0.3733rem;
        line-height: 0.5867rem;
      }
    }
  }
}
</style>


