<template>
<div class="aversion">
    <topNav title="版本信息" url='/' replace="true"></topNav>
    <div class="title">
        <img src="../../assets/img/icon.png" v-if="SYS_DEVICE != 'PUN'">
        <img src="../../assets/img/ys.png" v-else>
        <h2>版本号：{{version}}</h2>
    </div>
    <div class="infoList">
        <ul class="tip">
            <li>
                <h2>{{version}}</h2>
            </li>
            <li>1、此版本配合云餐饮2.5.28版本以上</li>
            <li>2、云餐饮增加登录</li>
            <li>3、从服务端返回支付方式</li>
        </ul>
        <ul class="tip">
            <li>
                <h2>1.5.0</h2>
            </li>
            <li>1、增加支付稳定性</li>
        </ul>
        <ul class="tip">
            <li>
                <h2>1.4.1</h2>
            </li>
            <li>1、增加卡券核销</li>
            <li>2、增加储值支付</li>
            <li>3、增加储值充值</li>
        </ul>
        <ul class="tip">
            <li>
                <h2>V1.3.0</h2>
            </li>
            <li>1、改进支付唯一判定</li>
        </ul>
        <ul class="tip">
            <li>
                <h2>V1.2.0</h2>
            </li>
            <li>1、入账核对接口</li>
            <li>2、修改错误消息提示</li>
        </ul>

        <ul class="tip">
            <li>
                <h2>V1.0.0</h2>
            </li>
            <li>1、支持银行卡、支付宝、微信支付。</li>
            <li>2、支持转房间、转AR账。</li>
            <li>3、支持关联会员、协议公司。</li>
            <li>4、交易查询。</li>
            <li>5、当天交易支持撤消。</li>
        </ul>
        <div class="ver">
            <img src="../../assets/img/vlogo.png" v-if="SYS_DEVICE != 'PUN'">
            <div>&copy; 2017 - 2019 ihotel.cn. All rights reserved.</div>
        </div>
    </div>
</div>
</template>

<script>
export default {
    data() {
        return {
            version: "V1.5.2",
            SYS_DEVICE: ''
        };
    },
    mounted() {
        this.SYS_DEVICE = this.$SYS_DEVICE;
        // if(this.SYS_DEVICE == "PUN"){
        //    this.version = "2.4.0";
        // }
    },
    methods: {},
    filters: {},
    components: {}
};
</script>

<style lang="less">
.aversion {
    .title {
        text-align: center;
        margin-top: 10px;

        h1 {
            margin: 20px auto 5px;
            font-size: 0.5rem;
        }

        h2 {
            font-size: 0.3rem;
            font-weight: normal;
            margin-top: 0;
            color: #999;
        }

        img {
            width: 72px;
            margin: 0 auto 5px;
        }
    }

    .tip {
        width: 90%;
        margin: 0 auto;
        margin-top: 1rem;
    }

    .tip li {
        line-height: 0.5rem;
        margin-top: 5px;
        text-align: left;
        color: #000;
    }

    .tip .pic img {
        width: 80%;
        margin: 0 auto;
    }

    .ver {
        position: fixed;
        bottom: 10px;
        left: 0;
        width: 100%;
        text-align: center;

        img {
            width: 63px;
            height: 18px;
            margin: 0 auto;
        }

        div {
            font-size: 9px;
            color: #d7d7d7;
            margin-top: 8px;
        }
    }
}
</style>



