import Vue from "vue";
import Router from "vue-router";
import baseRoutes from "./baseRouter";
import taikangRouter from "./taikangRouter";
import mobileOrderRouter from "./mobileOrderRouter";

Vue.use(Router);

const routesArr = [...baseRoutes, ...taikangRouter, ...mobileOrderRouter];
export default new Router({
  routes: routesArr
});



