<template>
  <div class="adminpass" >
        <div class="bgBox"></div>
        <div class="contentBox">
          <div class="top">
            <span>请输入管理员密码</span>
            <div class="iconfont icon-cuowu" @click="goHome"></div>
          </div>
          <div class="inputTwo">
        
            <input type="password" placeholder="密码" class="inp" v-model="pass" autocomplete="off" >
          </div>
          <span class="twoSure" @click="checkPass">确认</span>

        </div>
    </div>
</template>
<script>
import { Toast } from "mint-ui";
import md5 from 'js-md5';
export default {
  data() {
    return {
      pass: "",
      adpass:!!localStorage.adpass?localStorage.adpass:md5("1234")
    };
  },
  mounted() {},
  methods: {
    goHome(){
      this.$router.push("operapay");
    },
    checkPass(){
      if(md5(this.pass) != this.adpass){
         this.msg("密码不正确");
      }
      else{
        this.$emit('success');
      }
    },
    msg(msg) {
      Toast({
        message: msg,
        position: "bottom",
        duration: 3000
      });
    }
  },
  components: {}
};
</script>
<style lang='less'>
.adminpass {
  opacity: 0.7;
  background: #000000;
  width: 100%;
  height: 100%;
  .bgBox {
    height: 3rem;
  }
  .contentBox {
    width: 80%;
    margin: 0 auto;
    background: #ffffff;
    border: 1px solid #f3f3f3;
    border-radius: 5px;
    padding:0.3rem  0.3rem 1rem;
    .top {
      width: 92%;
      height: 1.1467rem;
      margin: 0 auto;
      span {
        font-size: 0.32rem;
        color: #333333;
        float: left;
        margin-top: 0.3467rem;
        font-weight: bold;
      }
      .icon-cuowu {
        font-size: 0.32rem;
        color: #999999;
        float: right;
        margin: 0.2667rem 0.2rem;
      }
    }
    .inputTwo {
      height: 2.2rem;
      width: 76.7%;
      margin: 0.6rem auto;
      .inp {
        width: 100%;
        height: 0.9867rem;
        text-transform: uppercase;
        background: #ffffff;
        border: 0.0267rem solid #cccccc;
        border-radius: 0.1333rem;
        margin-top: 0.2933rem;
        padding-left: 0.3733rem;
      }
    }
    .twoSure {
      font-size: 0.3733rem;
      color: #ffffff;
      width: 45%;
      height: 0.9867rem;
      line-height: 0.9067rem;
      background: #ff8902;
      border-radius: 0.1867rem;
      text-align: center;
      display: block;
      margin: 0 27%;
    }
    
  }
}
</style>


