import Vue from "vue";
import Vuex from "vuex";
import myList from "./modules/myList";
import food from "./modules/food";
import user from "./modules/user";
import mobileOrdering from "./modules/mobileOrdering";

Vue.use(Vuex);

export default new Vuex.Store({
  state: {
    count: 1,
    pageDirection: "fade",
    routeChain: []
  },
  mutations: {
    addRouteChain(state, route) {
      state.routeChain.push(route);
    },
    popRouteChain(state) {
      state.routeChain.pop();
    },
    setPageDirection(state, dir) {
      state.pageDirection = dir;
    }
  },
  modules: {
    myList,
    user,
    food,
    mobileOrdering
  }
});



