<template>
  <div class="anyPay">
        <home system="2"></home>
    <div class="index_box">
      <div class="topLogo">
        <div class="logofont">{{hotelName}}</div>
      </div>
      <router-link to='/checkstand'>
        <div class="orderPay">
          <div class="iconfont icon-shuaqia"></div>
          <div class="order_right">
            <div class='titleT'>收银台</div>
              <div class="titlehird" v-if="SYS_DEVICE=='NG'">微信／支付宝</div>
             <div class="titlehird" v-else>银行卡／微信／支付宝</div>
          </div>
        </div>
      </router-link>
      <router-link to='/anypaySearch'>
        <div class="tradingSearch">
          <div class="iconfont icon-dianzixianjinyuechaxun"></div>
          <div class='titleT'>交易查询</div>
          <div class="titlehird">历史订单／金额</div>
        </div>
      </router-link>
      <div class="right_box">
        <router-link to='anypayRefundList'>
          <div class="refunds">
            <div class="iconfont icon-tui"></div>
            <div class="titleS">退款</div>
          </div>
        </router-link>
        <router-link to='/settingPay'>
          <div class="seting">
            <div class="iconfont icon-shezhi"></div>
            <div class="titleS">设置</div>
          </div>
        </router-link>
      </div>
    </div>
  </div>
</template>

<script>
import { Toast } from "mint-ui";
import home from "../common/home";
export default {
  data() {
    return {
      hotelName: "",
       SYS_DEVICE:""
    };
  },
  mounted() {
     this.SYS_DEVICE = this.$SYS_DEVICE;
    if(localStorage.system == 2){
      this.init();
    }
  },
  methods: {
    init() {
      var gateway = localStorage.getItem("anygateway");
      if (gateway) {
        if (localStorage.JWT_TOKEN) {
           this.$http.post("/mobilepmsapi/sys/getHomeUserInfo").then(res => {
            if (res.data.result == 0) {
              this.hotelName = res.data.retVal.hotelName;
            }
          });
        } else {
          this.$router.push("login");
        }
      } else {
        this.msg("未设置参数，请点设置进行设置!");
      }
    },
    msg(msg) {
      Toast({
        message: msg,
        position: "bottom",
        duration: 3000
      });
    }
  },
  components: {
    home
  }
};
</script>
<style lang="less">
.anyPay {
  width: 100%; // border-top:0.0267rem solid #00487C;
  overflow: auto;
  height: 100vh;
  background: #003963;
  .index_box {
    width: 96%;
    height: auto;
    margin: 0 auto;
    .topLogo {
      margin-top: 0.2933rem;
      width: 100%;
      height: 2.6933rem;
      background: #00213f;
      border-radius: 0.1333rem;
      background-color: rgba(0, 0, 0, 0.3);
      text-align: center;
      .logofont {
        font-size: 0.8rem;
        color: #ffffff;
        line-height: 2.6667rem;
      }
    }
    .orderPay {
      margin-top: 0.2667rem;
      width: 100%;
      height: 3.8933rem;
      border-radius: 0.1333rem;
      background: #df6228;
      .icon-shuaqia {
        font-size: 1.7067rem;
        color: #ffffff;
        display: inline-block;
        margin-right: 0.2667rem;
        margin-top: 0.4533rem;
      }
      .order_right {
        width: 57%;
        display: inline-block;
        height: auto;
        text-align: left;
        margin-left: 0.2667rem;
        margin-top: 0.8267rem;
        vertical-align: top;
        .titleT {
          font-size: 0.7467rem;
          color: #ffffff;
        }
        .titlehird {
          font-size: 0.45rem;
          color: #ffffff;
          margin-top: 0.2667rem;
        }
      }
    }
    .tradingSearch {
      width: 48%;
      height: 7.2rem;
      background: #eeb900;
      margin-top: 0.2667rem;
      border-radius: 0.1333rem;
      overflow: hidden;
      display: inline-block;
      .icon-dianzixianjinyuechaxun {
        font-size: 1.7067rem;
        color: #ffffff;
        margin-top: 0.7467rem;
      }
      .titleT {
        font-size: 0.7467rem;
        color: #ffffff;
      }
      .titlehird {
        font-size: 0.3733rem;
        color: #ffffff;
        margin-top: 0.2667rem;
      }
    }
    .right_box {
      width: 47%;
      height: 7.2rem;
      margin-top: 0.2667rem;
      overflow: hidden;
      display: inline-block;
      margin-left: 0.2667rem;
      .refunds {
        width: 100%;
        height: 3.4667rem;
        background: #1888c0;
        border-radius: 0.1333rem;
        margin-bottom: 0.2667rem;
        overflow: hidden;
        .icon-tui {
          font-size: 1.3333rem;
          color: #ffffff;
          margin-top: 0.2667rem;
        }
        .titleS {
          font-size: 0.48rem;
          color: #ffffff;
        }
      }
      .seting {
        width: 100%;
        height: 3.4667rem;
        background: #07b5c6;
        border-radius: 0.1333rem;
        margin-bottom: 0.2667rem;
        overflow: hidden;
        .icon-shezhi {
          margin-top: 0.2667rem;
          font-size: 1.3333rem;
          color: #ffffff;
        }
        .titleS {
          font-size: 0.48rem;
          color: #ffffff;
        }
      }
    }
  }
}
</style>


