<template>
  <div class="menulist">
    <table class="tableDetail">
      <tr>
        <th style="width:30%;text-align:left;">项目</th>
        <th style="width:12%;white-space:nowrap">数量</th>
        <!-- <th style="width:25%;">单价</th> -->
        <th style="padding-right:20px;">金额</th>
      </tr>
        <template  v-for="(item,index) in detailLine"  >
        <tr class="contentDetail" v-if="!(item.detailType=='DtlDscAllocType' || item.detailType=='DtlDscType'  || item.detailType=='DtlTmedType'  || item.detailType=='DtlMiErrorCorrectType' || item.detailType =='DtlExtensibilityDataType' ||item.voidLink!=0 || item.salesCount == 0)" :key="index">
            <td class="dishname" :class="{subItem:item.parentDetailLink!=0}">{{item.menuItem?item.menuItem.name:meanutype(item.detailType) }}</td>
            <td>{{item.salesCount}}</td>
            <!-- <td style="padding-right:20px;">*</td> -->
            <td class="rt"><template v-if="item.total != 0">{{item.total | tofixed}}</template></td>
        </tr>
        </template>
    </table>
  </div>
</template>
<script>
export default {
  name: "menuList",
  data() {
    return {};
  },
  props: {
    detailLine: {
      type: Array,
      default() {
        return [];
      }
    }
  },
  methods: {
    meanutype(val) {
      switch (val) {
        case "DtlSvcType":
          return "[现金小费]";
          break;
        case "DtlDscType":
          return "自定金额折扣";
          break;
        case "DtlTmedType":
          return "已付金额";
          break;
        // case 'DtlExtensibilityDataType':
        //   return "123"
        //   break;
        default:
          return "";
          break;
      }
    }
  },
  filters: {
    tofixed(v) {
      return parseFloat(v).toFixed(2);
    }
  },
  mounted() {},
  computed: {}
};
</script>
<style lang="less">
.menulist {
  .tableDetail {
    border: none;
    text-align: left;
    width: 94%;
    margin: 0 auto;
    border-bottom: 0.0267rem dashed #cccccc;
    .contentDetail {
      height: 0.6467rem;
      td {
        text-align: right;
        white-space: nowrap;
        text-overflow: ellipsis;
        overflow: hidden;
        small {
          font-size: 0.2rem;
        }
      }
      .dishname {
        width: 30%;
        text-align: left;
          overflow: hidden;
    text-overflow: ellipsis;
    white-space: nowrap;
    max-width: 140px;
      }
      .subItem{
        padding-left:0.5rem;
      }
      .rt {
        padding-right: 20px;
      }
    }
    tr {
      th {
        width: 28%;
        height: 1.2rem;
        border-bottom: 0.0267rem solid #cccccc;
        padding-left: 0.2667rem;
        text-align: right;
      }
    }
  }
}
</style>


