<template>
<div class="gpicker">
    <div type="text" class="ginput" @click="open">
        <template v-if="disValue">{{disValue}}</template>
        <span v-else class="ph">{{placeholder}}</span>
    </div>
    <i class="iconfont icon-ADown" @click="open"></i>
    <vue-pickers :show="show" :selectData="optionSlot" v-on:cancel="close" v-on:confirm="confirmFn"></vue-pickers>
</div>
</template>

<script>
import VuePickers from "../common/selectPicker";
export default {
    data() {
        return {
            show: false,
            disValue: "",
            value: "",
            optionSlot: {
                columns: 1,
                default: [{
                    text: "",
                    value: ""
                }],
                pData1: []
            },
            data: [],
            disFormat: null
        };
    },
    props: {
        placeholder: {
            type: String
        }
    },
    mounted() {},
    methods: {
        setData(datas, dv, df) {
            this.data = datas;
            this.value = dv;
            if (df != null) {
                this.disFormat = df;
            }
            this.setInit();
            this.show = false;
        },
        setInit() {
            this.optionSlot.pData1 = [];
            let bfind = this.value == null ? true : false;
            if (this.data != null) {
                this.data.forEach((val, key, arr) => {
                    var text =
                        this.disFormat == null ? arr[key].name : this.disFormat(arr[key]);
                    this.optionSlot.pData1.push({
                        text: text,
                        value: arr[key].code
                    });
                    if (!bfind && this.value == arr[key].code) {
                        this.disValue = text;
                        bfind = true;
                    }
                });

                if (!bfind && this.optionSlot.pData1.length > 0) {
                    this.disValue = this.optionSlot.pData1[0].text;
                    this.value = this.optionSlot.pData1[0].value;
                }
                this.optionSlot.default = [{
                    text: this.disValue,
                    value: this.value
                }];
                //  console.log(this.optionSlot);
            }
        },
        setDefault(v) {
            this.value = v;
            this.setInit();
        },
        clear() {
            this.value = null;
            this.disValue = null;
        },
        open() {
            this.show = !this.show;
            if (this.show) {
                this.setInit();
            }
        },
        close() {
            this.show = false;
        },
        getValue() {
            return this.value;
        },
        getDisValue() {
            return this.disValue;
        },
        getDisValueF() {
            return this.getNameFromDis(this.disValue);
        },
        getNameFromDis(v) {
            if (v) {
                return v.split(' -- ')[1];
            }
            return "";
        },
        confirmFn(data) {
            this.show = false;
            this.disValue = data.select1.text;
            this.value = data.select1.value;
            this.$emit("change", this.value, this.disValue);
        }
    },
    filters: {},
    watch: {},
    components: {
        VuePickers
    }
};
</script>

<style lang="less">
.gpicker {
    height: 0.8rem;
    line-height: 0.8rem;

    .ginput {
        width: 100%;
        height: 100%;
    }

    .iconfont {
        position: absolute;
        right: 0.08rem;
        top: 0px;
        font-size: 0.3rem;
        color:#666;
    }

    .ph {
        color: #ccc;
    }
}
</style>



