<template>
  <div class="operaToFrontDest">
    <topNav title="转房间" :url="'/opOrderPayDetail?tableno='+getTableNo()"></topNav>
    <div class="listBox">

      <div class="infoDetails">
        <p style="width:47%;float:left;">主桌号:
          <span class="infoText">{{getTableNo()}}</span>

        </p>
        <p style="width:46%;float:left;">账单号：
          <span class="infoText">{{orderDetailObj.summary.checkNumber}}</span>
        </p>
        <!-- <div class="tableList">
            <p class="listlable">开单时间：</p>
            <span>{{orderDetailObj.summary.checkOpenTime | datef}}</span>
          </div>
          <div class="tableList">
            <p class="listlable">开桌时间：</p>
            <span>{{orderDetailObj.summary.tableOpenTime | datef }}</span>
          </div> -->
  
        <div class="infoL">
          <p class="listlable">开单时间：
            <span class="infoText">{{orderDetailObj.summary.checkOpenTime | datef }}</span>
          </p>
         
          <p>当前消费：
            <span class="infoText">
              <small>￥</small>{{orderDetailObj.summary.subTotal | tofixed}} </span>
          </p>
          <p>含税：
            <span class="infoText">
              <small>￥</small>{{orderDetailObj.summary.tax | tofixed}} </span>
          </p>
          <p>折扣：
            <span class="infoText">
              <small>￥</small>{{orderDetailObj.summary.payment | tofixed}} </span>
          </p>

          <p>应收：
            <b style="color:red">
              <small>￥</small>{{OpenChecksObj.checkTotalDue | tofixed}} </b>
          </p>
        </div>
        <div class="infoR">
            <p>人　数：
            <span class="infoText">
             {{orderDetailObj.summary.covers }} </span>
          </p>
          <p>服务费：
            <span class="infoText">
              <small>￥</small> {{orderDetailObj.summary.autoGratuity | tofixed}}</span>
          </p>
          <p>其他费用：
            <span class="infoText">
              <small>￥</small> {{orderDetailObj.summary.other | tofixed}}</span>
          </p>
          <p>已付款：
            <span class="infoText">
              <small>￥</small>{{orderDetailObj.summary.payment | tofixed}}</span>
          </p>

        </div>
      </div>
    </div>
    <div class="iconBox">
      <div class="iconfont icon-zhuanhuan"></div>
    </div>
    <div class="vipTop">
      <input class="orderNumer" type="text" name="" id="" placeholder="房号" v-model="keyInput">
      <div class="searchResult" @click="searchRoom">搜索</div>
    </div>
    <div class="vipList" v-for="(item, index) in roomObj" :key="index" @click="selectRoom(item,index)">
      <div class="blueLind"></div>
      <div class="vipCheckBox clearfix">
        <div class="infoBo">
          <div class="titleS">宾客号：</div>
          <span>{{item.guestNum}}</span>
        </div>
        <div class="greenCheck">
          <div class="checkBox" :class="{'noPost':item.noPost}">
            <div class="iconfont" v-bind:class="{ 'icon-right': item.isSelect}"></div>
          </div>
        </div>
      </div>
      <div class="infoDetail" style="margin-top:5px">
        <div class="wholeLine">
          <p>宾客名：
            <span>{{item.fullName}}</span>
          </p>
        </div>
        <div class="infoL" style="width:100%">
          <p>Vip状态：
            <span style="width:2.3467rem">{{item.guestVIPStatus}}</span>
          </p>       
        </div>
        <div class="infoR">
          <p>可用额度：
            <span>￥{{item.creditLimit}}</span>
          </p>
        </div>
      </div>
      <div class="infoDetail" v-if="item.guestArrival">
        <div class="infoDivide">
          <p style="text-align: left;width:46%">到日：
            <span>{{item.guestArrival | formatDate}}</span>
          </p>
          <p style="width:46%">离日：
            <span>{{item.guestDepart | formatDate}}</span>
          </p>
          <div class="clearfix"></div>
        </div>
      </div>
      <div class="infoDetail clearBottom">
        <div class="infoDivide">
          <p style="width:46%">PMS支付方式：
            <span>{{item.payMethod}}</span>
          </p>
          <p style="width:46%">可否挂账：
            <span>{{item.noPost|isCanPost}}</span>
          </p>
          <div class="clearfix"></div>
        </div>
      </div>
    </div>
    <div v-bind:class="{ 'hide': !isEmpty }">
      <img class="nothing" src="../../assets/img/nothing.png" v-show="!isRoomFrist">
      <p class="nothingTxt" v-show="!isRoomFrist">什么都没有</p>
    </div>
    <div class="clearChoice"></div>
    <div class="footerSure">
      <span @click='showAlter()'>确定</span>
    </div>

  </div>
</template>

<script>
import { Toast, MessageBox } from "mint-ui";
import md5 from "js-md5";
import moment from "moment";
import { getAccountJson } from "../../assets/js/md5Data.js";
export default {
  name: "operaToFrontDest",
  data() {
    return {
      showTwo: false,
      show: false,
      keyInput: "",
      roomObj: [],
      orderDetailObj: JSON.parse(localStorage.orderDetail),
      OpenChecksObj: JSON.parse(localStorage.OpenChecks),
      roomSelect: false,
      selectRoomObj: {},
      user: "",
      lastPrintObj: null,
      password: "",
      isEmpty: true,
      isRoomFrist: true,
      urlString: "/orderPayDetail?tableno"
    };
  },

  methods: {
    pushAccountToRoom() {
      let params = {};
      params.employeeObjectNum = this.OpenChecksObj.checkEmployeeObjectNum;
      params.checkRevenueCenter = this.OpenChecksObj.checkRevenueCenter;
      params.checkSeq = this.OpenChecksObj.checkSeq;
      params.paymenttotal = this.OpenChecksObj.checkTotalDue;
      params.amount = this.OpenChecksObj.checkTotalDue;
      params.checkNum = this.OpenChecksObj.checkNum;
      params.roomNum = this.keyInput;
      params.checkEmployee = this.OpenChecksObj.checkEmployee;
      params.checkRevenueCenterID = this.OpenChecksObj.checkRevenueCenterObjectNum;
      params.tableno = this.OpenChecksObj.checkTableObjectNum;
      params.orderTypeStr = this.OpenChecksObj.orderTypeStr;

      // params.serviceAmount = this.orderDetailObj.summary.autoGratuity;
      // params.taxAmount = this.orderDetailObj.summary.tax;
      // params.tipAmount = this.orderDetailObj.summary.other;
      // params.disAmount = this.getAlldsc();
      params.accountJson =  getAccountJson(this.orderDetailObj);
      params.guestNum = this.selectRoomObj.guestNum;
      params.pmsHotelId = this.selectRoomObj.hotelId;
      params.guestName = this.selectRoomObj.guestName;
      params.tmedObjectNum = "PUN_ROOM"; //this.selectRoomObj.payMethod
      
      params.periodID = this.orderDetailObj.summary.servingPeriodID;

     // console.log(params);

     // return;
      this.$http.post("/ohotel/posApi/pushAccountToRoom", params).then(res => {
        if (res.data.success) {
          if (
            res.data.message != null &&
            res.data.message.indexOf("300_") == 0
          ) {
            var arr = res.data.message.split("_");
            MessageBox.alert(
              "结账成功，结账金额与Opera抛多金额不一致，请人工补账，上次抛账金额为：" +
                arr[2]
            ).then(action => {
              this.signCode("请客人签名");
            });
          } else {
            this.signCode("入账成功,请客人签名");
          }
        } else {
          MessageBox.alert(res.data.message);
        }
      });
    },
    getTableNo() {
      return this.OpenChecksObj.checkTableName == null
        ? this.OpenChecksObj.checkTableObjectNum
        : this.OpenChecksObj.checkTableName;
    },
    getAlldsc() {
      let dsc = 0;
      if (!this.orderDetailObj.detailLines.detailLine) {
        return dsc;
      }
      this.orderDetailObj.detailLines.detailLine.forEach(function(
        el,
        key,
        arr
      ) {
        if (el.detailType == "DtlDscType") {
          dsc += parseFloat(el.total);
        }
      });
      return dsc;
    },
    showAlter() {

      // var tt =  getAccountJson(this.orderDetailObj);
      // console.log(tt);
      // return;
 
      if(this.orderDetailObj.summary.payment > 0){
        this.msg("本订单已存在支付，不支持MiPOS操作");
        return;
      }
      if (!this.selectRoomObj.isSelect) {
        this.msg("请选择宾客");
        return;
      }
      if (this.OpenChecksObj.checkTotalDue > this.selectRoomObj.creditLimit) {
        this.msg("可用挂账额度不足");
        return;
      }
      MessageBox.confirm("确定挂账?")
        .then(action => {
          this.pushAccountToRoom();
        })
        .catch(action => {});
    },
    signCode(msg) {
      handWrite(this.signSccess, null, msg);
    },
    signSccess(value) {
      if (value == "1") {
        var tobj = this.orderDetailObj;
        var gatewayJson = localStorage.getItem("simgateway");
        if (gatewayJson != null) {
          var gateway = JSON.parse(gatewayJson);
          tobj.hotelName = gateway.hotelName;
        }
        tobj.invoiceGateway = null;
        tobj.payway = null;
        tobj.tableNo = this.OpenChecksObj.checkTableObjectNum;
        tobj.checkEmployee = this.OpenChecksObj.checkEmployee;
        tobj.orderTypeStr = this.OpenChecksObj.orderTypeStr;
        tobj.summary.due = this.OpenChecksObj.checkTotalDue;
        tobj.addInfo = "挂账房间：" + this.keyInput;
        tobj.sign = "1";
        // console.log(JSON.stringify(tobj));
        this.lastPrintObj = tobj; //JSON.stringify(tobj);
        printSimphonyData(
          this.printSuccess,
          this.printError,
          this.lastPrintObj
        );
      } else {
        MessageBox.confirm("请确定是否取消打印?")
          .then(action => {
            this.$router.push({
              path: "/opOrderPayDetail?isSucceed=true"
            });
          })
          .catch(action => {
            this.signCode("");
          });
      }
    },
    printError(v) {
      MessageBox.confirm("打印出错:" + v + "，继续打印?")
        .then(action => {
          printSimphonyData(
            this.printSuccess,
            this.printError,
            this.lastPrintObj
          );
        })
        .catch(action => {
          this.$router.push({
            path: "/opOrderPayDetail?isSucceed=true"
          });
        });
    },
    printSuccess() {
      //console.log("printSuccess");
      this.$router.push({
        path: "/opOrderPayDetail?isSucceed=true"
      });
    },
    fork() {
      this.showTwo = false;
    },
    getPayAmout() {
      return (
        Number(this.orderDetailObj.charge) - Number(this.orderDetailObj.credit)
      );
    },
    msg(msg) {
      Toast({
        message: msg,
        position: "bottom",
        duration: 3000
      });
    },
    getFoFkList(keyInput) {
      this.$http
        .post("/ohotel/posApi/queryArRoom", { roomNum: keyInput })
        .then(res => {
          if (res.data.success) {
            this.roomObj = res.data.result;
            //  console.log(this.roomObj);
            if (this.roomObj.length > 0) {
              for (var item in this.roomObj) {
                this.roomObj[item].isSelect = false;
              }
              this.isEmpty = false;
              this.isRoomFrist = false;
            } else {
              this.isEmpty = true;
              this.isRoomFrist = false;
            }
          } else {
            this.msg(res.data.message);
            this.isEmpty = true;
          }
        });
    },
    searchRoom() {
      if (this.keyInput) {
        this.getFoFkList(this.keyInput);
      } else {
        this.msg("请输入房号");
      }
    },
    selectRoom(item, index) {
      if (item.noPost) {
        this.msg("该宾客未开通挂账功能！");
        return;
      }
      this.selectRoomObj = item;
      var itemObj = item;
      //只能选中其中一个
      if (!itemObj.isSelect) {
        for (var i = 0; i < this.roomObj.length; i++) {
          this.roomObj[i].isSelect = false;
        }
      }
      itemObj.isSelect = !itemObj.isSelect;
      this.$set(this.roomObj[index], "isSelect", itemObj.isSelect);
      // 触发数组检测
      this.roomObj.push(11);
      this.roomObj.pop(11);
    },
    checkOutOrder() {
      let postObj = {};
      this.$http.post("/pos/posUnion/saveOrderCheckOut", postObj).then(res => {
        if (res.data.errorCode == 0) {
          this.msg("入账成功");
          this.$router.push({
            path: "/orderPayDetail?isSucceed=true"
          });
        } else {
          this.msg(res.data.errorMsg);
        }
      });
    },
    checkUser() {
      var user = this.user;
      var password = md5(this.password);
      this.$http
        .post("/pos/posUnion/updateCheckUserInfo", {
          user: user,
          password: password
        })
        .then(res => {
          if (res.data.errorCode == 0) {
            // 校验用户信息成功，开始执行入账程序
            this.showTwo = !this.showTwo;
            this.checkOutOrder();
          } else {
            this.msg(res.data.errorMsg);
          }
        });
    }
  },
  mounted() {},
  filters: {
    formatDate(val) {
      return val == null ? "" : val.substr(0, 10);
    },
    tofixed(val) {
      return parseFloat(val).toFixed(2);
    },
    datef(value) {
      return moment(value)
        .add(8, "hour")
        .format("MM/DD HH:mm");
    },
    isCanPost(val) {
      return !val ? "是" : "否";
    }
  }
};
</script>
