        width: 45%;
        height: 0.9867rem;
        line-height: 0.9067rem;
        background: #ff8902;
        border-radius: 0.1867rem;
        display: inline-block;
        margin-bottom: 0.3467rem;
      }
      p {
        font-size: 0.32rem;
        color: #999999;
      }
    }
  }
  .nothing {
    width: 80px;
    height: 88px;
    margin: 100px auto 0 auto;
  }
  .hide {
    display: none;
  }
}
</style>



