import md5 from 'js-md5';
function getKeyUrlYun(hotelGroupCode,hotelCode,accnt) {
    var keyarry = [];
    keyarry.push({ k: "hotelGroupCode", v: hotelGroupCode});
    keyarry.push({ k: "hotelCode", v:hotelCode});
    keyarry.push({ k: "accnt", v: accnt });
    return getKeyUrl(keyarry);
}
function getKeyUrl(keyarry) {
  var arrSortResult = _.sortBy(keyarry, function (item) {
    return item.k;
  });
  var md5Str = "";
  _.forEach(arrSortResult, function (item) {
    if (md5Str != "") {
      md5Str += "&";
    }
    md5Str += item.k + "=" + _.trim(item.v);
  });
 // console.log(md5Str);
  var key = md5(md5(md5Str) + "f62edbd4141794d23dabeccf46e32500");
  return md5Str + "&key=" + key;
}
function getAccountJson(detail){
  
  return JSON.stringify({st:getScTotal(detail,'DtlMiType'), tx:{"1":detail.summary.tax},sc:{"1":detail.summary.autoGratuity,"2":detail.summary.other},dc:getdcTotal(detail.detailLines.detailLine)});
}
//服务费
function getScTotal(detail,type){
var ret = {};
  detail.detailLines.detailLine.forEach(function(el, key, arr) {
    if (el.detailType == type) {
      var num = el.menuItem.slsItmzrIndex.toString();
      if(ret[num] == undefined){
        ret[num] = parseFloat(el.total).toFixed(2);
      }
      else{
        ret[num]  = (parseFloat(ret[num])+ parseFloat(el.total)).toFixed(2);
      }
    }
  });
  return ret;
}
function  getdcTotal(dl) {
    var ret = {};
    dl.forEach(function(el, key, arr) {
      if (el.dscAlloc != null) {
        var as = el.dscAlloc.alloc.dscAllocEntry;
        if (Array.isArray(as)) {
          as.forEach(function(one) {
            var num = getDcOne(dl, one);
            //console.log(num);
            setNum(ret, num, one);
          });
        } else {
          var num = getDcOne(dl, as);
          //console.log(num);
          setNum(ret, num, as);
        }
      }
    });
    return ret;
  }
  function setNum(ret, num, one) {
    if (ret[num] == undefined) {
      ret[num] = -parseFloat(one.amount).toFixed(2);
    } else {
      ret[num] = (parseFloat(ret[num]) - parseFloat(one.amount)).toFixed(2);
    }
  }
  function getDcOne(dl, one) {
    for (var i = 0; i < dl.length; i++) {
      if (dl[i].detailLink == one.itemLink) {
        return dl[i].menuItem.slsItmzrIndex.toString();
      }
    }
    return null;
  }
export { 
    getKeyUrl,getKeyUrlYun,getAccountJson
}


