<template>
 <div class="am-flexbox-item am-grid-item11" style="width: 25%;">
        <div class="am-grid-item-content">
          <div class="am-grid-item-inner-content column-num-4">
            <i class="iconfont am-grid-icon " :class="icon" v-bind:style="{ color: iconColor}" ></i>
            <div class="am-grid-text" v-text="title"></div>
          </div>
        </div>
      </div>
</template>

<script>
/**
 * mt-header
 * @module components/header
 * @desc 顶部导航
 * @param {boolean} [fixed=false] - 固定顶部
 * @param {string} [title] - 标题
 * @param {slot} [left] - 显示在左侧区域
 * @param {slot} [right] - 显示在右侧区域
 *
 * @example
 * <mt-header title="我是标题" fixed>
 *   <mt-button slot="left" icon="back" @click="handleBack">返回</mt-button>
 *   <mt-button slot="right" icon="more"></mt-button>
 * </mt-header>
 */
export default {
  name: 'mt-grid',
  props: {
    icon: String,
    title: String,
    iconColor: String,
  },
  mounted (){
    
  },
};
</script>
<style lang="css">
 
</style>



