<template>
  <div class="optransactSearch">
    <topNav title="交易查询" url='/operapay'></topNav>
    <div class="dateAll">
      <div class="dataBox">
        <div class="data">
          <datepik @change='dateChange' style="margin:0 auto;float:none;" limit="pre" step="1"></datepik>
        </div>
        <div class="line"></div>
        <div class="allBox">
          共{{info.masterTotal}}笔，¥{{info.masterAmont |tofixed }}
        </div>
      </div>
      <div class="searchBox">
        <div class="search">
          <input class="tableNuber" type="text"  placeholder="桌号" v-model='searchOption.tableNo'>
          <input class="orderNumer" type="text"  placeholder="账单号" v-model='searchOption.checkNumbers'>
          <div class="searchResult" @click='search'>查询</div>
        </div>
      </div>
    </div>
    <div class="scrollBox" v-infinite-scroll="loadMore" infinite-scroll-disabled="loading" infinite-scroll-distance="10">
      <div class="orderList" v-for='(item,index) in listInfo' :key='item.checkNum'>
        <div class="blueLind"></div>
        <div class="numt">{{index+1}}</div>
        <div class="infoL" @click='openDetail(item)' >
          <div class="lineInfo">
            <div class="titleThird">账单：</div>
            <span>{{item.checkNum}}</span>
          </div>
          <div class="lineInfo">
            <div class="titleThird">餐厅：</div>
            <span>{{item.checkRevenueCenter}}</span>
          </div>
          <div class="lineInfo">
            <div class="titleThird">订单金额：</div>
            <span><small>￥</small>{{item.amount | tofixed}}</span>
          </div>
          <div class="lineInfo">
            <div class="titleThird">开单时间：</div>
            <span>{{item.checkOpenTime | fd}}</span>
          </div>
        </div>
        <div class="infoL infoR" @click='openDetail(item)' >
          <div class="lineInfo">
            <span class="ss">{{item.checkEmployee}}</span>
          </div>
           <div class="lineInfo" v-if="item.payCode == 'PUN_ROOM'">
            <span class="ss">房间：<font style="color:red">{{item.orderNo}}</font></span>
          </div>
           <div class="lineInfo" v-else-if="item.refunds == 0">
            <div class="titleThird">状态：</div>
            <span>已结</span>
          </div>
           <div class="lineInfo" v-else>
            <div class="titleThird">已退款：</div>
            <span style="color:red"><small>￥</small>{{item.refunds | tofixed}}</span>
          </div>
          <div class="lineInfo">
            <div class="titleThird">桌号：</div>
            <span>{{item.checkTableName}}</span>
          </div>
          <!-- <div class="lineInfo">
            <div class="titleThird">应付金额：</div>
            <span>{{item.checkTotalDue  | tofixed}}</span>
          </div> -->
          <div class="lineInfo">
            <div class="titleThird">{{item.payCode | payName}}：</div>
            <span><small>￥</small>{{item.payTotal}}</span>
          </div>
        </div>
        <i class="mintui mintui-back"></i>
         <div class="returnPay" v-if="parseFloat(item.amount) > parseFloat(item.refunds)">
          <div class="returnButton" @click='confirmP(item)'>重打印</div>
        </div>
      </div>
      <div v-bind:class="{ 'hide': !isEmpty }">
        <img class="nothing" src="../../assets/img/nothing.png">
        <p class="nothingTxt">什么都没有</p>
      </div>
      <div class="infoWarn" v-if='this.cutOff==1'> —— 我是有底线的 —— </div>
    </div>
        <mt-popup v-model="popupVisible" style="width:100%;height:100%" modal='false'>
      <div class="topHeader">
        <header class="mint-header is-fixed" style="background-color:#00487C;">
          <div class="mint-header-button is-left">
            <a class="router-link-exact-active router-link-active" @click='popupVisible=false' style="padding:.2rem .6rem .2rem 0;">
              <i class="mintui mintui-back"></i>
            </a>
          </div>
          <h1 class="mint-header-title">餐单详情</h1>
          <div class="mint-header-button is-right">
          </div>
        </header>
      </div>
      <operaOrderDetail ref="ordetailf"></operaOrderDetail>
    </mt-popup>
  </div>

</template>
<script>
import datepik from "../common/datepik";
import operaOrderDetail from "../common/operaOrderDetail";
import moment from "moment";
import { Toast, Actionsheet, MessageBox, InfiniteScroll } from "mint-ui";
import { getKeyUrl } from "../../assets/js/md5Data.js";
export default {
  data() {
    return {
      accnt: "",
      select_time: "",
      cutOff: "",
      isEmpty: true,
      popupVisible: false,
      info: {
        masterAmont: 0,
        masterTotal: 0
      },
      orderInfoObj: "",
      listInfo: [],
      searchOption: {
        lookUpStartDate: moment().format("YYYY-MM-DD"),
        checkNumbers: "",
        tableNo: null,
        orderTypeID: null,
        rvcObjectNum: null,
        currentPage: 1,
        pageSize: 10
      },
      vc: !!localStorage.vc
        ? JSON.parse(localStorage.vc)
        : {
            invoice: false,
            rvcObjectNum: ""
          },
      loading: false,
      lastPrintObj:null
    };
  },
  computed: {},
  mounted() {
    this.searchOption.rvcObjectNum = this.vc.rvcObjectNum;
    this.getInfo(false);
  },
  filters: {
    tofixed(v) {
      return parseFloat(v).toFixed(2);
    },
    payName(v){
      return getPayWayName(v);
    },
    fd(value) {
      return moment(value).format("HH:mm:ss"); //add(8,'hour').
    }
  },
  methods: {
    search() {
      this.getInfo(false);
    },

    dateChange(value) {
      this.searchOption.lookUpStartDate = moment(value).format("YYYY-MM-DD");
      this.listInfo = [];
      this.getInfo();
    },
    openDetail(item) {
      this.popupVisible = true;
      this.$refs.ordetailf.getMasterList(item,item.amount);
    },
    loadDetail(item){
     this.$http.post("/ohotel/posApi/getCheckDetail", {
          checkNumber:item.checkNum,
          checkSeqNumber:item.checkSeq,
        }).then(res=>{
           if (res.data.success) {
             this.PrintV(res.data.result,item)
           }else{
              this.msg(res.data.message);
           }
        })
    },
    confirmP(item) {
      event.preventDefault();
      MessageBox.confirm("确定重打印?")
        .then(action => {
          this.loadDetail(item);
        })
        .catch(action => {});
    },
     PrintV(tobj,item) {
          tobj.tableNo = item.checkTableName;
          tobj.checkEmployee = item.checkEmployee;
          tobj.orderTypeStr = item.orderTypeStr;
          tobj.summary.due =  item.amount;
          if (item.payCode != undefined) {
            tobj.payway = getPayWayName(item.payCode);
          } 
          var gatewayJson = localStorage.getItem("simgateway");
          if (gatewayJson != null) {
            var gateway = JSON.parse(gatewayJson);
            tobj.hotelName = gateway.hotelName;
            if(item.payCode == 'PUN_ROOM'){
                tobj.addInfo = "挂账房间：" + item.orderNo;
                tobj.payway = null;
                tobj.invoiceGateway = null;
            }
            else if (this.vc.invoice && item.payCode) {
              var keyarry = [];
              keyarry.push({ k: "checkNumber",v: item.checkNum });
              keyarry.push({ k: "hotelGroupCode", v: gateway.hotelGroupCode });
              keyarry.push({ k: "hotelCode", v: gateway.hotelCode });
              keyarry.push({ k: "bollingType", v: 2 });
              keyarry.push({ k: "checkSeqNumber", v: item.checkSeq
              });
              var urlv =
                gateway.url +
                "/ohotel/posApi/checkBilling?" +getKeyUrl(keyarry);
              tobj.invoiceGateway = urlv;
            }
          }
          tobj.reprint = 1;
          tobj.refunds = item.refunds;
          this.lastPrintObj = tobj;// JSON.stringify(tobj);
         //console.log(this.lastPrintObj);
          printSimphonyData(
            this.printSucceed,
            this.printError,
            this.lastPrintObj
          );
    },
    printError(v) {
      MessageBox.confirm("打印出错:" + v + "，继续打印?")
        .then(action => {
          printSimphonyData(this.printSucceed, this.printError, this.lastPrintObj);
        })
        .catch(action => {});
    },
    printSucceed(v){

    },
    getInfo(isloadMore) {
      this.cutOff = 0;
      if(!isloadMore){
        this.searchOption.currentPage = 1;
      }
      this.$http
        .post("/ohotel/posApi/getChecks", this.searchOption)
        .then(res => {
          if (res.data.success) {
            if (isloadMore) {
              // 加载更多
              _.forEach(res.data.result, (el, key) => {
                this.listInfo.push(el);
              });
            } else {
              this.listInfo = res.data.result;
            }
            this.isEmpty = false;

            this.info.masterTotal = res.data.checkCount;
            this.info.masterAmont =
              res.data.totalAmount == null ? 0 : res.data.totalAmount;
            this.loading = false;

            if (
              this.info.masterTotal >= 4 &&
              this.info.masterTotal <=
                this.searchOption.pageSize * this.searchOption.currentPage
            ) {
              this.cutOff = 1;
            }
            // 第一次加载数据
            if (this.info.masterTotal == 0) {
              this.isEmpty = true;
              this.cutOff = 0;
            }
          } else {
            this.isEmpty = true;
            this.info.masterAmont = 0;
            this.info.masterTotal = 0;
            this.listInfo = [];
            this.msg(res.data.message);
          }
        });
    },
    loadMore() {
      if (
        this.info.masterTotal >=
        parseFloat(this.searchOption.pageSize) *
          parseFloat(this.searchOption.currentPage)
      ) {
        this.searchOption.currentPage += 1;
        this.loading = true;
        this.getInfo(true);
      }
    },
    msg(msg) {
      Toast({
        message: msg,
        position: "bottom",
        duration: 3000
      });
    }
  },
  components: {
    datepik,
    operaOrderDetail,
    actionsheet: Actionsheet
  }
};
</script>

<style lang="less">
.optransactSearch {
  .testButton {
    width: 40%;
    height: 0.9867rem;
    text-align: center;
    line-height: 0.9067rem;
    color: #fff;
    font-size: 0.3733rem;
    background: #ff8902;
    border-radius: 0.1867rem;
    display: inline-block;
    margin-top: 0.4667rem;
    margin-left: 0.36rem;
  }
  overflow-y: hidden;
  .dateAll {
    width: 100%;
    height: 2.6rem;
    background: #fff;
    position: fixed;
    z-index: 2000;
    .dataBox {
      margin: 0 auto;
      width: 96%;
      height: 100%;
      .data {
        width: 59%;
        height: 1.0667rem;
        margin-top: 0.1067rem;
        display: inline-block;
        .iconBox {
          border-radius: 2.6667rem;
          width: 0.8533rem;
          height: 0.8533rem;
          display: inline-block;
          text-align: center;
          background: #fff;
          -moz-transform: none;
          -webkit-transform: none;
          -o-transform: none;
          -ms-transform: none;
          transform: none;
          .icon-jiantou,
          .icon-caret-right-copy {
            font-size: 0.32rem;
            line-height: 0.88rem;
            color: #d8d8d8;
            transform: scale(0.7);
          }
        }
        .blakBg {
          background: #f6f8f9 !important;
          border-radius: 2.6667rem;
        }
        .fontBlack {
          color: black !important;
        }
        .dataDtail {
          display: inline-block;
          font-size: 0.3733rem;
          color: #393c41;
          margin: 0.2667rem 0.3733rem 0rem;
        }
      }
      .line {
        width: 0.0267rem;
        text-align: center;
        display: inline-block;
        vertical-align: top;
        margin-top: 0.3733rem;
        height: 0.5867rem;
        background: #cccccc;
      }
      .allBox {
        width: 38%;
        margin-top: 0.48rem;
        height: 1.0667rem;
        display: inline-block;
        color: #393c41;
        font-size: 0.32rem;
        vertical-align: top;
      }
    }
    .searchBox {
      height: 1.4rem;
      margin: 0 auto;
      width: 100%;
      position: fixed;
      background: #f4f5f7;
      top: 2.3rem;
      .search {
        width: 94%;
        margin: 0 auto;
        height: 0.6rem;
        padding-top: 0.3rem;
        .tableNuber {
          width: 21%;
          height: 0.7733rem;
          font-size: 0.3733rem;
          padding-left: 0.2667rem;
          background: #fff;
          border: 0.0267rem solid #cccccc;
          border-radius: 0.1333rem;
          margin-left: 0.1333rem;
          float: left;
        }
        .orderNumer {
          width: 41%;
          height: 0.7733rem;
          font-size: 0.3733rem;
          padding-left: 0.2667rem;
          background: #fff;
          border: 0.0267rem solid #cccccc;
          border-radius: 0.1333rem;
          float: left;
          margin-left: 0.2667rem;
        }
        .searchResult {
          width: 20.5%;
          height: 0.7733rem;
          font-size: 0.3733rem;
          background: #d8af68;
          border-radius: 0.1867rem;
          float: right;
          color: #fff;
          line-height: 0.7467rem;
          margin-right: 0.08rem;
          margin-top: 0.0533rem;
        }
      }
    }
  }
  .scrollBox {
    position: fixed;
    margin-top: 2.6rem;
    width: 100%;
    height: 79%;
    overflow-y: auto;
    padding-bottom: 2rem;
    .orderList {
      background: #ffffff;
      margin-bottom: 0.2667rem;
      width: 96%;
      height: auto;
      padding-bottom: 0.2667rem;
      margin: 0 auto 0.2667rem;
      box-shadow: 0 0 0.2133rem 0rem #dadada;
      border-radius: 0.1333rem;
      position: relative;
      .numt{
        position: absolute;
        bottom: 5px;
        left: 5px;
        color:#eee;
        font-size: 10px;
      }
      .mintui {
        position: absolute;
        top: 30%;
        color:#ccc;
        right: 5%;
        transform: rotate(180deg);
      }
      .blueLind {
        height: 0.3733rem;
        width: 1.1%;
        background: #646f83;
        float: left;
        margin-top: 0.48rem;
      }
      .lineInfo {
        margin-bottom: 0.1067rem;
        color: #999999;
        clear: both;
        .ss{width:4rem;font-size:0.32rem;}
        .titleThird {
          color: #999999;
          letter-spacing: 0rem;
          float: left;
        }
        span {
          font-size: 0.3733rem;
          color: #151515;
          width: 2.5rem;
          float: left;
          white-space: nowrap;
          text-overflow: ellipsis;
          overflow: hidden;
        }
      }
      .returnPay {
    width: 94%;
    height: 0.8rem;
    border-top: 1px solid #eeeeee;
    margin: 0 auto;
    .returnButton {
      margin: 0 auto;
      width: 25%;
      background: #bbb;
      border-radius: 0.1867rem;
      color: #fff;
      font-size:0.28rem;
      line-height: 0.7rem;
      margin-top: 0.15rem;
    }
  }
      .infoL {
        width: 49%;
        height: auto;
        text-align: left;
        display: inline-block;
        padding-top: 0.3733rem;
      }
      .infoR {
        width: 45%;
      }
      .infoBo {
        text-align: left;
        margin-left: 0.3733rem;

        b {
          font-size: 0.32rem;
          color: #4990e2;
          text-decoration: underline;
          margin-left: 0.1333rem;
        }
      }
    }
  }
  .nothing {
    width: 80px;
    height: 88px;
    margin: 150px auto 0 auto;
  }
  .nothingTxt {
    text-align: center;
    font-size: 20px;
    color: #ccd1d5;
    margin-top: 20px;
  }
  .show {
    display: block;
  }
  .hide {
    display: none;
  }
  .mint-popup-right {
    width: 100%;
    height: 100%;
  }
  .infoWarn {
    height: 50px;
    color: #ccc;
  }
}
</style>


