import _ from "lodash";
import router from "../router/";
import { Indicator, MessageBox } from "mint-ui";
import { getKeyUrl } from "../assets/js/md5Data.js";
export default function (axios) {
  axios.interceptors.request.use(
    config => {

      //console.log("config", config);
      let bapp = window.navigator.userAgent.indexOf("Cordova") > 0;
      if (config.url.indexOf("http") != 0) {
        var ssv = localStorage.system;
        if (config.vv) {
          ssv = config.vv;
        }
        if (!ssv) {
          localStorage.setItem("system", 1);
          ssv = "1";
        }

        //任意付
        if (ssv == "2") {
          if (localStorage.JWT_TOKEN) {
            config.headers["Authorization"] = localStorage.JWT_TOKEN;
          }
          var gatewayJson = localStorage.getItem("anygateway");
          var gateway = null;
          if (gatewayJson != null) {
            gateway = JSON.parse(gatewayJson);
            config.url = gateway.url + config.url;
          }
          if (bapp) {
            logData(config.url);
          }
        } else if (ssv == 5) {
          var gatewayJson = localStorage.getItem("simgateway");
          var gateway = null;
          if (gatewayJson != null) {
            gateway = JSON.parse(gatewayJson);
            if (bapp) {
              config.url = gateway.url + config.url;
            }
          }
          let hotelGroupCode = gateway.hotelGroupCode;
          let hotelCode = gateway.hotelCode;
          let cont = {
            appKey: "00000",
            v: "1.0",
            format: "json",
            hotelGroupCode: hotelGroupCode,
            hotelCode: hotelCode
          };
          config.params = {
            ...cont,
            ...config.params
          };
          config.dpost = true;
          if (bapp) {
            logData(config.url + JSON.stringify(config.params));
          }
          // console.log(" config.url", config.url);
        } else if (ssv == 6) {
          var gatewayJson = localStorage.getItem("dcgateway");
          var gateway = null;
          if (gatewayJson != null) {
            gateway = JSON.parse(gatewayJson);

            if (bapp) {
              config.url = gateway.url + config.url;
            } else {
              config.url = `http://${gateway.ip}:${gateway.port}${config.url}`;
            }

            let cont = {
              appKey: "00000",
              v: "2.0",
              format: "json"
            };

            //axios封装get请求，data为假时， Content-Type会被清理掉
            if (config.method == 'get') {
              config.data = { unused: 0 }
              //console.log("gateway:", gateway)
              config.params = {
                ...cont,
                ...config.params
              };
            } else {
              config.data = Object.assign(config.data, cont);
            }
            config.headers["Content-Type"] = "application/json;charset=UTF-8";
            config.headers["rop-hotelcode"] = gateway.userInfo ? gateway.userInfo.hotelCode : "";
            config.headers["rop-hotelgroupCode"] = gateway.userInfo ?
              gateway.userInfo.hotelGroupCode : "";
            config.headers["rop-appcode"] = "M_POS";
            config.headers["rop-workstation"] = gateway.userInfo ?
              gateway.userInfo.workStationCode : "";
            config.headers["rop-usercode"] = gateway.userInfo ? gateway.userInfo.userCode : "";
            config.headers["rop-bizdate"] = gateway.userInfo ? gateway.userInfo.bizDate : "";
            config.headers["rop-shift"] = gateway.userInfo ? (gateway.userInfo.shift ? gateway.userInfo.shift : -1) : -1;

          }


          config.dpost = true;
          if (bapp) {
            logData(config.url + JSON.stringify(config.params));
          }
          //console.log(" config:", JSON.stringify(config));
        }
        //餐饮
        else if (ssv == "1" || ssv == "3") {
          if (config.data != null) {
            var keyarry = [];
            _.forEach(config.data, (v, k) => {
              keyarry.push({
                k: k,
                v: v
              });
            });
            var gatewayJson = localStorage.getItem(
              ssv == "1" ? "gateway" : "simgateway"
            );
            var gateway = null;
            if (gatewayJson != null) {
              gateway = JSON.parse(gatewayJson);
              if (bapp) {
                config.url = gateway.url + config.url;
              }
            }
            if (ssv == "3") {
              if (localStorage.getItem("vc") != null) {
                var objvc = JSON.parse(localStorage.vc);
                keyarry.push({
                  k: "revobj",
                  v: objvc.rvcObjectNum
                });
              } else {
                MessageBox.alert("请先设置餐厅参数");
              }
            }
            // console.log(config.url);
            if (gateway != null) {
              keyarry.push({
                k: "hotelGroupCode",
                v: gateway.hotelGroupCode
              });
              keyarry.push({
                k: "hotelCode",
                v: gateway.hotelCode
              });
            }
            config.url = config.url + "?" + getKeyUrl(keyarry);
          }
        } else if (ssv == "4") {
          var gatewayJson = localStorage.getItem("simgateway");
          var gateway = JSON.parse(gatewayJson);
          var userName = "ADMIN";
          var checkPS = localStorage.getItem("checkPs");
          if (checkPS) {
            userName = JSON.parse(checkPS).user;
          }
          if (bapp) {
            config.url = gateway.url + config.url;
          }
          let user = {
            hotelGroup: {
              id: gateway.hotelGroupId,
              code: gateway.hotelGroupCode
            },
            hotel: {
              id: gateway.hotelId,
              code: gateway.hotelCode
            },
            user: {
              code: userName
            }
          };
          gateway = JSON.parse(gatewayJson);
          if (config.params && config.params.condition) {
            config.params.condition.hotelGroupCode = gateway.hotelGroupCode;
            config.params.condition.hotelCode = gateway.hotelCode;
          }

          config.headers["userDto"] = JSON.stringify(user);
          config.headers["post-version"] = 2;
        }
        if (bapp) {
          try {
            logData(config.url);
          } catch (ex) { }
        }
      }


      config.timeout = config.timeout ? config.timeout : 45000;
      //console.log("config.timeout:", config.timeout);
      if (config.method == "post" || config.dpost) {
        Indicator.open();
      }
      return config;
    },
    error => {
      // Do something with request error
      return Promise.reject(error);
    }
  );
  // Add a response interceptor
  axios.interceptors.response.use(
    function (response) {
      // Do something with response data
      //console.log(response)
      if (response.config.method == "post" || response.config.dpost) {
        Indicator.close();
      }
      if (response.data.result == 50001) {
        MessageBox.alert("本功能需要酒店账号登录");
        router.replace({
          path: "login"
        });
      }
      return response;
    },
    function (error) {
      Indicator.close();
      MessageBox.alert("请求超时，请检查你的网络");
      return Promise.reject(error);
    }
  );
}



