<template>
<div class="settingPay">
   <topNav title="任意付参数设置" url="/anyPay"></topNav>
    <div class="titleS">请选择任意付接口地址</div>
    <div class="settingContent">
         <table>
             <tr>
               <th style="width:12.4%;"></th>
               <th style="width:23.3%">网络</th>
               <th style="width:47%">地址 </th>
               <th style="width: 8.5%;">响应</th>
             </tr>
             <tr v-for="item in address" :key="item.url">
               <td style="border:none">
                   <div class="checkBox">
                       <input type="radio" :value="item.url" name="urlcheck" v-model="vurl" :checked="select != null && select.url == item.url">
                   </div>
               </td>
               <td>{{item.name}}</td>
               <td>{{item.url}}</td>
               <td>{{item.ping}}</td>
             </tr>
         </table>
         <div class="settingAll">
           <div class="allBox">
              <div class="testButton" @click="ping(0)">测试</div>
              <router-link to='addApiPay'>
                   <div class="testButton">新增</div>
              </router-link>
              <div class="testButton" @click="selectUrl">选择</div>
              <div class="testButton" @click="clear">清空接口</div>
            </div>
         </div>
 <div class="botbtns"><div class="testButton" @click="loginOut">退出登录</div></div>
    </div>
   
</div>
</template>
<script>
import { Toast,MessageBox } from 'mint-ui';
import axios from 'axios'
export default {
 data() {
    return {
      address:[],
      select:null,
      vurl:"",
    };
  },
   mounted() {
      var selectJson = localStorage.getItem("anygateway");
      if(selectJson != null){
          this.select =  JSON.parse(selectJson);
          this.vurl = this.select.url;
      }
     var adds = localStorage.getItem("anyaddress");
    //console.log(selectJson);
    // console.log(adds);
     if(adds != null){
        var urls = adds.split('|');
        for(var i=0;i<urls.length;i++){
            var data = JSON.parse(urls[i]);
            data.ping = "";
            this.address.push(data);
        }
        this.ping();
     }
   },
    methods: {
      clear(){
          localStorage.removeItem("anygateway");
           localStorage.removeItem("anyaddress");
          this.address=[];
      },
      loginOut(){
        MessageBox.confirm("确定退出登录？", "")
        .then(action => {
          localStorage.removeItem("JWT_TOKEN");
          this.$router.push("login");
        })
        .catch(action => {});
      },
      ping(i){
        if(i == undefined){
          i = 0;
        }
        var add = this.address;
        if(add.length > i){
          var item =  this.address[i];
          var requestTime = new Date().getTime();
          var instance = axios.create({timeout: 5000});
          instance.get(item.url+"/mobilepmsapi/user/ping").then(res => {
              var responseTime = new Date().getTime();
              var pv = Math.abs(requestTime - responseTime);
              item.ping = pv;
            }).catch(function(error){
               item.ping = "超时";
            })
             this.ping(i+1);
        }
      },
      selectUrl(){
          for(var i=0;i<this.address.length;i++){
                if(this.address[i].url == this.vurl){
                    localStorage.setItem("anygateway", JSON.stringify(this.address[i]));
                    this.msg("选择成功");
                   return;
                }
          }
          this.msg("没有选中节点");
      },
       msg(msg) {
        Toast({
                message: msg,
                position: 'bottom',
                duration: 3000
            });
    }
    }
}
</script>

<style lang='less'>
.settingPay{
  width: 100%;
  margin:0 auto;
  text-align: left;
   .titleS{
     width: 96%;
     margin: 0 auto;
     font-size: 0.32rem;
     display: block;
     height: 1.0667rem;
     color: #666666;
     line-height: 1.0667rem;
   }
   .settingContent{
     box-shadow: 0 0 0.2133rem 0rem #dadada;
    border-radius: 0.1333rem;
      width: 96%;
      margin: 0 auto;
      height: auto;
      background: #FFF;
      table{
        margin: 0 auto;
        font-size: 0.3733rem;
        width: 91%;
        tr{
          th{
            height: 1.36rem;
            border-bottom: 0.0267rem solid #EEEEEE;
          }
        }
        tr{
          td{
            height: 1.0667rem;
            border-bottom:0.0267rem dashed #EEEEEE;
            .checkBox{
              width: 0.4267rem;
              height: 0.4267rem;
              margin:0 auto;
              border-radius: 0.1333rem;
              .icon-right {
                color:#6ED200;
                line-height: 0.4267rem;
              }
            }
          }
        }
      }
      .settingAll{
        width: 100%;
        height: auto;
        .allBox{
          width: 85%;
          margin:0 auto;
          margin-top: 0.5333rem;
          padding-bottom: 0.32rem;
        
        }
      }
   }  
     .testButton{
            width: 44%;
            height: 0.9867rem;
            text-align: center;
            line-height: 0.9067rem;
            color: #FFF;
            font-size: 0.3733rem;
            background: #FF8902;
            border-radius: 0.1867rem;
            display: inline-block;
            margin-bottom: 0.2667rem;
            margin-left: 0.36rem;
          }
          .botbtns{
            text-align:center;margin-top:-0.3rem;padding-bottom:0.8rem;
            .testButton{width:80%}
          }
}
</style>



