<template>
  <div class="anyrefundList_box">
    <topNav title="退款" url='/anypay'></topNav>

    <div class="search">

      <div class="searchBox">
        <!-- <input class="tableNuber" type="text" name="" id="" placeholder="桌号" v-model='searchOption.tableNo'> -->
        <input class="orderNumer" type="text" name="" id="" :placeholder="SYS_DEVICE=='ICBC'?'参考号 / 订单号':'凭证号 / 订单号'" v-model='searchOption.taNo'>
        <div class="searchResult" @click='search'>查询</div>
      </div>

    </div>

      <div class="orderList" v-for='item in listInfo' :key='item.id' >
        <div class="blueLind"></div>
          <div class="infoL">
          <div class="lineInfo">
            <div class="titleThird nomalL">订单号</div>&nbsp;:
            <span>{{item.id}}</span>
          </div>
          <div class="lineInfo">
            <div class="titleThird nomalL">费用代码</div>&nbsp;:
            <span>{{item.feeCode}}</span>
          </div>
          <div class="lineInfo">
            <div class="titleThird nomalL">总金额</div>&nbsp;:
            <span>{{item.totalMoney}}</span>
          </div>
          <div class="lineInfo">
            <div class="titleThird nomalL">支付时间</div>&nbsp;:
            <span>{{item.createDate | fd}}</span>
          </div>
        </div>
        <div class="infoL infoR">
         <div class="lineInfo">
            <div class="titleThird nomalL">{{SYS_DEVICE=="ICBC"?"参考号":"凭证号"}}</div>&nbsp;:
            <span>{{item.taNo | fitOrderNo}}</span>
          </div>
           <div class="lineInfo">
            <div class="titleThird nomalL">费用名称</div>&nbsp;:
            <span>{{item.feeName}}</span>
          </div>
          <div class="lineInfo">
            <div class="titleThird nomalL">支付金额</div>&nbsp;:
            <span>{{item.payMoney}}</span>
          </div>
          <div class="lineInfo">
            <div class="titleThird nomalL">状态</div>&nbsp;:
            <span>{{item.status}}</span>
          </div>
        </div>
      

        <div class="returnPay">
          <div class="returnButton detailButton"  @click='openDetail(item)'>详情</div>
          <div class="returnButton" @click='showAlter(item)'>撤销</div>

          <!--<div class="iconfont icon-you"></div>-->
        </div>
        <!-- <i  class="mintui mintui-back" @click='openDetail(item)'></i> -->


    </div>
    
      <div>
      <img class="nothing" src="../../assets/img/nothing.png" v-if="isEmpty || isFrist">
      <p class="nothingTxt" v-if="isEmpty">什么都没有</p>
      <p class="nothingTxt" v-if="isFrist">请输入查询条件查询退款订单<br>(仅能退营业日当天订单）</p>
      <img class="nothing" src="../../assets/img/chenggong.png" v-if="isSucceed">
      <p class="nothingTxt"  v-if="isSucceed">撤销成功</p>
    </div>
  <mt-popup
    v-model="popupVisible"
    position="right"
    modal='false'>
      <div class="topHeader">
        <header class="mint-header is-fixed" style="background-color:#00487C;">
          <div class="mint-header-button is-left">
            <a class="router-link-exact-active router-link-active" @click='openDetail'>
              <i class="mintui mintui-back"></i>
            </a>
          </div>
          <h1 class="mint-header-title">订单详情</h1>
          <div class="mint-header-button is-right">
          </div>
       </header>
       </div>
  <anypayMasterList ref="masterListC"></anypayMasterList>
    </mt-popup>
  </div>
</template>
<script>
import moment from 'moment'
import anypayMasterList from "../common/anypayMasterList";
import { Toast,MessageBox } from 'mint-ui';
import md5 from 'js-md5';
export default {
  data() {
    return {
      listInfo: [],
      isEmpty:false,
      isFrist:true,
      isSucceed:false,
      popupVisible: false,
      currentItem:{},
      SYS_DEVICE:"",
      searchOption: {
          //createDate:"2018-01-29",
          taNo: "",
          feeCode: "",
          status:"O",
          //currentPage: 1,
          pageSize: 100,
          pageIndex: 1
        }
    };
  },
  mounted() {
    this.SYS_DEVICE = this.$SYS_DEVICE;
  
  },
  methods: {
    notsees() {
      this.popupVisible = false;
    },
    fork(){
          this.showTwo=false;
      },
    showAlter(item){

         this.currentItem= item;
         MessageBox.confirm('是否确定撤销该订单？').then(action => {
            // 点击确定后撤销订单
            this.refundOrder();
        });


         // this.showTwo=! this.showTwo;
    },
    openDetail(item) {

        this.popupVisible=!this.popupVisible;
        this.$refs.masterListC.getMasterList(item.id);

    },
     //调用硬件退款
    refundSucceed(value){
      if(value.transData.resCode=='00'){
        this.checkOutRevoke(value.transData);
    }else{
      this.msg(value.transData.resDesc);
    }

    },
    // 硬件退款
    refundOrder(){
      //流水号
      var traceNo=this.currentItem.taNo;
       var credit=Math.round(this.currentItem.totalMoney * 100);
      var paycodeDesc=this.currentItem.taWay;
      refund(this.refundSucceed,null,traceNo,credit,paycodeDesc,this.currentItem);
    },
    // 撤销ding'dan
    checkOutRevoke(data){
      this.$http.post("/mobilepmsapi/pay/cancelOrder",{id:this.currentItem.id,cancel_money:data.amt,remark:data.traceNo}).then(res => {
        if(res.data.result == 0){
            // 校验用户信息成功，开始执行入账程序
          this.msg("账单撤销成功");
          this.isEmpty=false,
          this.isFrist= false;
          this.isSucceed= true;
          this.listInfo =[];
        }
        else{
            this.msg(res.data.msg);
        }
        });
    },

    checkUser(){
       this.refundOrder()
      },
    search() {
      
      if(this.searchOption.taNo == ''){
        this.msg('查询内容不能为空');
      }else{
        this.getInfo();
      }
    },
    getInfo() {
      
      this.$http.post("/mobilepmsapi/pay/findPageList", this.searchOption).then(res => {
        if (res.data.result == 0) {
        this.listInfo = res.data.retVal.list
          this.isEmpty = false;
          this.isFrist = false;
          this.isSucceed= false;
          if (this.listInfo.length == 0) {
            this.isEmpty = true;
          }
        }
        else {
          this.msg(res.data.msg);
        }
      })
    },
    msg(msg) {
      Toast({
        message: msg,
        position: 'bottom',
        duration: 3000
      });
    }
  },
   filters: {
      tofixed(v) {
        return parseFloat(v).toFixed(2);
      },
      fitOrderNo(v){
         return (v != null && v.indexOf("_") > 0) ?  v.split('_')[0]:v;
      },
      fd(value) {
        if (value == null || value == "") return "";
        return value.substr(11);
      }
    },
   components: {
    anypayMasterList
   }
}
</script>
<style lang='less'>
.anyrefundList_box {
  .refundTop {
    width: 100%;
    height: 1.28rem;
    background: #fff;
    .data {
      width: 59%;
      height: 1.0667rem;
      margin-top: 0.1067rem;
      display: inline-block; // background: blue;
      .iconBox {
        border-radius: 2.6667rem;
        width: 0.8533rem;
        height: 0.8533rem;
        display: inline-block;
        text-align: center;
        background: #FFF;
        -moz-transform: none;
        -webkit-transform: none;
        -o-transform: none;
        -ms-transform: none;
        transform: none;
        .icon-jiantou,
        .icon-caret-right-copy {
          font-size: 0.32rem;
          line-height: 0.88rem;
          color: #D8D8D8;
          transform: scale(0.7);
          margin-left: 0.1067rem;
        }
        .icon-jiantou {
          margin-left: 0rem;
        }
      }
      .blakBg {
        background: #F6F8F9!important;

        border-radius: 2.6667rem;
      }
      .fontBlack {
        color: black!important;
      }
      .dataDtail {
        display: inline-block;
        font-size: 0.3733rem;
        color: #393C41;
        margin: 0.2667rem 0.3733rem 0rem;
      }
    }
  }
  .search {
    width: 100%;
    border-radius: 0.2667rem;
    background: #F4F5F7;
    margin: 0 auto 0 auto;
    height: 1.6rem;
    .searchBox{
      height: 0.8rem;
      margin: 0 auto;
      width: 96%;
      padding-top: 0.4rem;
      .tableNuber {
        width: 21%;
        height: 0.7733rem;
        font-size: 0.3733rem;
        padding-left: 0.2667rem;
        background: #FFF;
        border: 0.0267rem solid #CCCCCC;
        border-radius: 0.1333rem;
        margin-left: 0.1333rem;
        float: left;
      }
      .orderNumer {
        width: 68%;
        height: 0.7733rem;
        font-size: 0.3733rem;
        padding-left: 0.2667rem;
        background: #FFF;
        border: 0.0267rem solid #CCCCCC;
        border-radius: 0.1333rem;
        float: left;
        margin-left: 0.2667rem;
      }
      .searchResult {
        width: 20.5%;
        height: 0.7733rem;
        font-size: 0.3733rem;
        background: #D8AF68;
        border-radius: 0.1867rem;
        float: right;
        color: #FFF;
        line-height: 0.7467rem;
        margin-right: 0.08rem;
        margin-top: 0.0533rem;
      }
    }
  }


  .orderList {
    background: #FFFFFF;
    margin-bottom: 0.2667rem;
    width: 96%;
    height: auto;
    margin: 0 auto 0.2667rem;
    box-shadow: 0 0 0.2133rem 0rem #dadada;
    border-radius: 0.1333rem;
    position: relative;
    .mintui{
        position: absolute;
        top: 40%;
        right: 5%;
        transform:rotate(180deg)
      }
    .blueLind {
      height: 0.3733rem;
      width: 0.9%;
      background: #646f83;
      display: inline-block;
      float: left;
      margin-top: 0.48rem;
    }
    .infoL {
      width: 53%;
      height: auto;
      text-align: left;
      display: inline-block;
      padding-top: 0.3733rem;
      text-indent: 0.1533rem;
      .lineInfo {
        margin-bottom: 0.1067rem;
        color: #999999;
        .titleThird {
          display: inline-block;
          color: #999999;
          letter-spacing: 0.2667rem;
        }
        .titileS {
          display: inline-block;
          color: #999999;
        }
        .nomalL {
          letter-spacing: 0rem;
        }

        span {
          margin-left: 0.1333rem;
          font-size: 0.3733rem;
          color: #151515;
        }
      }
    }
    .infoR {
      width: 45%;
    }
    .infoBo {
      text-align: left;
      margin-left: 0.3733rem;
      margin-bottom: 0.2133rem;
      .titleThird {
        display: inline-block;
        color: #999999;
        letter-spacing: 0.2667rem;
      }
      .titileS {
        display: inline-block;
        color: #999999;
      }
      .nomalL {
        letter-spacing: 0rem;
      }
      span {
        margin-left: 0.1333rem;
        font-size: 0.3733rem;
        color: #151515;
      }
      b {
        font-size: 0.32rem;
        color: #4990E2;
        text-decoration: underline;
        margin-left: 0.1333rem;
      }
    }
    .returnPay {
      width: 94%;
      height: 1.2rem;
      border-top: 0.0267rem solid #EEEEEE;
      margin: 0 auto;
      .returnButton {
        float: right;
        width: 33%;
        height: 0.8267rem;
        background: #FF8902;
        border-radius: 0.1867rem;
        color: #FFF;
        font-size: 0.3733rem;
        line-height: 0.7467rem;
        margin-top: 0.2133rem;
      }
      .detailButton{float: left;
    background: #2d8cf0;
    }
      .icon-you {
        float: right;
        font-size: 0.3733rem;
        line-height: 1.1733rem;
        margin-right: 0.08rem;
      }
    }
  }
}

.nothing{width:80px; height:88px; margin: 150px  auto 0 auto;}
.nothingTxt{text-align:center;font-size:20px; color:#CCD1D5;margin-top:20px;}
.show{display:block};
.hide{display:none;}
.restaurantDetail_box {
    overflow: auto;
    height: 100%;
  .DetailTop {
    width: 90%;
    height: 1.2533rem;
    margin: 0 auto;
    font-size: 0.32rem;
    color: #333333;
    .topL {
      float: left;
      line-height: 1.2267rem;
    }
    .topR {
      float: right;
      line-height: 1.2267rem;
      span {
        display: inline-block;
      }
      .circleBox {
        width: 0.5333rem;
        height: 0.5333rem;
        background: #646F83;
        border-radius: 1.36rem;
        color: #FFF;
        display: inline-block;
        vertical-align: top;
        margin-top: 0.3733rem;
        line-height: 0.5867rem;
      }
    }
  }
  .mint-popup-right {
  width: 100%;
  height: 100%;
}
}
</style>



