<template>
  <div class="checkstand">
    <div class="topHeader clear">
      <header class="mint-header is-fixed" style="background-color: rgb(57, 60, 65);">
        <div class="mint-header-button is-left">
          <a href="#/anypay" class="router-link-active">
            <i class="mintui mintui-back"></i>
          </a>
        </div>
        <h1 class="mint-header-title">收银台</h1>
        <div class="mint-header-button is-right">
            <mt-button icon="more" @click="sheetVisible=true" slot="right"></mt-button>
            <actionsheet :actions="actions" v-model="sheetVisible"></actionsheet>
        </div>
      </header>
    </div>
    <div class="checkstand-main">
        <p class="des">结账金额</p>
        <div class="screen clear">
            <span class="category " v-bind:class="{ active: listVisible }" @click='toggleList'>
              {{paySubListSelect.descript}}
              <i class="iconfont icon-ADown"></i>
            </span>

            <div class="price-wrapper">
              <input class="price" :value="price" />
            </div>          
        </div>
        <div class="category-list clear" v-if="listVisible">
            <div class="category-tabs">
                <ul>
                  <li v-for="(item ,index) in payList" :name =" item.code" v-bind:class="{ active: (item.code== payListActive) }" @click="changeFirstPayCode(index)"> <span>{{item.descript}}</span></li>
                </ul>
            </div>
            <div class="category-content">
                <ul>
                  <li  v-for="(item ,index) in paySubList" :name =" item.code" v-bind:class="{ active: (item.code== paySubListActive) }" @click="changeSubPayCode(index)"><span>{{item.descript}} </span></li>
                 
                </ul>
            </div>
        </div>

        <div class="keyboard" v-else>
            <div class="column">
              <ul class="clear">
                <li @click ="keyDown('number','1')"><span>1</span></li>
                <li @click ="keyDown('number','2')"><span>2</span></li>
                <li @click ="keyDown('number','3')"><span>3</span></li>
                <li @click ="keyDown('action','delete')"><span class="action">退格</span></li>
              </ul>
            </div>
            <div class="column">
              <ul class="clear">
                <li @click ="keyDown('number','4')"><span>4</span></li>
                <li @click ="keyDown('number','5')"><span>5</span></li>
                <li @click ="keyDown('number','6')"><span>6</span></li>
                <li @click ="keyDown('action','reset')"><span class="action">清除</span></li>
              </ul>
            </div>
            <div class=" column2 clear">
              <div class="column2-left">
                <ul class="clear">
                <li @click ="keyDown('number','7')"><span>7</span></li>
                <li @click ="keyDown('number','8')"><span>8</span></li>
                <li @click ="keyDown('number','9')"><span>9</span></li>
                
              </ul>
              <ul class="clear">
                <li @click ="keyDown('number','00')"><span class="doubale-zero">00</span></li>
                <li @click ="keyDown('number','0')"><span >0</span></li>
                <li @click ="keyDown('number','.')"><span >.</span></li>
              
              </ul>
              </div>
              <div class="column2-right" @click ="keyDown('action','submmit')">
                  <p>
                    确定
                  </p>
                  <span v-if="payMethod == 1">(银行卡)</span>
                  <span class="scan" v-else>(支付宝/微信)</span>
              </div>
            </div>
        </div>
    </div>
  </div>
</template>

<script>
import { Toast, Actionsheet, Popup,MessageBox } from "mint-ui";
import Grid from "../common/grid";

export default {
  data() {
    return {
      listVisible: false,
      payList: [],
      payListActive: "",
      payCode: "",
      paySubList: [],
      paySubListActive: "",
      paySubListSelect: {},
      payMethod: !!localStorage.payMethod ? localStorage.payMethod : 2, // 1 银行卡 2 微信支付宝
      orderId: "",
      sheetVisible: false,
      price: "0",
      SYS_DEVICE:"",
      actions: []
    };
  },
  mounted() {
   this.SYS_DEVICE = this.$SYS_DEVICE;
    this.init();
    var lPaySubListActive = localStorage.paySubListActive;
    if(lPaySubListActive!=undefined){
      this.payListActive = localStorage.payListActive;
      this.paySubList = JSON.parse(localStorage.paySubList);
      this.paySubListActive = localStorage.paySubListActive;
      this.paySubListSelect =JSON.parse(localStorage.paySubListSelect);
    }
    this.actions = [];
    if(SYS_DEVICE != "NG"){
      this.actions.push( { name: "设置银行卡支付", method: this.setUnionPay });
      this.actions.push( { name: "设置支付宝/微信支付", method: this.setScanPay});
    }
     if(SYS_DEVICE == "PUN"){
        this.actions.push( {name: "最后一笔重入账", method: this.lastReIn });
     }
  },
  methods: {
    msg(msg) {
      Toast({
        message: msg,
        position: "bottom",
        duration: 3000
      });
    },
    lastReIn(){
      reAccount(this.successUnionPay, null, "000000");
    },
    toggleList() {
      this.listVisible = !this.listVisible;
    },
    getCategoryList() {
      this.$http.post("/mobilepmsapi/pay/getPayAccount").then(res => {
        if (res.data.result == 0) {
          var result = res.data.retVal;
          this.payList = result.pay;
          this.payCode = result.payCode;
          // 默认选中列表中的第一个内容
          var lPaySubListActive = localStorage.paySubListActive;
          if(lPaySubListActive==undefined){
            if (JSON.stringify(this.payList[0]) != "{}") {
                this.paySubList = result.pay[0].list;
                this.payListActive = result.pay[0].code;

                if (JSON.stringify(this.payList[0].list) != "{}") {
                  this.paySubListActive = this.payList[0].list[0].code;
                  this.paySubListSelect = this.payList[0].list[0];
                }
              }
          } 
        } else {
          this.msg(res.data.msg);
        }
      });
    },
    changeFirstPayCode(index) {
      this.paySubList = this.payList[index].list;
      this.payListActive = this.payList[index].code;
      localStorage.setItem('payListActive', this.payListActive)
      localStorage.setItem('paySubList',JSON.stringify(this.paySubList))
    },
    changeSubPayCode(index) {
      this.paySubListActive = this.paySubList[index].code;
      this.paySubListSelect = this.paySubList[index];
      //将选项中的内容放到缓存里面
      localStorage.setItem('paySubListActive', this.paySubListActive)
      localStorage.setItem('paySubListSelect', JSON.stringify(this.paySubListSelect))
      this.listVisible = false;
    },
    setScanPay() {
      //this.
      this.payMethod = 2;
      localStorage.setItem("payMethod", this.payMethod);
    },
    setUnionPay() {
      this.payMethod = 1;
      localStorage.setItem("payMethod", this.payMethod);
    },
    successUnionPay(value) {
      if (value.transData.resCode == "00") {
        //提交订单
        // 支付成功后
        this.paySuccess(value.transData);
      } else {
        //failed 支付失败，弹出对应的支付失败的错误码
        this.msg(value.transData.resDesc);
      }
    },
    // 调用硬件的UnionPay
    checkUnionPay(money) {
      unionPay(this.successUnionPay, null, money, this.orderId);
    },
    //sm支付
    payCard(money) {
      unionScan(this.successUnionPay, null, money, this.orderId);
    },
    payOrder() {
      var money = Number(this.price);
      if (money <= 0 || money > 100000) {
        this.msg("支付金额不正确");
        return;
      }
      if (this.payMethod == 1) {
        this.checkUnionPay(Math.round(money * 100));
      } else {
        this.payCard(Math.round(money * 100));
      }
    },
    initOrder() {
      var url = "/mobilepmsapi/pay/initOrder";
      var parameter = {
        accont: this.paySubListSelect.ext,
        feeCode: this.paySubListSelect.code,
        feeName: this.paySubListSelect.descript,
        totalMoney: this.price
      };
      this.$http.post(url, parameter).then(res => {
        if (res.data.result == 0) {
          this.orderId = res.data.retVal;
          //调用支付接口
          this.payOrder();
        } else {
          this.msg(res.data.msg);
        }
      });
    },
    ///mobilepmsapi/pay/paySuccess
    paySuccess(transData) {
      if (transData.eSignJpeg) {
        transData.eSignJpeg = null;
      }
      var parameter = {
        id: this.orderId,
        payCode: this.payCode,
        payMoney: Number(transData.amt),
        taBody: JSON.stringify(transData),
        taNo: transData.traceNo,
        taWay:getPayWay(transData),
        taOrderNo: transData.refNo
      };
      this.$http.post("/mobilepmsapi/pay/paySuccess", parameter).then(res => {
        console.log(res.data);
        if (res.data.result == 0) {
          //支付成功后相关的提示
          MessageBox.alert("订单已支付成功！", "提示");
          //this.msg("支付成功");
          this.price = 0;
        } else {
          this.msg(res.data.msg);
        }
      });
    },
    keyDown(type, value) {
      // 初次的情况
      if (this.price == "0") {
        if (type == "number") {
          if (value == ".") {
            this.price = this.price + value;
          } else if (value == "00") {
            this.price = "0";
          } else {
            this.price = value;
          }
        } else {
          if (value == "submmit") {
            this.msg("请输入支付金额");
          }
        }
      } else {
        if (type == "number") {
          var strv = this.price + "";
          var dIndex = strv.indexOf(".");
          if (dIndex >= 0) {
            if (value == ".") {
              return;
            }
            if (strv.length - dIndex > 2) {
              return;
            }
          }
          if (Number(this.price + value) > 100000) {
            this.msg("超过最多允许金额");
            return;
          }
          this.price = this.price + value;
        } else {
          if (value == "delete") {
            var vLength = this.price.length;
            if (vLength == 1) {
              this.price = "0";
            } else {
              this.price = this.price.substring(0, vLength - 1);
            }
          }
          if (value == "reset") {
            this.price = "0";
          }
          if (value == "submmit") {
            this.initOrder();
          }
        }
      }
    },
    init() {
      this.getCategoryList();
    }
  },
  components: {
    actionsheet: Actionsheet
  }
};
</script>
<style lang="less">
html,
body,
#app {
  height: 100%;
  position: relative;
}
.clear:after {
  content: "";
  display: block;
  clear: both;
  height: 0;
  overflow: hidden;
  visibility: hidden;
}
.clear {
  zoom: 1;
}
.checkstand {
  height: 100%;
  position: relative;

  .topHeader {
    height: 40px;
    .mintui-back{font-size: 0.64rem;}
  }
  .topHeader .mint-header {
    color: #fff;
    font-size: 16px;
    z-index: 100;
    background: #00487c !important;
  }
  .checkstand-main {
    position: absolute;
    height: auto;
    top: 40px;
    bottom: 0;
    left: 0;
    right: 0;
    padding: 5px 8px;
    .des {
      text-align: left;
      color: #666666;
      font-size: 12px;
      line-height: 20px;
      height: 20px;
      margin: 0 0 3px 6px;
    }
    .screen {
      background: #fff;
      width: calc(100%);
      border-radius: 5px;
      position: relative;
      -moz-box-shadow: 0px 0px 3px #999;
      -webkit-box-shadow: 0px 0px 1px #999;
      box-shadow: 0px 0px 3px #999;
      height: 2.1rem;
      .category {
        display: inline-block;
        float: left;
        font-size: 14px;
        line-height: 1.6rem;
        height: 1.6rem;
        margin: 0 0 0 15px;
        color: #999;
        position: absolute;
        top: 0.2rem;
        left: 0.3rem;
        z-index: 10;
        i {
          padding: 2px 0 0 0;
          font-size: 12px;
        }
      }
      .active {
        color: #d8af68;
      }
      .price-wrapper {
        background: #f7f7f7;
        border: 1px solid #eeeeee;
        border-radius: 5px;
        height: 1.4rem;
        position: absolute;
        top: 0.3rem;
        left: 3%;
        right: 0.2rem;
        width: 94%;
      }
      .price {
        display: inline-block;
        float: right;
        font-size: 26px;
        line-height: 1.4rem;
        height: 1.4rem;
        margin: 0 10px 0 0;
        color: #cc7837;
        font-weight: bold;
        text-align: right;
        border: none;
        background: #f7f7f7;
        width: 90%;
      }
    }
    .price:focus {
      outline: none;
    }
    .keyboard {
      position: absolute;
      height: auto;
      top: 3.2rem;
      bottom: 0;
      left: 0;
      right: 0;
      padding: 0 0 0 0;
      .column {
        height: 25%;
        background: #fff;
        color: #999;
      }
      .column ul:before {
        content: " ";
        position: absolute;
        left: 0;
        top: 0;
        right: 0;
        height: 1px;
        border-top: 1px solid #d9d9d9;
        color: #d9d9d9;
        -webkit-transform-origin: 0 0;
        transform-origin: 0 0;
        -webkit-transform: scaleY(0.5);
        transform: scaleY(0.5);
      }
      .column ul {
        height: 100%;
        position: relative;
      }
      .column ul li {
        float: left;
        display: inline-block;
        width: 25%;
        height: 100%;
        position: relative;
        text-align: center;
      }
      .column ul li:before {
        content: " ";
        position: absolute;
        right: 0;
        top: 0;
        width: 1px;
        bottom: 0;
        border-right: 1px solid #d9d9d9;
        color: #d9d9d9;
        -webkit-transform-origin: 100% 0;
        transform-origin: 100% 0;
        -webkit-transform: scaleX(0.5);
        transform: scaleX(0.5);
      }
      .column ul li span {
        font-size: 32px;
        position: absolute;
        top: 50%;
        left: 50%;
        margin: -16px 0 0 -10px;
        line-height: 32px;
      }
      .column ul li span.action {
        font-size: 18px;
        font-weight: normal;
        margin: -9px 0 0 -18px;
      }

      .column2 {
        height: 50%;
        background: #fff;
        color: #999;
        .column2-left {
          display: inline-block;
          float: left;
          width: 75%;
          height: 100%;
          position: relative;
        }
        .column2-left ul {
          height: 50%;
          position: relative;
        }
        .column2-left ul:before {
          content: " ";
          position: absolute;
          left: 0;
          top: 0;
          right: 0;
          height: 1px;
          border-top: 1px solid #d9d9d9;
          color: #d9d9d9;
          -webkit-transform-origin: 0 0;
          transform-origin: 0 0;
          -webkit-transform: scaleY(0.5);
          transform: scaleY(0.5);
        }

        .column2-left ul li {
          float: left;
          display: inline-block;
          width: 33.33333333%;
          height: 100%;
          position: relative;
          text-align: center;
        }
        .column2-left ul li:before {
          content: " ";
          position: absolute;
          right: 0;
          top: 0;
          width: 1px;
          bottom: 0;
          border-right: 1px solid #d9d9d9;
          color: #d9d9d9;
          -webkit-transform-origin: 100% 0;
          transform-origin: 100% 0;
          -webkit-transform: scaleX(0.5);
          transform: scaleX(0.5);
        }
        .column2-left ul li span {
          font-size: 32px;
          position: absolute;
          top: 50%;
          left: 50%;
          margin: -16px 0 0 -10px;
          line-height: 32px;
        }
        .column2-left ul li span.doubale-zero {
          margin: -16px 0 0 -16px;
        }
        .column2-right {
          display: inline-block;
          float: right;
          width: 25%;
          background: #e3e3e3;
          height: 100%;
          position: relative;
        }
        .column2-right p {
          font-size: 18px;
          position: absolute;
          top: 50%;
          left: 50%;
          margin: -12px 0 0 -18px;
          font-weight: normal;
        }
        .column2-right span {
          font-size: 10px;
          position: absolute;
          top: 50%;
          left: 50%;
          margin: 14px 0 0 -18px;
        }
        .column2-right span.scan {
          margin: 14px 0 0 -30px;
        }
      }
    }
    .category-list {
      position: absolute;
      height: auto;
      top: 3.2rem;
      bottom: 0;
      left: 0;
      right: 0;
      margin: 0 8px 10px 8px;
      padding: 13px 0;
      background: #ffffff;
      box-shadow: 0 0 4px 0 rgba(0, 0, 0, 0.1);
      border-radius: 5px;
      .category-tabs {
        width: 33.33%;
        float: left;
        display: inline-block;
        height: 100%;
        border-right: 1px solid #eeeeee;
      }
      .category-tabs ul {
        height: 100%;
        overflow: auto;
      }
      .category-tabs ul li {
        height: 14.28%;
        position: relative;
        vertical-align: middle;
      }
      .category-tabs ul li.active {
        background: #d8af68;
        color: #fff;
      }
      .category-tabs ul li.active span {
        color: #fff;
      }
      .category-tabs ul li span {
        display: inline-block;
        width: 100%;
        height: 100%;
        font-size: 14px;
        color: #999999;
        line-height: 67px;
        vertical-align: middle;
      }
      .category-content {
        width: 66%;
        float: right;
        display: inline-block;
        height: 100%;
      }
      .category-content ul {
        height: 100%;
        padding: 0 30px;
        overflow: auto;
      }
      .category-content ul li {
        height: 14.28%;
        position: relative;
        vertical-align: middle;
      }
      .category-content ul li.active span {
        color: #d8af68;
      }

      .category-content ul li:before {
        content: " ";
        position: absolute;
        left: 0;
        bottom: 0;
        right: 0;
        height: 1px;
        border-bottom: 1px solid #d9d9d9;
        color: #eeeeee;
        -webkit-transform-origin: 0 0;
        transform-origin: 0 0;
        -webkit-transform: scaleY(0.5);
        transform: scaleY(0.5);
      }
      .category-content ul li span {
        font-size: 14px;
        color: #000000;
        line-height: 67px;
      }
    }
  }
}
</style>


