<template>
  <div class="sinfo">
      <topNav title="硬件信息" url="/"></topNav>
            <ul class="linfo">
              <li>
               <span>设备厂商：</span>{{info.Vendor}}
              </li>
              <li>
               <span>硬件序列号：</span>{{info.SN}}
              </li>
               <li>
               <span>设备机型：</span>{{info.TermType}}
              </li>
               <li>
               <span>OS版本：</span>{{info.OSVer}}
              </li>
               <li>
               <span>智能桌面版本：</span>{{info.SoftwareVer}}
              </li>
               <li>
               <span>网格类型：</span>{{info.NetworkState == 1 ? "wifi":"移动"}}
              </li>
               <li v-if="info.Sim">
               <span>Sim imei：</span>{{info.Sim.imei}}
              </li>
              <li v-if="info.Sim">
               <span>Sim imsi：</span>{{info.Sim.imsi}}
              </li>
               <!-- <li>
               <span>经度：</span>{{info.Location.Longitude}}
              </li>
               <li>
               <span>纬度：</span>{{info.Location.Latitude}}
              </li>
               <li>
               <span>城市码：</span>{{info.Location.CityCode}}
              </li>
               <li>
               <span>城市名称：</span>{{info.Location.City}}
              </li>
               <li>
               <span>地址：</span>{{info.Location.Address}}
              </li> -->
            </ul>
  </div>
</template>

<script>
export default {
  data() {
    return {
        info:{}
    };
  },
  mounted() {
    try{
     getSysInfo(this.sysBack);
    }
    catch(ex){
      var that = this;
       setTimeout(() => {
         getSysInfo(that.sysBack);
       }, 3000);
    }
  },
  methods: {
    sysBack(v){
      console.log(v);
      this.info = JSON.parse(v);
    }
  }
};
</script>

<style lang="less">
.sinfo {
  .linfo{
    background: #FFFFFF;
    margin-bottom: 0.2667rem;
    width: 96%;
    height: auto;
    margin: 0 auto 0.2667rem;
    box-shadow: 0 0 0.2133rem 0rem #dadada;
    overflow: hidden;
    border-radius: 0.1333rem;
    margin-top: 0.4rem;
    padding: 0.2rem 0;
    li{min-height:0.6rem;float:left;width:100%;text-align: left;margin:0.1rem 0.3rem;
      font-size: 0.4rem;
     }
  }
}
</style>



