<template>
  <div class="index">
    <div class="topHeader">
      <home system="1"></home>
    </div>
    <div class="index_box">
      <div class="topLogo">
        <div class="logofont">{{hotelName}}</div>
      </div>
      <ul class="vitem">
        <li v-for="(item,index) in icons" :key="index">
          <router-link :to="item.link">
            <div class="orderPay" :style="{background:item.c}">
              <div class="iconfont" :class="item.icon"></div>
              <div class="titleT">{{item.title}}</div>
            </div>
          </router-link>
        </li>
      </ul>
    </div>
  </div>
</template>

<script>
import { Toast, Indicator } from "mint-ui";
import home from "../common/home";
export default {
  data() {
    return {
      hotelName: "",
      SYS_DEVICE: "",
      icons: [
        {
          c: "#95bc56",
          title: "订单支付",
          link: "orderPayDetail",
          sub: "银行卡／微信／支付宝",
          icon: "icon-shuaqia"
        },
        {
          c: "#EE9526",
          title: "交易退款",
          link: "refundList",
          sub: "",
          icon: "icon-tui"
        },
        {
          c: "#FBC303",
          title: "交易查询",
          link: "transactSearch",
          sub: "",
          icon: "icon-dianzixianjinyuechaxun"
        },
        {
          c: "#15B7C2",
          title: "参数设置",
          link: "settingApiContentCloudDining",
          sub: "",
          icon: "icon-shezhi"
        },
        {
          c: "#15B7C2",
          title: "app2.0",
          link: "/mobileOrdering/login",
          sub: "",
          icon: "icon-shezhi"
        }
      ]
    };
  },
  mounted() {
    Indicator.close();
    this.SYS_DEVICE = this.$SYS_DEVICE;
    if (!localStorage.system || localStorage.system == "1") {
      var gatewayJson = localStorage.getItem("gateway");
      if (gatewayJson != null) {
        var gateway = JSON.parse(gatewayJson);
        this.hotelName = gateway.hotelName;
      } else {
        this.msg("未设置参数，请点设置进行设置");
      }
    }
    if (localStorage.cloudParams) {
      var cp = JSON.parse(localStorage.cloudParams);
      if (cp.isChong == "T") {
        this.icons.splice(1, 0, {
          c: "#1795D0",
          title: "会员充值",
          link: "cardInMony",
          sub: "",
          icon: "icon-chongzhi"
        });
      }
      if (cp.isCopon == "T") {
        this.icons.splice(1, 0, {
          c: "#E96565",
          title: "卡券核销",
          link: "CouponUse",
          sub: "",
          icon: "icon-qiaquan"
        });
      }
    }
  },
  methods: {
    msg(msg) {
      Toast({
        message: msg,
        position: "bottom",
        duration: 3000
      });
    },
    test() {
      handWrite(this.success, this.error, "");
    },
    success(value) {
      console.log(value);
      alert(value);
    },
    error(value) {
      alert("error" + value);
    }
  },
  components: {
    home
  }
};
</script>

<style lang="less">
.index {
  width: 100%; // border-top:0.0267rem solid #00487C;
  overflow: auto;
  height: 100vh;
  background: #003963;

  .index_box {
    width: 96%;
    height: auto;
    margin: 0 auto;

    .topLogo {
      margin-top: 0.2933rem;
      width: 100%;
      height: 2.6933rem;
      background: #00213f;
      border-radius: 0.1333rem;
      background-color: rgba(0, 0, 0, 0.3);
      text-align: center;

      .logofont {
        font-size: 0.8rem;
        color: #ffffff;
        line-height: 2.6667rem;
      }
    }
    .vitem {
      margin-left: -2%;

      li {
        float: left;
        width: 48%;
        margin-left: 2%;
        .orderPay {
          margin-top: 0.2rem;
          width: 100%;
          height: 3.7rem;
          border-radius: 0.1333rem;
          .iconfont {
            font-size: 1.2rem;
            color: #ffffff;
            display: inline-block;
            width: 100%;
            margin-top: 0.4533rem;
          }

          .titleT {
            width: 100%;
            font-size: 0.55rem;
            color: #ffffff;
            margin-top: 0.2rem;
            float: left;
          }
        }
      }
    }
  }
}
</style>


