<template>
  <div class="topHome">
    <mt-header :title="title" fixed>
      <mt-button icon="more" @click="sheetVisible=true" slot="right"></mt-button>
    </mt-header>
    <actionsheet :actions="actions" v-model="sheetVisible"></actionsheet>
  </div>
</template>

<script>
import { Actionsheet, MessageBox } from "mint-ui";
export default {
  props: {
    system: {
      default: 1
    }
  },
  data() {
    return {
      title: "",
      sheetVisible: false,
      SYS_DEVICE: "",
      actions: []
    };
  },
  mounted() {
    this.SYS_DEVICE = this.$SYS_DEVICE;
    if (localStorage.system) {
      if (localStorage.system != this.sysInfo) {
        this.goSystem();
      }
    } else {
      localStorage.setItem("system", 1);
    }
    if (localStorage.system == 3) {
      this.actions = [
        {
          name: "云餐饮",
          method: this.pos
        }
      ];
      if (this.SYS_DEVICE == "NG") {
        this.actions.push({
          name: "早餐核销",
          method: this.Breakfast
        });
           this.actions.push({
              name: "移动点菜",
              method: this.taikangPos
         });
      }
      this.title =
        this.SYS_DEVICE == "PUN" ? "银商优住.Simphony" : "绿云MiPOS - Simphony";
    } else if (localStorage.system == 4) {
      this.title = "早餐核销";
      this.actions = [
        {
          name: "Simphony",
          method: this.operapay
        }
      ];
    } else if(localStorage.system != 1) {
      this.actions = [
        {
          name: this.SYS_DEVICE == "PUN" ? "银商优住.餐饮" : "云餐饮",
          method: this.pos
        },
        // { name: "任意付", method: this.anyPay },
        {
          name: "Simphony",
          method: this.operapay
        }
      ];
    }
    this.actions.push({
      name: "设备信息",
      method: this.sysInfo
    });
    this.actions.push({
      name: "版本信息",
      method: this.version
    });
    this.actions.push({
      name: "系统管理",
      method: this.clearData
    });

    if (localStorage.system == 1) {
      this.actions.push(
        {
          name: "退出系统",
          method: this.signOut
        }
      );
      this.title =
        this.SYS_DEVICE == "PUN" ? "银商优住.餐饮" : "绿云MiPOS - 云餐饮";
    } 
  },
  methods: {
    pos() {
      MessageBox.confirm("是否切换云餐饮", "")
        .then(action => {
          localStorage.setItem("system", 1);
          this.goSystem();
        })
        .catch(action => {});
    },
    clearData() {
      this.$router.push("/sysadmin");
    },
    taikangPos() {
      MessageBox.confirm("是否切换到移动点菜", "")
        .then(action => {
          localStorage.setItem("system", 5);
          this.$router.push("/foodLogin");
        })
        .catch(action => {});
    },
    anyPay() {
      MessageBox.confirm("是否切换任意付", "")
        .then(action => {
          localStorage.setItem("system", 2);
          this.goSystem();
        })
        .catch(action => {});
    },
    operapay() {
      MessageBox.confirm("是否切换Simphony", "")
        .then(action => {
          localStorage.setItem("system", 3);
          this.goSystem();
        })
        .catch(action => {});
    },
    signOut() {
      MessageBox.confirm("确定退出？", "")
        .then(action => {
          localStorage.setItem("system", 1);
          localStorage.isLogin = "false";
          let LoginOption = JSON.parse(localStorage.LoginOption);
          LoginOption.password = "";
          localStorage.LoginOption = JSON.stringify(LoginOption);
          this.goSystem();
        })
        .catch(action => {});
    },
    Breakfast() {
      MessageBox.confirm("是否切换早餐核销", "")
        .then(action => {
          localStorage.setItem("system", 4);
          this.goSystem();
        })
        .catch(action => {});
    },
    goSystem() {
      var s = localStorage.system;
      if (s == "1") {
        if (localStorage.isLogin == "true") {
          this.$router.push("/");
        } else {
          this.$router.push("/loginCloudDining");
        }
      } else if (s == "2") {
        this.isLoginCloudDining();
        this.$router.push("/anypay");
      } else if (s == "3") {
        this.isLoginCloudDining();
        this.$router.push("/operapay");
      } else if (s == "4") {
        this.isLoginCloudDining();
        this.$router.push("/checkIndex");
      } else if (s == "5") {
        this.$router.push("/foodLogin");
      }
    },
    sysInfo() {
      this.$router.push("/sysInfo");
    },
    version() {
      this.$router.push("/version");
    },
    isLoginCloudDining() {
      localStorage.isLogin = "false";
    }
  },
  components: {
    actionsheet: Actionsheet
  }
};
</script>

<style lang="less" scoped>
.topHome {
  height: 1.1rem;
  background: #00487c;

  .mint-header {
    color: #fff;
    font-size: 16px;
    z-index: 100;
    background: #00487c !important;
  }

  .mintui-back {
    /*font-size: 0.7rem;*/
    line-height: 0.853333rem;
    color: #fff;
    font-size: 0.64rem;
  }
}
</style>



