<template>
<div class="rey_box">
    <div class="topHeader" style="height:1.1rem;">
        <header class="mint-header is-fixed" style="background-color:#00487c;">
            <div class="mint-header-button is-left">
                <a href="#/" class="router-link-active">
            <i class="mintui mintui-back"></i>
          </a>
            </div>
            <h1 class="mint-header-title">退款</h1>
            <div class="mint-header-button is-right">
                <mt-button icon="more" @click="sheetVisible=true" slot="right"></mt-button>
                <actionsheet :actions="actions" v-model="sheetVisible"></actionsheet>
            </div>
        </header>
    </div>
    <div class="search">
        <div class="searchBox">
            <input class="tableNuber" type="text" placeholder="桌号" v-model="searchOption.tableNo">
            <input
          class="orderNumer"
          type="text"
          :placeholder="SYS_DEVICE=='ICBC'?'参考号 / 订单号':'凭证号 / 订单号'"
          v-model="searchOption.masterNo"
        >
            <div class="searchResult" @click="search">查询</div>
        </div>
    </div>
    <div class="scrollBox" v-infinite-scroll="loadMore" infinite-scroll-disabled="loading" infinite-scroll-distance="10">
        <div class="orderList" v-for="item in listInfo" :key="item.index">
            <div class="blueLind"></div>
            <div class="infoL">
                <div class="lineInfo">
                    <div class="titleThird nomalL">订单号</div>&nbsp;:
                    <span>{{item.accnt}}</span>
                </div>
                <div class="lineInfo">
                    <div class="titleThird nomalL">营业点</div>&nbsp;:
                    <span>{{item.pcCodeDescript}}</span>
                </div>
                <div class="lineInfo">
                    <div class="titleThird nomalL">总金额</div>&nbsp;:
                    <span>
              <small>￥</small>
              {{item.charge}}
            </span>
                </div>
            </div>
            <div class="infoL infoR">
                <div class="lineInfo">
                    <div class="titleThird">桌</div>
                    <div class="titileS">号&nbsp;:</div>
                    <span>{{item.tableno}}</span>
                </div>
                <div class="lineInfo">
                    <div class="titleThird nomalL">{{SYS_DEVICE=="ICBC"?"参考号":"凭证号"}}</div>&nbsp;:
                    <span>{{item.orderno | fitOrderNo}}</span>
                </div>
                <div class="lineInfo">
                    <div class="titleThird nomalL">{{item.descript}}</div>&nbsp;:
                    <span>
              <small>￥</small>
              {{item.credit}}
            </span>
                </div>
            </div>
            <div class="infoBo">
                <div class="titileS">支付时间&nbsp;:</div>
                <span>{{item.logdate}}</span>
            </div>

            <div class="returnPay">
                <div class="returnButton detailButton" @click="openDetail(item)">详情</div>
                <div class="returnButton" @click="showAlter(item)">撤销</div>
                <!--<div class="iconfont icon-you"></div>-->
            </div>
        </div>
        <div v-bind:class="{ 'hide': !isEmpty }">
            <img class="nothing" src="../../assets/img/nothing.png">
            <p class="nothingTxt" v-show="!isFrist">什么都没有</p>
            <p class="nothingTxt" v-show="isFrist">
                请输入查询条件查询退款订单
                <br>(仅能退营业日当天订单）
        </p>
                <img class="nothing" src="../../assets/img/chenggong.png" v-show="isSucceed">
                <p class="nothingTxt" v-show="isSucceed">撤销成功</p>
        </div>
        <div class="infoWarn" v-bind:class="{ 'hide': cutOff }">我是有底线的</div>
    </div>
    <mt-popup v-model="popupVisible" position="right" modal="false" style="width:100%;height:100%;overflow:auto">
        <div class="topHeader">
            <header class="mint-header is-fixed" style="background-color:#00487C;">
                <div class="mint-header-button is-left">
                    <a class="router-link-exact-active router-link-active" @click="openDetail">
              <i class="mintui mintui-back"></i>
            </a>
                </div>
                <h1 class="mint-header-title">订单详情</h1>
                <div class="mint-header-button is-right"></div>
            </header>
        </div>
        <masterList ref="masterListC"></masterList>
    </mt-popup>

    <div class="alertBox" v-if="showTwo==true">
        <div class="bgBox"></div>
        <div class="contentBox">
            <div class="top">
                <span>请输入账号验证身份</span>
                <div class="iconfont icon-cuowu" @click="fork"></div>
            </div>
            <div class="inputTwo">
                <input type="text" placeholder="工号" class="inp" v-model="user">
                <input type="password" placeholder="密码" class="inp" v-model="password">
        </div>
                <span class="twoSure" @click="checkUser">确认</span>
            </div>
        </div>
    </div>
</template>

<script>
import moment from "moment";
import orderdetail from "../common/orderdetail";
import masterList from "../common/masterList";
import {
    Toast,
    Actionsheet,
    MessageBox
} from "mint-ui";
import md5 from "js-md5";
export default {
    data() {
        return {
            accnt: "",
            showTwo: false,
            cutOff: true,
            masterTotal: "",
            listInfo: [],
            loading: false,
            info: {
                masterTotal: ""
            },
            isEmpty: true,
            isFrist: true,
            isSucceed: false,
            user: location.LoginOption ? JSON.parse(location.LoginOption).user : "",
            password: location.LoginOption ?
                JSON.parse(location.LoginOption).password :
                "",
            orderInfoObj: "",
            sheetVisible: false,
            actions: [],
            popupVisible: false,
            SYS_DEVICE: "",
            currentItem: null,
            searchOption: {
                sta: "o",
                tableNo: "",
                masterNo: "",
                bizDate: "",
                masterNo: "",
                paycode: "", //PUN,PUN_CARD,PUN_ALIPAY,PUN_WEIXIN,PUN_UPAY
                pageSize: 10,
                page: 1
            },
            ps: !!localStorage.ps ?
                JSON.parse(localStorage.ps) :
                {
                    accountLogUser: "",
                    invoice: false,
                    printPayCount: 1,
                    printRoomCount: 1
                }
        };
    },
    mounted() {
        this.SYS_DEVICE = this.$SYS_DEVICE;
        this.user = this.ps.accountLogUser;
        this.actions = [];
        this.actions.push({
            name: "退款核对",
            method: this.reDoFunc
        });
    },
    methods: {
        notsees() {
            this.popupVisible = false;
        },
        fork() {
            this.showTwo = false;
        },
        showAlter(item) {
            MessageBox({
                title: "提示",
                message: "确定撤消?",
                showCancelButton: true,
                closeOnClickModal: false,
                confirmButtonText: "确定"
            }).then(action => {
                if (action == "confirm") {
                    this.currentItem = item;
                    this.showTwo = !this.showTwo;
                }
            });
        },
        openDetail(item) {
            //this.getMasterList(item.accnt);
            //this.popupVisible=!this.popupVisible
            this.accnt = item.accnt;
            this.popupVisible = !this.popupVisible;
            this.$refs.masterListC.getMasterList(item.accnt, item.tableno);
            //this.$refs["odetail"].getMasterList(item.accnt);
        },
        //调用硬件退款
        refundSucceed(value) {
            if (value.transData.resCode == "00") {
                this.checkOutRevoke();
            } else {
                this.msg(value.transData.resDesc);
            }
        },
        // 硬件退款
        refundOrder() {
            //流水号
            var traceNo = this.currentItem.orderno;
            var credit = Math.round(this.currentItem.credit * 100);
            var paycodeDesc = this.currentItem.paycodeDesc;
            var nOrderNo = null;
            if (this.currentItem.accountNum) {
                nOrderNo = this.getAccnt();
            }
            if (this.currentItem.paycodeDesc.indexOf("PUN") >= 0) {
                refund(
                    this.refundSucceed,
                    null,
                    traceNo,
                    credit,
                    paycodeDesc,
                    this.currentItem,
                    nOrderNo
                );
            } else {
                this.checkOutRevoke();
            }

        },
        reDoFunc() {
            if (this.currentItem == null && this.listInfo.length > 0) {
                this.currentItem = this.listInfo[0];
            }
            if (!this.currentItem.accountNum) {
                this.msg("当前云餐饮版本不支持退款核对");
                return;
            }
            reDoFund(
                this.refundSucceed,
                null,
                this.currentItem.orderno,
                this.getAccnt(),
                this.currentItem
            );

        },
        getAccnt() {
            return this.currentItem.accnt + "_" + this.currentItem.accountNum;
        },
        checkOutRevoke() {
            var accnt = this.currentItem.accnt;
            this.$http
                .post("/pos/posUnion/saveOrderCheckOutRevoke", {
                    accnt: accnt,
                    userCode: this.ps.accountLogUser
                })
                .then(res => {
                    if (res.data.errorCode == 0) {
                        // 校验用户信息成功，开始执行入账程序
                        this.msg("账单撤销成功");
                        (this.isEmpty = false), (this.isFrist = false);
                        this.currentItem = null;
                        this.isSucceed = true;
                        this.listInfo = [];
                    } else {
                        this.msg(res.data.errorMsg);
                    }
                });
        },
        checkUser() {
            var user = this.user;
            var password = md5(this.password);
            this.$http
                .post("/pos/posUnion/updateCheckUserInfo", {
                    user: user,
                    password: password,
                    checkCancelFlag: 1
                })
                .then(res => {
                    if (res.data.errorCode == 0) {
                        // 校验用户信息成功，开始执行入账程序
                        this.showTwo = !this.showTwo;
                        this.refundOrder();
                    } else {
                        this.msg(res.data.errorMsg);
                    }
                });
        },

        search() {
            this.searchOption.page = 1;
            if (this.searchOption.tableNo == "" && this.searchOption.masterNo == "") {
                this.msg("查询内容不能为空");
            } else {
                this.getInfo();
            }
        },
        getInfo(isloadMore) {
            this.$http
                .post("/pos/posUnion/getMasterListWithSta", this.searchOption)
                .then(res => {
                    if (res.data.errorCode == 0) {
                        this.info = res.data.result;
                        this.masterTotal = this.info.masterTotal;

                        //
                        if (isloadMore) {
                            // 加载更多
                            _.forEach(this.info.masterlist, (el, key) => {
                                this.listInfo.push(el);
                                this.cutOff = false;
                            });
                        } else {
                            // 第一次加载数据
                            this.listInfo = this.info.masterlist;
                        }
                        if (Number(this.searchOption.page) > 1) {
                            if (this.info.masterlist.length < this.searchOption.pageSize) {
                                this.cutOff = false;
                            }
                        }
                        this.isEmpty = false;
                        this.isFrist = false;
                        this.loading = false;
                        this.isSucceed = false;
                        if (this.info.masterTotal == 0) {
                            this.msg("查询结果为0笔");
                            this.isEmpty = true;
                            this.cutOff = true;
                        }
                    } else {
                        this.msg(res.data.errorMsg);
                    }
                });
        },
        msg(msg) {
            Toast({
                message: msg,
                position: "bottom",
                duration: 3000
            });
        },
        loadMore() {
            if (
                this.info.masterTotal >=
                parseFloat(this.searchOption.pageSize) *
                parseFloat(this.searchOption.page)
            ) {
                this.searchOption.page += 1;
                this.loading = true;
                var isloadMore = true;
                let sef = this;
                sef.getInfo(isloadMore);
            } else {
                console.log("加载没生效");
            }
        }
    },
    filters: {
        tofixed(v) {
            return parseFloat(v).toFixed(2);
        },
        fitOrderNo(v) {
            return v != null && v.indexOf("_") > 0 ? v.split("_")[0] : v;
        },
        fd(value) {
            if (value == null || value == "") return "";
            return value.substr(11);
        }
    },
    components: {
        orderdetail,
        masterList,
        Actionsheet
    }
};
</script>

<style lang="less">
.rey_box {
    .refundTop {
        width: 100%;
        height: 1.28rem;
        background: #fff;

        .data {
            width: 59%;
            height: 1.0667rem;
            margin-top: 0.1067rem;
            display: inline-block; // background: blue;

            .iconBox {
                border-radius: 2.6667rem;
                width: 0.8533rem;
                height: 0.8533rem;
                display: inline-block;
                text-align: center;
                background: #fff;
                -moz-transform: none;
                -webkit-transform: none;
                -o-transform: none;
                -ms-transform: none;
                transform: none;

                .icon-jiantou,
                .icon-caret-right-copy {
                    font-size: 0.32rem;
                    line-height: 0.88rem;
                    color: #d8d8d8;
                    transform: scale(0.7);
                    margin-left: 0.1067rem;
                }

                .icon-jiantou {
                    margin-left: 0rem;
                }
            }

            .blakBg {
                background: #f6f8f9 !important;

                border-radius: 2.6667rem;
            }

            .fontBlack {
                color: black !important;
            }

            .dataDtail {
                display: inline-block;
                font-size: 0.3733rem;
                color: #393c41;
                margin: 0.2667rem 0.3733rem 0rem;
            }
        }
    }

    .search {
        width: 100%;
        border-radius: 0.2667rem;
        background: #f4f5f7;
        margin: 0 auto 0 auto;
        height: 1.6rem;

        .searchBox {
            height: 0.8rem;
            margin: 0 auto;
            width: 96%;
            padding-top: 0.4rem;

            .tableNuber {
                width: 21%;
                height: 0.7733rem;
                font-size: 0.3733rem;
                padding-left: 0.2667rem;
                background: #fff;
                border: 0.0267rem solid #cccccc;
                border-radius: 0.1333rem;
                margin-left: 0.1333rem;
                float: left;
            }

            .orderNumer {
                width: 41%;
                height: 0.7733rem;
                font-size: 0.3733rem;
                padding-left: 0.2667rem;
                background: #fff;
                border: 0.0267rem solid #cccccc;
                border-radius: 0.1333rem;
                float: left;
                margin-left: 0.2667rem;
            }

            .searchResult {
                width: 20.5%;
                height: 0.7733rem;
                font-size: 0.3733rem;
                background: #d8af68;
                border-radius: 0.1867rem;
                float: right;
                color: #fff;
                line-height: 0.7467rem;
                margin-right: 0.08rem;
                margin-top: 0.0533rem;
            }
        }
    }

    .scrollBox {
        .orderList {
            background: #ffffff;
            margin-bottom: 0.2667rem;
            width: 96%;
            height: auto;
            margin: 0 auto 0.2667rem;
            box-shadow: 0 0 0.2133rem 0rem #dadada;
            border-radius: 0.1333rem;
            position: relative;

            .blueLind {
                height: 0.3733rem;
                width: 0.9%;
                background: #646f83;
                display: inline-block;
                float: left;
                margin-top: 0.48rem;
            }

            .infoL {
                width: 52%;
                height: auto;
                text-align: left;
                display: inline-block;
                padding-top: 0.3733rem;
                text-indent: 0.08rem;

                .lineInfo {
                    margin-bottom: 0.1067rem;
                    color: #999999;

                    .titleThird {
                        display: inline-block;
                        color: #999999;
                        letter-spacing: 0.2667rem;
                    }

                    .titileS {
                        display: inline-block;
                        color: #999999;
                    }

                    .nomalL {
                        letter-spacing: 0rem;
                    }

                    span {
                        font-size: 0.32rem;
                        color: #151515;
                    }
                }
            }

            .infoR {
                width: 45%;
            }

            .infoBo {
                text-align: left;
                margin-left: 0.3733rem;
                margin-bottom: 0.2133rem;

                .titleThird {
                    display: inline-block;
                    color: #999999;
                    letter-spacing: 0.2667rem;
                }

                .titileS {
                    display: inline-block;
                    color: #999999;
                }

                .nomalL {
                    letter-spacing: 0rem;
                }

                span {
                    margin-left: 0.1333rem;
                    font-size: 0.3733rem;
                    color: #151515;
                }

                b {
                    font-size: 0.32rem;
                    color: #4990e2;
                    text-decoration: underline;
                    margin-left: 0.1333rem;
                }
            }

            .returnPay {
                width: 94%;
                height: 1.2rem;
                border-top: 0.0267rem solid #eeeeee;
                margin: 0 auto;

                .returnButton {
                    float: right;
                    width: 33%;
                    height: 0.8267rem;
                    background: #ff8902;
                    border-radius: 0.1867rem;
                    color: #fff;
                    font-size: 0.3733rem;
                    line-height: 0.7467rem;
                    margin-top: 0.2133rem;
                }

                .detailButton {
                    float: left;
                    background: #2d8cf0;
                }

                .icon-you {
                    float: right;
                    font-size: 0.3733rem;
                    line-height: 1.1733rem;
                    margin-right: 0.08rem;
                }
            }
        }
    }
}

.nothing {
    width: 80px;
    height: 88px;
    margin: 150px auto 0 auto;
}

.nothingTxt {
    text-align: center;
    font-size: 20px;
    color: #ccd1d5;
    margin-top: 20px;
}

.show {
    display: block;
}

.hide {
    display: none;
}

.restaurantDetail_box {
    overflow: auto;
    height: 100%;

    .DetailTop {
        width: 90%;
        height: 1.2533rem;
        margin: 0 auto;
        font-size: 0.32rem;
        color: #333333;

        .topL {
            float: left;
            line-height: 1.2267rem;
        }

        .topR {
            float: right;
            line-height: 1.2267rem;

            span {
                display: inline-block;
            }

            .circleBox {
                width: 0.5333rem;
                height: 0.5333rem;
                background: #646f83;
                border-radius: 1.36rem;
                color: #fff;
                display: inline-block;
                vertical-align: top;
                margin-top: 0.3733rem;
                line-height: 0.5867rem;
            }
        }
    }
}

.mint-popup-right {
    width: 100%;
    height: 100%;
}

.alertBox {
    .bgBox {
        opacity: 0.7;
        background: #000000;
        width: 100%;
        height: 100%;
        position: fixed;
        top: 0rem;
    }

    .contentBox {
        width: 80%;
        height: 7.3067rem;
        background: #ffffff;
        position: fixed;
        top: 14%;
        left: 10%;

        .top {
            width: 92%;
            height: 1.1467rem;
            margin: 0 auto;

            span {
                font-size: 0.32rem;
                color: #333333;
                float: left;
                margin-top: 0.3467rem;
                font-weight: bold;
            }

            .icon-cuowu {
                font-size: 0.32rem;
                color: #999999;
                float: right;
                margin-top: 0.2667rem;
            }
        }

        .inputTwo {
            height: 3.2rem;
            width: 76.7%;
            margin: 0 auto;

            .inp {
                width: 100%;
                height: 0.9867rem;
                text-transform: uppercase;
                background: #ffffff;
                border: 0.0267rem solid #cccccc;
                border-radius: 0.1333rem;
                margin-top: 0.2933rem;
                padding-left: 0.3733rem;
            }
        }

        .twoSure {
            font-size: 0.3733rem;
            color: #ffffff;
            width: 45%;
            height: 0.9867rem;
            line-height: 0.9067rem;
            background: #ff8902;
            border-radius: 0.1867rem;
            display: inline-block;
            margin-bottom: 0.3467rem;
        }

        p {
            font-size: 0.32rem;
            color: #999999;
        }
    }
}
</style>



