<template>
<div class="sysadmin">
   <topNav title="系统操作" url="/"></topNav>
    <div class="settingContent">
         <div class="settingAll">
           <div class="allBox">
              <div class="testButton" @click="clear">清除所有参数</div>
            </div>
         </div>
    </div>
     <div class="defaultNumber">
      <h3>管理员密码修改</h3>
      <div class="inputNumber">
         <input type="password" class="workNumber" v-model="adpass" placeholder="输入新的管理员密码" >
      </div>
    <div class="inputNumber">
         <input type="password" class="workNumber" v-model="adpass2" placeholder="确认你的管理员密码">
      </div>
        <div class="testButton" @click="savepass">确定修改</div>
    </div>
     <mt-popup v-model="popupVisible"  style="width:100%;height:100%"  modal='true' closeOnClickModal='false'>
         <adminpass @success="popupVisible=false"></adminpass>
    </mt-popup>
</div>
</template>
<script>
import { Toast, MessageBox } from "mint-ui";
import adminpass from "../common/adminpass";
import md5 from "js-md5";
export default {
  data() {
    return {
      popupVisible: true,
      adpass: "",
      adpass2: ""
    };
  },
  mounted() {},
  methods: {
    clear() {
      MessageBox.confirm("确定清除本地数据？", "")
        .then(action => {
          var system = localStorage.system;
          var adpass = "";
          if (!!localStorage.adpass) {
            adpass = localStorage.adpass;
          }
          localStorage.clear();
          if (adpass != "") {
            localStorage.setItem("adpass", adpass);
          }
          localStorage.setItem("system", system);
          this.$router.push("/");
        })
        .catch(action => {});
    },
    msg(msg) {
      Toast({
        message: msg,
        position: "bottom",
        duration: 3000
      });
    },
    savepass() {
      if(this.adpass != this.adpass2){
        this.msg("两次密码不一致");
        return;
      }
      if (this.adpass == "" || this.adpass.length < 4) {
          this.msg("密码长度必需4位以上");
          return;
      } 
     
      localStorage.setItem("adpass", md5(this.adpass));
      this.msg("修改成功");
    }
  },
  components: {
    adminpass
  }
};
</script>
<style lang='less'>
.sysadmin {
  width: 100%;
  margin: 0 auto;
  text-align: left;
  .clearfix:before,
  .clearfix:after {
    content: "";
    display: block;
    clear: both;
  }
  .clearfix {
    zoom: 1;
  }

  .defaultNumber {
    box-shadow: 0 0 0.2133rem 0rem #dadada;
    border-radius: 0.1333rem;
    width: 96%;
    margin: 0 auto;
    margin-top: 0.2667rem;
    background: #fff;
    padding-top: 0.0267rem;
    padding-bottom: 0.267rem;
    h3 {
      font-size: 0.32rem;
      color: #666666;
      margin-top: 0.3733rem;
      margin-bottom: 0.1333rem;
      margin-left: 3.65%;
    }
    .inputNumber {
      width: 92.7%;
      margin: 0 auto;
      padding-bottom: 0.5333rem;
      .workNumber {
        width: 90%;
        padding-left: 0.2667rem;
        border-radius: 0.1333rem;
        height: 0.9867rem;
        text-transform: uppercase;
        border: 0.0267rem solid #cccccc;
        margin: 0 auto;
      }
    }
  }
  .settingContent {
    box-shadow: 0 0 0.2133rem 0rem #dadada;
    border-radius: 0.1333rem;
    width: 96%;
    margin: 0 auto;
    height: auto;
    background: #fff;
    .settingAll {
      width: 100%;
      height: auto;
      .allBox {
     
        margin: 0 auto;
        margin-top: 0.5333rem;
        padding: 0.32rem 0;
        
      }
    }
  }
  .testButton {
          width: 44%;
          height: 0.9867rem;
          text-align: center;
          line-height: 0.9067rem;
          color: #fff;
          font-size: 0.3733rem;
          background: #ff8902;
          border-radius: 0.1867rem;
          display: inline-block;
          margin-bottom: 0.2667rem;
          margin-left: 0.36rem;
        }
}
</style>


