<template>
  <div class="masList">
    <div class="mr_box">
      <div class="DetTop">
        <div class="topL">预订单号：{{orderInfoObj.accnt}}</div>
        <div class="topR">
          <span>状态：</span>
             <div class="circleBox" v-if="orderInfoObj.sta == 'C'">X</div>
          <div class="circleBox" v-else>{{orderInfoObj.sta}}</div>
        </div>
      </div>
      <div class="mrList">
        <div class="listBox">
          <div class="blueLind"></div>
          <div class="vipCheckBox">
            <div class="infoBo">
              <span>基本信息</span>
            </div>
          </div>
          <div class="infoDe clearfix">
            <ul>
              <li>
                <p><label>主桌号：</label>{{orderInfoObj.tableno}}</p>
                <p><label>人数：</label>{{orderInfoObj.gsts}}</p>
              </li>
              <li>
                <p><label>营业日期：</label>{{orderInfoObj.bizDate}}</p>
                <p><label>班次：</label>{{orderInfoObj.shift}}</p>
              </li>
              <li>
                <p><label>优惠模式：</label>{{orderInfoObj.dscModeDescript}}</p>
              </li>
              <li>
                <p><label>会员卡：</label> {{orderInfoObj.cardNo}}</p>

              </li>
              <li >
                <p><label>会员姓名：</label> {{orderInfoObj.cardInfo}}</p>
              </li>

               <li class="p100">
                <p class="p100"><label>协议公司：</label> {{orderInfoObj.cusinfo}}</p>
              </li>
              <li>
                <p><label>当前消费：</label> <small>￥</small> {{getFP().toFixed(2)}}</p>

              </li>
              <li >
                <p><label>服务费：</label> <small>￥</small> {{orderInfoObj.srvAmount}}</p>
              </li>
              <li>
                <p><label>税费：</label> <small>￥</small>{{orderInfoObj.taxAmount}}</p>
              </li>
              <li >
                <p><label>折扣：</label><small>￥</small> {{orderInfoObj.dscAmount}}</p>
              </li>
              <li >
                <p><label>付款：</label><small>￥</small> {{orderInfoObj.credit}}</p>
              </li>
              <li>
                <p style="color:red"><label>应付款：</label><small>￥</small> {{getPayAmout().toFixed(2)}}</p>
              </li>
            </ul>
          </div>
        </div>
        <div class="listBox clearBottom" v-if="orderInfoObj != null && orderInfoObj.unionslist != null && orderInfoObj.unionslist.length > 0">
          <div class="blueLind"></div>
          <div class="vipCheckBox">
            <div class="infoBo">
              <span>相关桌台</span>
            </div>
          </div>
          <div class="infoDe clearfix">
            <ul class="tablList">
              <li v-for="(item,index) in orderInfoObj.unionslist" @click="getMasterList(orderInfoObj.accnt,item.unionTableNo)" :key="index">
                  {{item.unionTableNo}}
              </li>
            </ul>
          </div>
        </div>


  <div class="listBox clearBottom">
          <div class="blueLind"></div>
          <div class="vipCheckBox">
            <div class="infoBo">
              <span>本桌菜目</span>
            </div>
          </div>
          <div class="infoDe clearfix">

<table class="tableDetail">
          <tr>
            <th style="width:30%;text-align:left;">项目</th>
            <th style="width:14%;white-space:nowrap">数量</th>
            <th style="width:23%;">单价</th>
            <th style="padding-right: 20px;">金额</th>
          </tr>
          <tr class="contentDetail" v-for="(i2,index) in orderInfoObj.detailDtos" :key="index">
            <td style="width: 30%;text-align: left;" class="dishname">{{i2.pluDescript}}</td>
            <td>{{Number(i2.pluNumber)}}</td>
            <td>{{i2.pluPrice}}<small>/{{i2.pluUnit}}</small></td>
            <td style="padding-right: 20px;">{{i2.pluAmount}}</td>
          </tr>
        </table>
          </div>
        </div>

        <div class="listBox clearBottom">
          <div class="blueLind"></div>
          <div class="vipCheckBox">
            <div class="infoBo">
              <span>更多信息</span>
            </div>
          </div>
          <div class="infoDe clearfix">
            <ul>
              <li>
                <p><label>来源码：</label>{{orderInfoObj.sourceDescript}}</p>
                <p><label>市场码：</label>{{orderInfoObj.marketDescript}}</p>
              </li>
              <li>
                <p><label>销售员：</label>{{orderInfoObj.saleName}}</p>
              </li>
            </ul>
          </div>
        </div>
       
      </div>
    </div>
  </div>
</template>

<script>
import { Toast } from "mint-ui";
export default {
  data() {
    return {
      tableNo: "",
      detailTitle: "餐单详情",
      orderInfoObj: "",
    };
  },
  props: {},
  mounted() {
    
  },
  methods: {
    getFP() {
      return (
        Number(this.orderInfoObj.charge) +
        Number(this.orderInfoObj.dscAmount) -
        Number(this.orderInfoObj.srvAmount)
      );
    },
    getPayAmout() {
      return (
        Number(this.orderInfoObj.charge) - Number(this.orderInfoObj.credit)
      );
    },
    msg(msg) {
      Toast({
        message: msg,
        position: "bottom",
        duration: 3000
      });
    },
    getMasterList(accnt, tableno) {
      if (accnt == undefined) {
        return;
      }

      this.$http
        .post("/pos/posUnion/getMasterList", {
          accnt: accnt,
          tableno: tableno
        })
        .then(res => {
          if (res.data.errorCode == 0) {
            this.orderInfoObj = res.data.result;
            this.orderInfoObj.bizDate = this.orderInfoObj.bizDate.substring(
              0,
              10
            );
          } else {
            this.msg(res.data.errorMsg);
          }
        });
    }
  }
};
</script>

<style lang="less">
.masList {
  .mr_box {
    overflow: auto;
    height: 100%;
    .DetTop {
      width: 90%;
      height: 1.2533rem;
      margin: 0 auto;
      margin-top: 1.1rem;
      font-size: 0.32rem;
      color: #333333;
      .topL {
        float: left;
        line-height: 1.2267rem;
      }
      .topR {
        float: right;
        line-height: 1.2267rem;
        span {
          display: inline-block;
        }
        .circleBox {
          width: 0.64rem;
          height: 0.64rem;
          background: #646f83;
          border-radius: 1.36rem;
          color: #fff;
          display: inline-block;
          vertical-align: top;
          margin: 0.3733rem 0.2rem 0 0;
          line-height: 0.6367rem;
        }
      }
    }
    .mrList {
      background: #ffffff;
      margin-bottom: 0.2667rem;
      width: 96%;
      height: auto;
      margin: 0 auto 0.2667rem;
      box-shadow: 0 0 0.2133rem 0rem #dadada;
      border-radius: 0.1333rem;
      .listBox {
        width: 100%;
        height: auto;
        padding-bottom: 0.2667rem;
        border-bottom: 0.0267rem dashed #eeeeee;
        .blueLind {
          height: 0.3733rem;
          width: 1.1%;
          background: #646f83;
          display: inline-block;
          float: left;
          margin-top: 0.4rem;
        }
        .infoDe {
          width: 90%;
          margin: 0 auto;
          text-align: left;
          margin-top: 0.2667rem;
          font-size: 0.35rem;
          color: #333333;
          ul {
            li {
              p {
                display: inline-block;
                width: 50%;
                float: left;
              }
            }
            .p100 {
              width: 100%;
              clear: both;
            }
          }
          .tablList {
            li {
              float: left;
              margin-right: 8px;
              text-decoration: underline;
              color: #4990e2;
            }
          }
          .tableDetail {
            border: none;
            width: 100%;
            text-align: left;
            margin: 0 auto;
            border-bottom: 0.0267rem dashed #cccccc;
            .contentDetail {
              height: 0.7467rem;
              td {
                text-align: right;
                white-space: nowrap;
                text-overflow: ellipsis;
                overflow: hidden;
                small {
                  font-size: 0.2rem;
                }
              }
            }
            tr {
              th {
                border-bottom: 0.0267rem solid #cccccc;
                padding-left: 0.2667rem;
                text-align: right;
              }
            }
          }
          .infoL {
            width: 50%;
            height: auto;
            text-align: left;
            display: inline-block;
            white-space: nowrap;
            overflow: hidden;
            text-overflow: ellipsis;
            float: left;
          }
          .infoR {
            width: 50%;
            height: auto;
            text-align: left;
            display: inline-block;
            white-space: nowrap;
            overflow: hidden;
            text-overflow: ellipsis;
            float: right;
          }
        }
        .vipCheckBox {
          width: 94.5%;
          margin: 0 auto;
          padding-top: 0.2933rem;
          .infoBo {
            text-align: left;
            margin-left: 0.08rem;
            span {
              font-weight: bold;
              margin-left: 0.1333rem;
              font-size: 0.3733rem;
              color: #151515;
            }
          }
        }
      }
      .clearBottom {
        border-bottom: none;
      }
    }
  }
}
</style>


