<template>
<div class="orderPayDetail">
    <div class="topHeader" style="height:1.1rem">
        <header class="mint-header is-fixed" style="background-color:#00487C;">
            <div class="mint-header-button is-left">
                <a href="#/" class="router-link-active pp2">
            <i class="mintui mintui-back"></i>
          </a>
            </div>
            <h1 class="mint-header-title">订单支付</h1>
            <div class="mint-header-button is-right">
                <mt-button icon="more" @click="sheetVisible=true" slot="right"></mt-button>
                <actionsheet :actions="actions" v-model="sheetVisible"></actionsheet>
            </div>
        </header>
    </div>
    <div class="topSearch">
        <span class="category" v-bind:class="{ active: listVisible }" @click="toggleList">
        <i class="iconfont icon-ADown"></i>
      </span>
        <input
        type="text"
        class="search"
        placeholder="桌号"
        @focus="getsearchOnfocus()"
        v-model="tableNo"
      >
        <span class="tableDesc">{{orderDetailObj.tableDesc}}</span>
        <div class="iconfont icon-saoma" @click="scanOrder"></div>
        <div class="searchCheck" @click="searchOrder">搜索</div>
    </div>
    <div class="category-list clear" v-if="listVisible">
        <div class="category-tabs">
            <ul>
                <li v-for="(item ,index) in tableList" :key="index" :name=" item.code" v-bind:class="{ active: (item.code== tableListActive) }" @click="changeFirstPayCode(index)">
                    <span>{{item.descript}}</span>
                </li>
            </ul>
        </div>
        <div class="category-content">
            <ul>
                <li v-for="(item ,index) in tableSubList" :key="index" :name=" item.code" v-bind:class="{ active: (item.code== tableSubListActive) }" @click="changeSubPayCode(index)">
                    <span>({{item.code}}){{item.descript}}</span>
                </li>
            </ul>
        </div>
    </div>

    <div class="info" v-bind:class="{ 'hide': isEmpty }">
        <ul>
            <li class="clear">
                <p style="width:55%">
                    <label>单号：</label>
                    {{orderDetailObj.accnt}}
                    <b @click="openDetail">详情</b>
                </p>
                <p style="width:35%">
                    <label>状态：</label>
                    <span class="circleBox">{{orderDetailObj.sta}}</span>
                </p>
            </li>
            <li class="clear">
                <p style="width:55%" v-if="orderDetailObj.cardNo">
                    <label>会员卡号：</label>
                    {{orderDetailObj.cardNo}}
                    <span
              class="iconfont icon-tianjia"
              @click="openVip(false)"
            ></span>
                </p>
                <p style="width:55%" v-else-if="orderDetailObj.cusinfo">
                    <label style="float:left">协议：</label>
                    <span class="comspan">{{orderDetailObj.cusinfo}}</span>
                    <span class="iconfont icon-tianjia" @click="companyOpen=true"></span>
                </p>
                <p style="width:55%" v-else>
                    <label>会员/协议：</label>
                    <span
              class="iconfont icon-tianjia"
              @click="openVip(false)"
              style="font-size:0.35rem;color:#151515"
            >会员</span>
                    <span
              class="iconfont icon-tianjia"
              @click="companyOpen=true"
              style="font-size:0.35rem;margin-left:0.4rem;color:#151515"
            >协议</span>
                </p>
                <p style="width:35%" v-show="orderDetailObj.dscModeDescript!=null">
                    <label style="float:left">优惠：</label>
                    <span class="yh" @click="openVinfo">
              {{orderDetailObj.dscModeDescript}}
              <i
                class="iconfont icon-you"
                style="font-size:0.27rem;margin-left:0.3rem"
              ></i>
            </span>
                </p>
            </li>
            <li class="clear" v-show="orderDetailObj.unionslist.length>0">
                <p style="width:98%">
                    <label style="float:left">关联桌号：</label>
                    <span
              class="unionTableNo"
              v-for="item in orderDetailObj.unionslist"
              :key="item.unionTableNo"
              @click="changeTableNo(item)"
            >{{item.unionTableNo}}</span>
                </p>
            </li>
        </ul>
    </div>
    <div class="detail" v-bind:class="{ 'hide': isEmpty,'detail2': orderDetailObj.unionslist.length==0}">
        <div class="topLine">
            <div class="changeColor"></div>
        </div>
        <div class="infoDetails">
            <table class="tableDetail">
                <tr>
                    <th style="width:30%;text-align:left;">项目</th>
                    <th style="width:12%;white-space:nowrap">数量</th>
                    <th style="width:25%;">单价</th>
                    <th style="padding-right:20px;">金额</th>
                </tr>
                <tr class="contentDetail" v-for="(item,index) in orderDetailObj.detailDtos" :key="index">
                    <td style="width:30%;text-align:left;" class="dishname">{{item.pluDescript}}</td>
                    <td>{{Number(item.pluNumber)}}</td>
                    <td>
                        {{item.pluPrice}}
                        <small>/{{item.pluUnit}}</small>
                    </td>
                    <td style="padding-right:20px;">{{item.pluAmount}}</td>
                </tr>
            </table>
            <div class="allserver">
                <ul>
                    <li>
                        <div class="payFirst"></div>
                        <div class="titleS">当前消费：</div>
                        <span>{{getFP().toFixed(2)}}</span>
                    </li>
                    <li v-if="orderDetailObj.srvAmount != 0">
                        <div class="payFirst"></div>
                        <div class="titleS">服务费：</div>
                        <span>{{orderDetailObj.srvAmount}}</span>
                    </li>
                    <li v-if="orderDetailObj.taxAmount != 0">
                        <div class="payFirst"></div>
                        <div class="titleS">含税：</div>
                        <span>{{orderDetailObj.taxAmount}}</span>
                    </li>
                    <li v-if="orderDetailObj.dscAmount != 0">
                        <div class="payFirst"></div>
                        <div class="titleS">折扣：</div>
                        <span>{{orderDetailObj.dscAmount}}</span>
                    </li>
                    <li v-if="orderDetailObj.credit > 0">
                        <div class="payFirst"></div>
                        <div class="titleS">已付款：</div>
                        <span>{{orderDetailObj.credit}}</span>
                    </li>
                </ul>
            </div>

            <div class="clearFloat"></div>
        </div>
    </div>
    <div v-bind:class="{ 'hide': !isEmpty }">
        <img class="nothing" src="../../assets/img/nothing.png" v-show="!isFrist">
        <p class="nothingTxt" v-show="!isFrist">什么都没有</p>

        <img class="nothing" src="../../assets/img/chenggong.png" v-show="isSucceed">
        <p class="nothingTxt" v-show="isSucceed">付款成功</p>
    </div>
    <div class="clearChoice"></div>
    <div class="footerChoice" v-bind:class="{ 'hide': isEmpty }">
        <div class="allPrice">
            <div class="titleS">应付金额：</div>
            <div class="payFirst">
                <small>￥</small>
                {{getPayAmout().toFixed(2)}}
            </div>
        </div>
        <template v-if="SYS_DEVICE != 'BOC'">
            <div class="fixL">
                <ul>
                    <template v-for="(item,index,key) in payways">
                        <template v-if="SYS_DEVICE != 'NG' || item.catPosting != 'PUN_CARD'">
                            <li :key="key" v-if="index<2 || show==false" @click="payByItem(item)">{{item.descript}}</li>
                        </template>
                    </template>
                </ul>
            </div>
            <div class="fixR" @click="showList">
                <div class="omit" v-if="show==true">...</div>
                <div class="omit" v-else>
                    <div class="iconfont icon-xia"></div>
                </div>
            </div>
        </template>
        <template v-else>
            <div class="fboc">
                <ul>
                    <li class="fb" @click="checkUnionPay()" style="margin-right: 0.3rem;">
                        <img src="../../assets/img/bank.png">
                        <div>中国银行</div>
                    </li>
                    <li class="fb" @click="checkUnionScan('alipay')">
                        <img src="../../assets/img/alipay.png">
                        <div>支付宝</div>
                    </li>
                </ul>
                <div class="ro" @click="showList">
                    <div class="iconfont icon-you" v-if="show==true"></div>
                    <div class="iconfont icon-xia" v-else></div>
                </div>
                <ul v-if="show==false">
                    <li class="fb" @click="checkUnionScan('unionPay')" style="margin-right: 0.3rem;">
                        <img src="../../assets/img/ysf.png">
                        <div>云闪付</div>
                    </li>
                    <li class="fb" @click="checkUnionScan('wechatPay')">
                        <img src="../../assets/img/weixin.png">
                        <div>微信</div>
                    </li>
                </ul>
                <ul v-if="show==false">
                    <li class="fb" @click="toFrontDest" style="margin-right: 0.3rem;">
                        <img src="../../assets/img/room.png">
                        <div>转房间</div>
                    </li>
                    <li class="fb" @click="toAR">
                        <img src="../../assets/img/ar.png">
                        <div>转AR</div>
                    </li>
                </ul>
                <ul v-if="show==false">
                    <li class="fb" @click="selectCardPay" style="margin-right: 0.3rem;">
                        <img src="../../assets/img/card.png">
                        <div>储值支付</div>
                    </li>
                </ul>
            </div>
        </template>
    </div>
    <mt-popup v-model="popupVisible" class="popFull" modal="false">
        <div class="thh" style="background:#00487c">
            <header class="mint-header is-fixed" style="background-color: #00487c">
                <div class="mint-header-button is-left">
                    <a class="router-link-exact-active router-link-active pp2" @click="openDetail">
              <i class="mintui mintui-back"></i>
            </a>
                </div>
                <h1 class="mint-header-title">餐单详情</h1>
                <div class="mint-header-button is-right"></div>
            </header>
        </div>
        <masterList ref="masterListC"></masterList>
    </mt-popup>
    <mt-popup v-model="vipOpen" class="popFull" modal="false">
        <top-nav :title="getMemberCardTitle()" v-on:notseeChild="notsees()" :emptyRight="true"></top-nav>
        <div class="vipSearch">
            <div class="vipTop" v-if="cloudParams.isMobileValidate == 'T'">
                <input class="orderNumer" type="tel" style="width:4.5rem !important;" maxlength="11" placeholder="手机号码" :disabled="cardInfoObj2" v-model="cardInfo">
                <div class="searchResult" @click="getCardInfo2" style="width:4rem" v-if="!cardInfoObj2">查询并发送验证码</div>
                <div class="searchResult" @click="clearMobile" v-else>清除</div>
                        <div class="searchResult" @click="getCardInfo2" v-if="cardInfoObj2" style="margin-right:0.2rem">再次发送</div>
                <div class="vm" v-if="cardInfoObj2">
                    <input class="orderNumer" type="tel" placeholder="验证码" v-model="vmobile">
                    <div class="searchResult" @click="confirmVmobile">确定</div>
                </div>
            </div>
            <div class="vipTop" v-else>
                <div class="iconfont icon-saoma" @click="scanVip"></div>
                <input class="orderNumer" type="text" name id placeholder="会员卡号/姓名/手机" v-model="cardInfo">
                <div class="searchResult" @click="searchCard">查询</div>
            </div>
            <div class="vipList" v-for="(item, index) in cardInfoObj" :key="index" @click="selectCard(item,index)">
                <div class="blueLind"></div>
                <div class="vipCheckBox">
                    <div class="greenCheck">
                        <div class="checkBox">
                            <div class="iconfont" v-bind:class="{ 'icon-right':  item.isSelect  }"></div>
                        </div>
                    </div>
                </div>
                <div class="infoL">
                    <div class="lineInfo">
                        <div class="lineInfo">
                            <div class="titleThird">卡号：</div>
                            <span>{{item.cardNo}}</span>
                        </div>
                        <div class="titleThird">姓名：</div>
                        <span>{{item.cardInfo}}</span>
                    </div>
                    <div class="lineInfo">
                        <div class="titleThird nomalL">手机号：</div>
                        <span>{{item.mobile}}</span>
                    </div>
                </div>
                <div class="infoL">
                    <div class="lineInfo">
                        <div class="titleS">余额：</div>
                        <span>{{item.balance}}</span>
                    </div>
                    <div class="lineInfo">
                        <div class="titleS">状态：</div>
                        <span>{{item.sta}}</span>
                    </div>
                    <div class="lineInfo">
                        <div class="titleThird">优惠：</div>

                        <span>{{item.dscMode}}</span>
                    </div>
                </div>
            </div>
            <div v-bind:class="{ 'hide': !isCardEmpty }">
                <img class="nothing" src="../../assets/img/nothing.png" v-show="!isCardFrist">
                <p class="nothingTxt" v-show="!isCardFrist">什么都没有</p>
            </div>
        </div>
    </mt-popup>
    <mt-popup v-model="VOpen" class="popFull" modal="false">
        <top-nav title="优惠折扣" v-on:notseeChild="notsees()" :emptyRight="true"></top-nav>
        <li @click="selectV(item)" class="mint-cell" v-for="(item, index) in vinfo" :key="index">
            <span class="mint-cell-mask"></span>
            <div class="mint-cell-left"></div>
            <div class="mint-cell-wrapper">
                <div class="mint-cell-title">
                    <span class="mint-cell-text">{{item[1]}}</span>
                </div>
                <div class="mint-cell-value is-link">
                    <span></span>
                </div>
            </div>
            <div class="mint-cell-right"></div>
            <i class="mint-cell-allow-right"></i>
        </li>
    </mt-popup>
    <mt-popup v-model="companyOpen" class="popFull" modal="false">
        <top-nav title="协议公司" v-on:notseeChild="notsees()" :emptyRight="true"></top-nav>
        <div class="vipSearch">
            <div class="vipTop">
                <input class="orderNumer" type="text" placeholder="协议公司名称" v-model="companyInfo">
                <div class="searchResult" @click="searchCompany">查询</div>
            </div>
            <div class="vipList" v-for="(item, index) in companyObj" :key="index" @click="selectCompany(item,index)">
                <div class="blueLind"></div>
                <div class="vipCheckBox">
                    <div class="greenCheck">
                        <div class="checkBox">
                            <div class="iconfont" v-bind:class="{ 'icon-right':  item.isSelect  }"></div>
                        </div>
                    </div>
                </div>
                <ul class="lrp">
                    <li class="p100">
                        <label>编号：</label>
                        {{item.accnt}}
                    </li>
                    <li class="p100">
                        <label>名称：</label>
                        {{item.name}}
                    </li>
                    <li>
                        <label>状态：</label>
                        {{item.sta}}
                    </li>
                    <li>
                        <label>优惠：</label>
                        {{item.posMode}}
                    </li>
                </ul>
            </div>
            <div v-bind:class="{ 'hide': !isCompanyEmpty }">
                <img class="nothing" src="../../assets/img/nothing.png" v-show="!isCompanyFrist">
                <p class="nothingTxt" v-show="!isCardFrist">什么都没有</p>
            </div>
        </div>
    </mt-popup>
</div>
</template>

<script>
import topNav from "../common/topNav";
import masterList from "../common/masterList";
import {
    Toast,
    Actionsheet,
    MessageBox
} from "mint-ui";
import {
    mapState
} from "vuex";
import _ from "lodash";
import md5 from "js-md5";
import {
    getKeyUrlYun
} from "../../assets/js/md5Data.js";
export default {
    data() {
        return {
            listVisible: false,
            tableList: [],
            tableListActive: localStorage.tableListActive ?
                localStorage.tableListActive : "",
            tableCode: "",
            tableSubList: [],
            tableSubListActive: "",
            tableSubListSelect: {},

            tableNo: "",
            accnt: "",
            orderDetailDefaultObj: {
                charge: "0.00",
                srvAmount: "0.00",
                dscAmount: "0.00",
                taxAmount: "0.00",
                unionslist: []
            },
            orderDetailObj: {
                charge: "0.00",
                srvAmount: "0.00",
                dscAmount: "0.00",
                taxAmount: "0.00",
                unionslist: []
            },
            isEmpty: true,
            isFrist: true,
            isCardEmpty: true,
            isCardFrist: true,
            isSucceed: false,
            cardInfo: "",
            vmobile: "",
            cardInfoObj: "",
            cardInfoObj2: null,
            sessionId:null,
            companyObj: {},
            companyInfo: "",
            companyOpen: false,
            isCompanyEmpty: false,
            isCompanyFrist: true,
            cardSelect: false,
            companySlect: false,
            orderInfoObj: "",
            show: true,
            popupVisible: false,
            vipOpen: false,
            sheetVisible: false,
            actions: [],
            VOpen: false,
            vinfo: [],
            SYS_DEVICE: "",
            lastPrintObj: null,
            bcardPay: false,
            ps: !!localStorage.ps ?
                JSON.parse(localStorage.ps) : {
                    accountLogUser: "",
                    invoice: false,
                    printPayCount: 1,
                    printRoomCount: 1
                },
            cloudParams:!!localStorage.cloudParams ?
                JSON.parse(localStorage.cloudParams) : {
                    isMobileValidate: 'F',
                    isChong:'T'
                },
            printMaxCount: 0,
            printNowCount: 0,
            membalance: 0,
            nowPayway: null,
            payways: [{
                "catPosting": "PUN_CARD",
                "code": "",
                "descript": "银行卡"
            }, {
                "catPosting": "PUN",
                "code": "",
                "descript": "支付宝微信"
            }, {
                "catPosting": "TF",
                "code": "",
                "descript": "转房间"
            }, {
                "catPosting": "TA",
                "code": "",
                "descript": "转AR"
            }, {
                "catPosting": "CRV",
                "code": "",
                "descript": "储值卡"
            }]
        };
    },
    methods: {
        getsearchOnfocus() {
            this.listVisible = false;
        },
        clearMobile(){
            this.cardInfo = null;
            this.cardInfoObj2 = null;
            this.isCardFrist = true;
            this.isCardEmpty = true;
        },
        confirmVmobile() {
            if (!this.cardInfo) {
                this.msg('请输入手机');
                return;
            }
            if (!this.vmobile) {
                this.msg('请输入手机验证码');
                return;
            }
            this.$http.post("/pos/posUnion/updateKunLunVerifyCheckCode", {
                mobile: this.cardInfo,
                checkCode: this.vmobile,
                sessionId: this.sessionId,
            }).then(res => {
                if (res.data.errorCode == 0) {
                    this.cardInfoObj = this.cardInfoObj2;
                    this.cardInfoObj2 = null;
                    this.isCardFrist = false;
                    this.isCardEmpty = false;
                } else {
                    this.msg2(res.data.errorMsg);
                }
            });
        },
        openVinfo() {
            this.$http.post("/pos/posUnion/getPosDescMode", {}).then(res => {
                this.vinfo = res.data.result;
                this.VOpen = true;
            });
        },
        selectV(code) {
            MessageBox({
                title: "提示",
                message: "确定使用“" + code[1] + "”?",
                showCancelButton: true,
                closeOnClickModal: false,
                confirmButtonText: "确定"
            }).then(action => {
                if (action == "confirm") {
                    var accnt = this.orderDetailObj.accnt;
                    var dscMode = code[0];
                    this.$http
                        .post("/pos/posUnion/updateCardInfoToMaster", {
                            accnt: accnt,
                            dscMode: dscMode,
                            userCode: this.ps.accountLogUser

                        })
                        .then(res => {
                            if (res.data.errorCode == 0) {
                                this.VOpen = false;
                                this.searchOrder();
                            } else {
                                this.msg2(res.data.errorMsg);
                            }
                        });
                }
            });
        },
        notsees() {
            this.popupVisible = false;
            this.vipOpen = false;
            this.companyOpen = false;
            this.VOpen = false;
        },
        jumpFrom() {
            this.popupVisible = !this.popupVisible;
        },
        showList() {
            this.show = !this.show;
        },
        payCard() {
            //银行卡支付
            unionScan(this.successPay, null, "");
        },
        successPay(value) {
            JSON.parse(value);
        },
        openDetail() {
            if (this.orderDetailObj.accnt == undefined) {
                this.msg("请先查询桌号");
            } else {
                this.$refs.masterListC.getMasterList(
                    this.orderDetailObj.accnt,
                    this.orderDetailObj.tableno
                );
                this.popupVisible = !this.popupVisible;
            }
        },
        // 打印成功后
        printSucceed(value) {
            if (this.printNowCount < this.printMaxCount) {
                MessageBox({
                    title: "提示",
                    message: "正在打印第" + (this.printNowCount + 1) + "份，请撕掉后点继续?",
                    showCancelButton: true,
                    closeOnClickModal: false,
                    confirmButtonText: "继续"
                }).then(action => {
                    if (action == "confirm") {
                        this.printNowCount++;
                        printData(this.printSucceed, this.printError, this.lastPrintObj);
                    }
                });
            }
        },
        getmlist(obj, i, tscount, payway, traceNo) {
            if (i >= tscount) {
                this.printFact(obj, payway, traceNo);
                return;
            }
            var element = obj.unionslist[i];
            this.$http
                .post("/pos/posUnion/getMasterList", {
                    accnt: element.unionAccnt,
                    tableno: element.unionTableNo
                })
                .then(res => {
                    if (res.data.errorCode == 0) {
                        element.datas = res.data.result.detailDtos;
                        this.getmlist(obj, i + 1, tscount, payway, traceNo);
                    } else {
                        this.msg(res.data.errorMsg);
                    }
                });
        },
        // 打印
        printOrder(payway, traceNo, count, obj) {
            this.printMaxCount = count;
            this.printNowCount = 1;
            if (this.isEmpty) {
                this.msg("请输入桌号");
            } else {
                var tscount = obj.unionslist.length;
                if (tscount > 0) {
                    var that = this;
                    MessageBox({
                        title: "提示",
                        message: "关联桌号是否一起打印?",
                        showCancelButton: true,
                        closeOnClickModal: false,
                        confirmButtonText: "确定"
                    }).then(action => {
                        if (action == "confirm") {
                            this.getmlist(obj, 0, tscount, payway, traceNo);
                        } else {
                            this.printFact(obj, payway, traceNo);
                        }
                    });
                } else {
                    this.printFact(obj, payway, traceNo);
                }
            }
        },
        printFact(tobj, payway, traceNo) {
            if (payway != undefined) {
                tobj.traceNo = traceNo;
                tobj.payway = payway; // getPayWayName(payway);
            } else {
                tobj.payway = null;
            }
            tobj.membalance = this.membalance;
            var gatewayJson = localStorage.getItem("gateway");
            if (gatewayJson != null) {
                var gateway = JSON.parse(gatewayJson);
                tobj.hotelName = gateway.hotelName;

                var taxJson = localStorage.getItem("ps");
                if (taxJson != null) {
                    var ps = JSON.parse(taxJson);
                    if (ps.invoice && payway) {
                        var urlv =
                            gateway.url +
                            "/pos/posUnion/getQRCode?" +
                            getKeyUrlYun(
                                gateway.hotelGroupCode,
                                gateway.hotelCode,
                                tobj.accnt
                            );
                        tobj.invoiceGateway = urlv;
                    }
                }
            }
            this.lastPrintObj = tobj; //;
            // console.log(JSON.stringify(tobj));
            printData(this.printSucceed, this.printError, this.lastPrintObj);
        },
        printError(v) {
            MessageBox({
                title: "提示",
                message: "打印出错:" + v + "，继续打印?",
                showCancelButton: true,
                closeOnClickModal: false,
                confirmButtonText: "确定"
            }).then(action => {
                if (action == "confirm") {
                    printData(this.printSucceed, this.printError, this.lastPrintObj);
                }
            });
        },
        // 扫描成功后
        scanSucceed(value) {
            if (value != null && value != "") {
                if (value.length < 10) {
                    // console.log(value);
                    this.tableNo = value;
                    this.searchOrder();
                } else {
                    this.msg("不是有效桌号");
                }
            } else {
                this.msg("扫描数据为空");
            }
        },
        // 扫描
        scanOrder() {
            ScanData(this.scanSucceed, null);
        },

        // 扫描成功后
        scanVipSucceed(value) {
            if (value != null && value != "") {
                if (value.length < 100) {
                    this.$http
                        .post("/pos/posUnion/qrToCardInfo", {
                            paycode: value
                        })
                        .then(res => {
                            if (res.data.errorCode == 0) {
                                this.cardInfo = res.data.result.cardNo;
                                this.searchCard();
                            } else {
                                this.msg(res.data.errorMsg);
                            }
                        });
                } else {
                    this.msg("不是有效会员号");
                }
            } else {
                this.msg("扫描数据为空");
            }
        },
        // 扫描
        scanVip() {
            ScanData(this.scanVipSucceed, null);
        },
        getMemberCardTitle() {
            return "会员卡" + (this.bcardPay ? "支付" : "搜索");
        },
        openVip(b) {
            this.bcardPay = b;
            this.vipOpen = !this.vipOpen;
        },
        msg(msg) {
            Toast({
                message: msg,
                position: "bottom",
                duration: 3000
            });
        },
        msg2(msg) {
            if (msg == "用户取消") {
                this.msg(msg);
            } else {
                MessageBox.alert(msg);
            }
        },
        getMasterDetail(tableNo) {
            this.$http
                .post("/pos/posUnion/getMasterDetail", {
                    tableNo: tableNo,
                    userCode: this.ps.accountLogUser
                })
                .then(res => {
                    if (res.data.errorCode == 0) {
                        var result = res.data.result;
                        this.orderDetailObj = result[result.length - 1];
                        this.isEmpty = false;
                        this.isFrist = false;
                    } else {
                        this.msg(res.data.errorMsg);
                        this.orderDetailObj = this.orderDetailDefaultObj;
                        this.isEmpty = true;
                        this.isFrist = false;
                        this.isSucceed = false;
                    }
                });
        },
        searchOrder() {
            if (this.tableNo) {
                this.getMasterDetail(this.tableNo.toUpperCase());
            } else {
                this.msg("请输入桌号");
            }
        },
        changeTableNo(item) {
            this.tableNo = item.unionTableNo;
            this.searchOrder();
        },
        getCardInfo2() {
            if(!this.cardInfo){
                this.msg("请输入手机号码");
                return;
            }
             if(this.cardInfo.length != 11){
                this.msg("手机号码格式不正确");
                return;
            }
            this.$http
                .post("/pos/posUnion/getKunLunMemberInfoByMobile", {
                    mobile: this.cardInfo,
                    pccode: this.orderDetailObj.pccode,
                    accnt: this.orderDetailObj.accnt
                })
                .then(res => {
                    if (res.data.errorCode == 0) {
                        this.cardInfoObj2 = res.data.result;
                        this.sessionId = res.data.sessionId;

                    } else {
                        this.cardInfoObj = null;
                        this.isCardEmpty = true;
                        this.isCardFrist = false;
                        this.msg(res.data.errorMsg);
                    }
                });
        },
        getCardInfo(cardInfo) {
            this.$http
                .post("/pos/posUnion/getCardInfo", {
                    cardInfo: cardInfo,
                    pccode: this.orderDetailObj.pccode,
                    accnt: this.orderDetailObj.accnt
                })
                .then(res => {
                    if (res.data.errorCode == 0) {
                        this.cardInfoObj = res.data.result;
                        for (var item in this.cardInfoObj) {
                            this.cardInfoObj[item].isSelect = false;
                            this.isCardEmpty = false;
                            this.isCardFrist = false;
                        }
                    } else {
                        this.cardInfoObj = res.data.result;
                        this.isCardEmpty = true;
                        this.isCardFrist = false;
                        this.msg(res.data.errorMsg);
                    }
                });
        },
        searchCompany() {
            this.$http
                .post("/pos/posUnion/getCusinfo", {
                    susInfo: this.companyInfo
                })
                .then(res => {
                    if (res.data.errorCode == 0) {
                        this.companyObj = res.data.result;
                        for (var item in this.companyObj) {
                            this.companyObj[item].isSelect = false;
                            this.isCompanyEmpty = false;
                            this.isCompanyFrist = false;
                        }
                    } else {
                        this.isCompanyEmpty = true;
                        this.isCompanyFrist = false;
                        this.msg(res.data.errorMsg);
                    }
                });
        },
        searchCard() {
            this.getCardInfo(this.cardInfo);
        },
        updateCardInfoToMaster(cardObj) {
            var accnt = this.orderDetailObj.accnt;
            var cardNo = cardObj.cardNo;
            var cardInfo = cardObj.cardInfo;
            var dscMode = cardObj.dscMode;
            this.$http
                .post("/pos/posUnion/updateCardInfoToMaster", {
                    accnt: accnt,
                    cardNo: cardNo,
                    cardInfo: cardInfo,
                    dscMode: dscMode,
                    userCode: this.ps.accountLogUser
                })
                .then(res => {
                    if (res.data.errorCode == 0) {
                        this.searchOrder();
                    } else {
                        this.msg2(res.data.errorMsg);
                    }
                });
        },
        updateCompanyInfoToMaster(item) {
            this.$http
                .post("/pos/posUnion/updateCusinfoToMaster", {
                    accnt: this.orderDetailObj.accnt,
                    cusNo: item.accnt,
                    cusinfo: item.name,
                    dscMode: item.posMode,
                    userCode: this.ps.accountLogUser
                })
                .then(res => {
                    if (res.data.errorCode == 0) {
                        this.searchOrder();
                    } else {
                        this.msg2(res.data.errorMsg);
                    }
                });
        },
        // 选中会员卡
        selectCard(item, index) {
            var carll = () => {
                // var itemObj = item;
                // for (var i = 0; i < this.cardInfoObj.length; i++) {
                //     this.$set(this.cardInfoObj[i], "isSelect", false);
                // }
                // this.$set(this.cardInfoObj[index], index, itemObj);
                // // 触发数组检测
                // this.cardInfoObj.push(11);
                // this.cardInfoObj.pop(11);
                this.vipOpen = false;
                if (this.bcardPay) {
                    var left = item.balance;
                    console.log("left",left);
                    if (left < this.getPayAmout() ) {
                        if(this.cloudParams.isChong == 'T'){
                        MessageBox({
                            title: "提示",
                            message: "余额不足：" + left + "，是否现在充值？",
                            showCancelButton: true,
                            closeOnClickModal: false,
                            confirmButtonText: "确定"
                        }).then(action => {
                            if (action == "confirm") {
                                this.$router.push(
                                    "/cardInMony?cardNo=" +
                                    item.cardNo +
                                    "&redirect=" +
                                    encodeURIComponent(
                                        "/orderPayDetail?tableno=" + this.tableNo
                                    )
                                );
                            }
                        });
                        }
                        else{
                            this.msg('余额不足');
                            this.vipOpen = true;
                        }
                    } else {
                        this.membalance = item.balance;
                        this.cardPay(item.cardId);
                    }
                } else {
                    this.updateCardInfoToMaster(item);
                }
            };
            if (this.bcardPay) {
                MessageBox({
                    title: "提示",
                    message: "确定使用会员卡支付?",
                    showCancelButton: true,
                    closeOnClickModal: false,
                    confirmButtonText: "确定"
                }).then(action => {
                    if (action == "confirm") {
                        carll();
                    }
                });
            } else {
                carll();
            }
        },
        selectCompany(item, index) {
            var itemObj = item;
            //只能选中其中一个
            if (!itemObj.isSelect) {
                for (var i = 0; i < this.companyObj.length; i++) {
                    this.companyObj[i].isSelect = false;
                }
            }
            itemObj.isSelect = !itemObj.isSelect;
            this.$set(this.companyObj[index], index, itemObj);
            // 触发数组检测
            this.companyObj.push(11);
            this.companyObj.pop(11);
            this.updateCompanyInfoToMaster(item);
            this.companyOpen = false;
        },
        //银行卡,支付宝支付成功
        successUnionPay(value) {
            if (value.transData.resCode == "00") {
                this.checkOutOrder(value.transData);
            } else {
                //failed 支付失败，弹出对应的支付失败的错误码
                this.msg2(value.transData.resDesc);
            }
        },
        getAccnt() {
            if (this.orderDetailObj.accountNum) {
                return this.orderDetailObj.accnt + "_" + this.orderDetailObj.accountNum;
            } else {
                return this.orderDetailObj.sta == "I" ?
                    this.orderDetailObj.accnt :
                    this.orderDetailObj.accnt + "R";
            }
        },
        payByItem(item) {
            if (this.getPayAmout() > 0) {
                this.nowPayway = item;
                localStorage.payCodeSelect = JSON.stringify(item);
                if (item.catPosting == "PUN_CARD") {
                    this.checkUnionPay();
                } else if (item.catPosting == "PUN") {
                    this.checkUnionScan();
                } else if (item.catPosting == "RCV" || item.catPosting == "KYK") {
                    this.selectCardPay();
                } else if (item.catPosting == "TF" || item.catPosting == "TFO") {
                    this.toFrontDest();
                } else if (item.catPosting == "TA" || item.catPosting == "TAO") {
                    this.toAR();
                } else {
                    MessageBox({
                        title: "提示",
                        message: "确定" + item.descript + "支付？",
                        showCancelButton: true,
                        closeOnClickModal: false,
                        confirmButtonText: "确定"
                    }).then(action => {
                        if (action == "confirm") {
                            this.PayCommon();
                        }
                    });
                }
            } else {
                this.msg("金额必须大于 0");
            }
        },
        // 调用硬件的UnionPay
        async checkUnionPay() {
            await this.searchOrder();
            unionPay(
                this.successUnionPay,
                null,
                Math.round(this.getPayAmout() * 100),
                this.getAccnt()
            );
        },
        async checkUnionScan(pw) {
            await this.searchOrder();
            unionScan(
                this.successUnionPay,
                null,
                Math.round(this.getPayAmout() * 100),
                this.getAccnt(),
                pw
            );
        },
        getPayAmout() {
            return (
                Number(this.orderDetailObj.charge) - Number(this.orderDetailObj.credit)
            );
        },
        getFP() {
            return (
                Number(this.orderDetailObj.charge) +
                Number(this.orderDetailObj.dscAmount) -
                Number(this.orderDetailObj.srvAmount)
            );
        },
        reInAccount() {
            if (!this.orderDetailObj.accnt) {
                this.msg("请输入桌号");
                return;
            }
            reAccount(this.successUnionPay, null, this.getAccnt());
        },
        //银行卡支付成功后入账信息
        checkOutOrder(transData) {
            var postObj = {};
            postObj.accnt = this.orderDetailObj.accnt;
            if (this.orderDetailObj.pcrec) {
                postObj.pcrec = this.orderDetailObj.pcrec;
            }
            postObj.charge = Number(transData.amt); //this.getPayAmout();
            postObj.payCode = getPayWayPOS(transData); // 'PUN';
            postObj.credit = this.orderDetailObj.credit;
            postObj.userCode = this.ps.accountLogUser;
            postObj.flag = 0;

            // postObj.balance='';
            postObj.orderno = transData.traceNo;
            postObj.serialno = transData.refNo;
            if (transData.eSignJpeg) {
                transData.eSignJpeg = null;
            }
            postObj.remarkjson = JSON.stringify(transData);
            this.$http.post("/pos/posUnion/saveOrderCheckOut", postObj).then(res => {
                if (res.data.errorCode == 0) {
                    this.msg("入账成功");
                    this.printOrder(
                        getPayWayName(postObj.payCode),
                        transData.traceNo,
                        this.ps.printPayCount,
                        this.orderDetailObj
                    );
                    //支付成功后显示对应的页面内容
                    this.isEmpty = true;
                    this.isFrist = true;
                    this.isSucceed = true;
                    this.cardInfoObj = null;
                    this.orderDetailObj = this.orderDetailDefaultObj;
                } else {
                    this.msg2(res.data.errorMsg);
                }
            });
        },
        cardPay(cardId) {
            var postObj = {};
            postObj.accnt = this.orderDetailObj.accnt;
            if (this.orderDetailObj.pcrec) {
                postObj.pcrec = this.orderDetailObj.pcrec;
            }
            postObj.charge = this.getPayAmout();
            postObj.payCodeMode = 1;
            postObj.payCode = this.nowPayway.code;
            postObj.credit = this.orderDetailObj.credit;
            postObj.userCode = this.ps.accountLogUser;
            postObj.flag = 0;
            postObj.accntNo = cardId;
            this.$http.post("/pos/posUnion/saveOrderCheckOut", postObj).then(res => {
                if (res.data.errorCode == 0) {
                    this.payfinish(postObj, this.nowPayway.descript);
                } else {
                    this.msg2(res.data.errorMsg);
                }
            });
        },
        payfinish(postObj, paywayName) {
            this.msg("入账成功");
            this.printOrder(
                paywayName,
                "",
                this.ps.printPayCount,
                this.orderDetailObj
            );
            //支付成功后显示对应的页面内容
            this.isEmpty = true;
            this.isFrist = true;
            this.isSucceed = true;
            this.cardInfoObj = null;
            this.orderDetailObj = this.orderDetailDefaultObj;
        },
        PayCommon() {
            var postObj = {};
            postObj.accnt = this.orderDetailObj.accnt;
            if (this.orderDetailObj.pcrec) {
                postObj.pcrec = this.orderDetailObj.pcrec;
            }
            postObj.charge = this.getPayAmout();
            postObj.payCode = this.nowPayway.code;
            postObj.payCodeMode = 1;
            postObj.credit = this.orderDetailObj.credit;
            postObj.userCode = this.ps.accountLogUser;
            postObj.flag = 0;
            this.$http.post("/pos/posUnion/saveOrderCheckOut", postObj).then(res => {
                if (res.data.errorCode == 0) {
                    this.payfinish(postObj, this.nowPayway.descript);
                } else {
                    this.msg2(res.data.errorMsg);
                }
            });
        },
        async toFrontDest() {
            await this.searchOrder();
            if (this.orderDetailObj.accnt == undefined) {
                this.msg("请先查询桌号");
            } else {
                localStorage.orderDetail = JSON.stringify(this.orderDetailObj);
                this.$router.push("toFrontDest");
            }
        },
        selectPayWay() {
            this.$http
                .post("/pos/posUnion/getPayWays", {})
                .then(res => {
                    if (res.data.errorCode == 0) {
                        this.payways = res.data.result;
                    } else {
                        this.msg(res.data.msg);
                    }
                });
        },
        async toAR() {
            await this.searchOrder();
            if (this.orderDetailObj.accnt == undefined) {
                this.msg("请先查询桌号");
            } else {
                localStorage.orderDetail = JSON.stringify(this.orderDetailObj);
                this.$router.push("toAR");
            }
        },
        async selectCardPay() {
            if (this.getPayAmout() > 0) {
                if (!this.orderDetailObj.cardNo) {
                    this.msg("请先绑定会员卡");
                    this.openVip(false);
                    return;
                }
                await this.searchOrder();
                this.$http
                    .post("/pos/posUnion/getCardInfo", {
                        cardInfo: this.orderDetailObj.cardNo,
                        pccode: this.orderDetailObj.pccode,
                        accnt: this.orderDetailObj.accnt
                    })
                    .then(res => {
                        if (res.data.errorCode == 0) {
                            if (res.data.result.length == 1) {
                                var vitem = res.data.result[0];
                                var left = vitem.balance;
                                if (left < this.getPayAmout()) {
                                    MessageBox({
                                        title: "提示",
                                        message: "余额不足：" + left + "，是否现在充值？",
                                        showCancelButton: true,
                                        closeOnClickModal: false,
                                        confirmButtonText: "确定"
                                    }).then(action => {
                                        if (action == "confirm") {
                                            this.$router.push(
                                                "/cardInMony?cardNo=" +
                                                vitem.cardNo +
                                                "&redirect=" +
                                                encodeURIComponent(
                                                    "/orderPayDetail?tableno=" + this.tableNo
                                                )
                                            );
                                        }
                                    });
                                } else {
                                    MessageBox({
                                        title: "提示",
                                        message: "确定使用会员卡：" +
                                            this.orderDetailObj.cardNo +
                                            "支付？",
                                        showCancelButton: true,
                                        closeOnClickModal: false,
                                        confirmButtonText: "确定"
                                    }).then(action => {
                                        if (action == "confirm") {
                                            this.membalance = left;
                                            this.cardPay(vitem.cardId);
                                        }
                                    });
                                }
                            } else {
                                this.cardInfoObj = res.data.result;
                                for (var item in this.cardInfoObj) {
                                    this.cardInfoObj[item].isSelect = false;
                                    this.isCardEmpty = false;
                                    this.isCardFrist = false;
                                }
                                this.bcardPay = true;
                                this.vipOpen = true;
                                this.cardInfo = this.orderDetailObj.cardNo;
                            }
                        } else {
                            this.msg(res.data.errorMsg);
                        }
                    });
            } else {
                this.msg("金额必须大于 0");
            }
        },
        toggleList() {
            this.listVisible = !this.listVisible;
            this.getCategoryList();
        },
        getCategoryList() {
            this.$http.post("/pos/posUnion/getPosPccode", {}).then(res => {
                if (res.data.errorCode == 0) {
                    var result = res.data.result;
                    this.tableList = result;
                } else {
                    this.msg(res.data.msg);
                }
            });
        },

        changeFirstPayCode(index) {
            this.tableListActive = this.tableList[index].code;
            localStorage.setItem("tableListActive", this.tableListActive);

            this.tableSubList = this.tableList[index].list;

            this.$http
                .post("/pos/posUnion/getPosTableNoByPccode", {
                    pccode: this.tableListActive
                })
                .then(res => {
                    if (res.data.errorCode == 0) {
                        var result = res.data.result;
                        this.tableSubList = result;
                        localStorage.setItem("tableSubList", JSON.stringify(result));
                    } else {
                        this.msg(res.data.msg);
                    }
                });
        },
        changeSubPayCode(index) {
            this.tableSubListActive = this.tableSubList[index].code;
            this.tableSubListSelect = this.tableSubList[index];
            //将选项中的内容放到缓存里面
            // localStorage.setItem('tableSubListActive', this.tableSubListActive)
            // localStorage.setItem('tableSubListSelect', JSON.stringify(this.tableSubListSelect))
            this.listVisible = false;
            this.tableNo = this.tableSubListSelect.code;
            this.searchOrder();
        }
    },
    mounted() {
        // 头部有参数的时候默认去查询相关的
        this.SYS_DEVICE = this.$SYS_DEVICE;
        if (typeof this.$route.query.tableno == "string") {
            this.tableNo = this.$route.query.tableno;
            this.searchOrder();
        }
        if (localStorage.tableListActive) {
            this.tableSubList = JSON.parse(localStorage.tableSubList);
        }
        // 支付成功的时候相关的查询内容
        this.actions = [{
                name: "打印菜目",
                method: () => {
                    this.printOrder(null, null, 1, this.orderDetailObj);
                }
            }
            /*{ name: "关联会员", method: this.openVip },
                  { name: "关联协议", method:()=>{this.companyOpen=true;} }*/
        ];
        //工行没有入向其核对功能

        this.actions.push({
            name: "入账核对",
            method: this.reInAccount
        });

        if (typeof this.$route.query.isSucceed == "string") {
            (this.isEmpty = true), (this.isFrist = true);
            this.isSucceed = true;
        }
        this.selectPayWay();
    },
    components: {
        topNav,
        masterList,
        actionsheet: Actionsheet
    }
};
</script>

<style lang="less">
.orderPayDetail {
    .topSearch {
        width: 100%;
        height: 1.6rem;
        background: #eeeeee;
        position: fixed;

        .category {
            position: absolute;
            top: 0.28rem;
            left: 15px;
            font-size: 14px;
            line-height: 0.9867rem;
            height: 0.9867rem;
            width: 1rem; // display: inline-block;

            &.active {
                transform: rotate(180deg);
                -ms-transform: rotate(180deg);
                /* IE 9 */
                -moz-transform: rotate(180deg);
                /* Firefox */
                -webkit-transform: rotate(180deg);
                /* Safari 和 Chrome */
                -o-transform: rotate(180deg);
            }
        }

        .tableDesc {
            position: absolute;
            top: 0.28rem;
            left: 4rem;
            line-height: 0.9867rem;
            color: #ccc;
        }

        .icon-saoma {
            display: inline-block;
            font-size: 0.64rem;
            line-height: 1.4667rem;
            vertical-align: top;
            margin-left: 0.3467rem;
        }

        .search {
            display: inline-block;
            width: 46%;
            height: 0.9867rem;
            padding-left: 1.1rem;
            border-radius: 0.1333rem;
            margin-top: 0.2667rem;
            border: none;
        }

        .searchCheck {
            display: inline-block;
            padding: 0.1333rem 0.6667rem;
            background: #d8af68;
            border-radius: 0.1867rem;
            color: #fff;
            margin-left: 0.4rem;
        }
    }

    .comspan {
        width: 3.9rem;
        float: left;
        white-space: nowrap;
        text-overflow: ellipsis;
        overflow: hidden;
    }

    .yh {
        width: 2.2rem;
        float: left;
        white-space: nowrap;
        text-overflow: ellipsis;
        overflow: hidden;
    }

    .info {
        width: 100%;
        height: auto;
        background: #fff;
        box-shadow: 0rem -0.08rem 0.1333rem #dadada;
        border-bottom: 0.0533rem solid #646f83;
        padding: 0.2333rem 0;
        position: fixed;
        top: 2.7rem;

        ul {
            li {
                margin-bottom: 0.1867rem;
                text-align: left;

                p {
                    width: 45%;
                    float: left;
                    font-size: 0.33rem;
                    color: #151515;
                    text-align: left;
                    padding-left: 5%;

